import React, { useState, useEffect, useCallback } from 'react';
import { Table, Switch, message } from 'antd';
import Search from 'antd/lib/input/Search';
import request from '../../../request';
import { UserInfo } from '../../../request/types';

interface Props {
	onParams: (key: string, value: string | number) => void
	params: URLSearchParams
}

const A: React.FC<Props> = (props) => {

	const [loading, setLoading] = useState(false);

	const [data, setData] = useState<{ pageIndex: number, pageSize: number, rows: UserInfo[], total: number }>({
		pageIndex: 1,
		pageSize: 10,
		rows: [],
		total: 0
	})

	const columns: any[] = [
		{
			title: '登录名', dataIndex: 'userName', align: 'center',
		},
		{
			title: '昵称', dataIndex: 'nickName', align: 'center', width: 200,
		},
		{
			title: '手机号', dataIndex: 'mobile', align: 'center',
		},
		{
			title: '启用', dataIndex: 'flagEnable', align: 'center',
			render: (l: boolean, r: any) =>
				<Switch defaultChecked={l} checkedChildren='启用' unCheckedChildren='禁用' onChange={v => {
					updateUser({ ...r, flagEnable: !l })
				}}></Switch>
		},
	];

	/**
	 * 保存用户信息
	 * @param v 
	 */
	const updateUser = (v: any) => {

		setLoading(true)
		request.admin.client.saveData(v).then(res => {

			if (res.success) {
				message.success('用户信息保存成功！')
			} else {
				message.error('用户信息保存失败！')
			}
			updateData()
		})
	}

	/**
	 * 更新用户数据
	 */
	const updateData = useCallback(() => {
		request.admin.client.getList({
			keyword: props.params.get('keyword') || undefined,
			pageIndex: parseInt(props.params.get('pageIndex') || '1') || 1,
			pageSize: 10,
		}).then(res => {

			setData(res)
			setTimeout(() => {
				setLoading(false)
			}, 300);
		})
	}, [props.params])

	useEffect(() => {

		updateData()

	}, [props.params, updateData])

	return (
		<div>
			<div style={{ padding: '8px 0' }}>
				<Search placeholder='搜索 登录名/昵称/手机号' allowClear defaultValue={props.params.get('keyword') || undefined} style={{ width: 240 }} enterButton onSearch={v => {
					props.onParams('keyword', v)
				}} />
			</div>

			<Table

				columns={columns}
				dataSource={data.rows.map(v => ({ ...v, key: v.id }))}
				scroll={{ x: 600 }}
				loading={loading}
				pagination={{ showTotal: (total) => `总共：${ total }人 `, showSizeChanger: true }}
				bordered>
			</Table>
		</div>
	)
}

export default A;