import React, { useEffect, useState } from 'react';
import css from './categoryImages.module.less';
import request, { ufileDownUrl, requestCache } from '../../request';
import { CategoryList } from '../../request/types';
import { Spin } from 'antd';

export interface Props {
	/**
	 * 图片点击回调， select(img.src)=>void
	 */
	select: (id: string) => void,
	id: string
}

const App: React.FC<Props> = (props) => {

	const [data, setData] = useState<CategoryList[]>(requestCache[`request.main.ui.category.sublist_${ props.id }`] || [])

	useEffect(() => {
		request.main.ui.category.sublist(props.id).then(res => {
			requestCache[`request.main.ui.category.sublist_${ props.id }`] = res.rows
			setData(res.rows)
		})

	}, [props.id])

	return (
		<div className={css['root']}>
			{data.map((v, i, a) => (

				v.smallSha1.length === 0 ?
					<div className={css['item']} style={{ flex: '100 200px', backgroundColor: '#efefef', padding: '40px 0' }} onClick={() => { props.select(v.id) }} key={v.id}>
						<Spin><div>努力加载中</div><div>可是好像确实没找到图片...</div></Spin>
						<div className={css['itemTitle']}><b>{v.name}</b></div>
					</div> :
					<div className={css['item']} style={{ flex: `${ v.width * 200 / v.heigth } ${ v.width * 200 / v.heigth }px`, maxWidth: v.width * 1.5 }} onClick={() => { props.select(v.id) }} key={v.id}>
						<img className={css['itemImage']} alt={v.name} src={ufileDownUrl + v.smallSha1}></img>
						<div className={css['itemTitle']}><b>{v.name}</b></div>
					</div>
			))}
		</div>
	)
}

export default App;