import React from 'react';
import Form, { FormComponentProps } from 'antd/lib/form';
import { Button, Input, Switch, Select, message } from 'antd';
import request from '../../../../request';

const { Option } = Select;

interface Props extends FormComponentProps<{
	flagEnable: boolean
	mobile: string
	nickName: string
	roleId: string
	userName: string
}> {
	beginLoading: () => void
	endLoading: () => void
	roles: { name: string, id: string }[]
}

const A: React.FC<Props> = (props) => {

	/**
	 * 提交信息
	 */
	const handleSubmit = () => {

		props.form.validateFieldsAndScroll((error, values) => {

			if (!error) {
				props.beginLoading()

				request.admin.user.saveUser(values).then((res: any) => {
					setTimeout(() => {
						props.endLoading()
					}, 300);

					if (res.code === 0) {
						message.success('添加成功！')
					} else {
						message.error(`添加失败! (${ res.message || res.code })`)
					}
				})
			}
		})
	}

	/**
	 * 登录名
	 */
	const itemID = () => {
		const { getFieldDecorator } = props.form;

		return getFieldDecorator('userName', {
			rules: [{ required: true, message: '请输入登录名' }]
		})(<Input placeholder='登录名 (ID)'></Input>)
	}
	/**
	 * 用户名
	 */
	const itemName = () => {
		const { getFieldDecorator } = props.form;

		return getFieldDecorator('nickName', {
			rules: [{ required: true, message: '请输入昵称' }]
		})(<Input placeholder='用户显示的昵称'></Input>)
	}

	/**
	 * 手机号
	 */
	const itemPhone = () => {
		const { getFieldDecorator } = props.form;

		return getFieldDecorator('mobile', {
			rules: [{ required: true, message: '请输入手机号' }]
		})(<Input placeholder='手机号 (登录、修改密码的凭证)'></Input>)
	}

	/**
	 * 角色权限
	 */
	const itemSale = () => {
		const { getFieldDecorator } = props.form;

		return getFieldDecorator('roleId', {
			rules: [{ required: true, message: '请选择角色权限' }]
		})(
			<Select placeholder='设置角色权限'>
				{
					props.roles.map(v => <Option key={v.id} value={v.id}>{v.name}</Option>)
				}
			</Select>
		)
	}

	/**
	 * 状态
	 */
	const itemEnable = () => {
		const { getFieldDecorator } = props.form;

		return getFieldDecorator('flagEnable', {
			initialValue: true, valuePropName: 'checked'
		})(<Switch checkedChildren='启用' unCheckedChildren='禁用' />)
	}

	return (
		<div>
			<Form labelCol={{ span: 6 }} wrapperCol={{ span: 12 }}>
				<Form.Item label='登录名' >
					{itemID()}
				</Form.Item>
				<Form.Item label='昵称' >
					{itemName()}
				</Form.Item>
				<Form.Item label='手机号'>
					{itemPhone()}
				</Form.Item>
				<Form.Item label='角色权限' hasFeedback>
					{itemSale()}
				</Form.Item>

				<Form.Item label='状态'>
					{itemEnable()}
				</Form.Item>
				<Form.Item label='完成'>
					<Button type='primary' style={{ minWidth: '100%' }} onClick={handleSubmit}>提交信息</Button>
				</Form.Item>

			</Form>
		</div>
	)
}

export default Form.create<Props>()(A);