import React, { useState, useEffect, useCallback } from 'react';
import { Table, Switch, Button, Divider, Card, Typography, message, Popconfirm, Select } from 'antd';
import Search from 'antd/lib/input/Search';
import Paragraph from 'antd/lib/typography/Paragraph';
import css from './style.module.less';
import NFrom from './forms/add';
import request from '../../../request';
import { UserInfo } from '../../../request/types';

interface Props {
	onParams: (key: string, value: string | number) => void
	params: URLSearchParams
}

const { Option } = Select;

const A: React.FC<Props> = (props) => {

	const [editing, setEditing] = useState(false);
	const [loading, setLoading] = useState(false);
	const [adding, setAdding] = useState(false);
	const [loadingForm, setLoadingForm] = useState(false);
	const [roles, setRoles] = useState<{ name: string, id: string }[]>([])
	const [data, setData] = useState<{ pageIndex: number, pageSize: number, rows: UserInfo[], total: number }>({
		pageIndex: 1,
		pageSize: 10,
		rows: [],
		total: 0
	})

	const columns: any[] = [
		{
			title: '登录名', dataIndex: 'userName', align: 'center',
			render: (text: string) => <Typography.Paragraph copyable>{text}</Typography.Paragraph>
		},
		{
			title: '昵称', dataIndex: 'nickName', align: 'center', width: 200,
			render: (text: string, r: any) =>
				<Paragraph editable={editing && {
					onChange: (v) => {
						if (v.trim().length > 0) {
							updateUser({ ...r, nickName: v })
						} else {
							message.error('昵称不能为空！')
						}
					}
				}}>{text}</Paragraph>

		},
		{
			title: '手机号', dataIndex: 'mobile', align: 'center',
			render: (text: string, r: any) =>
				<Paragraph editable={editing && {
					onChange: (v) => {
						updateUser({ ...r, mobile: v })
					}
				}}>{text}</Paragraph>
		},
		{
			title: '角色', dataIndex: 'role', align: 'center', width: 200,
			render: (obj: any, v: any) => (

				<Select<{ roleId: string, roleName: string }> disabled={!editing} placeholder='设置角色权限'
					defaultValue={obj && obj.roleName} style={{ width: 160 }} onChange={key => {
						updateUser({ ...v, roleId: key })
					}}>
					{
						roles.map(v => <Option key={v.id} value={v.id}>{v.name}</Option>)
					}
				</Select>
			)
		},
		{
			title: '启用', dataIndex: 'flagEnable', align: 'center',

			render: (l: boolean, r: any) =>
				<Switch defaultChecked={l} checkedChildren='启用' unCheckedChildren='禁用'
					disabled={!editing} onChange={v => {
						updateUser({ ...r, flagEnable: !l })
					}}></Switch>
		},

		{
			title: '操作', key: 'action', fixed: 'right', align: 'center', width: 240,
			render: (r: any) => (
				<div>
					<Popconfirm title='确定删除用户？' onConfirm={() => {
						setLoading(true)
						request.admin.user.delete(r.id).then(res => {
							updateData()
						})

					}}>
						<Button type='danger'  icon='delete' >删除</Button>
					</Popconfirm>

					<Divider type='vertical'></Divider>
					<Popconfirm title='确定对用户密码初始化？' onConfirm={() => {
						setLoading(true)
						request.admin.user.resetPassword(r.id).then(res => {
							updateData()
						})
					}}>
						<Button >初始化密码</Button>
					</Popconfirm>
				</div>)
		},
	];

	/**
	 * 添加用户
	 */
	const add = () => {

		if (adding) {
			return (

				<div className={css['add-show']}>
					<Card
						loading={loadingForm}
						title='填写用户信息'
						size='small'
						extra={<Button type='primary' onClick={() => { setAdding(false) }}>关闭</Button>}>
						<NFrom
							roles={roles}
							beginLoading={() => { setLoadingForm(true) }}
							endLoading={() => {
								setLoadingForm(false)
								updateData()
							}}></NFrom>
					</Card>
				</div>
			)
		}
	}

	/**
	 * 保存用户信息
	 * @param v 
	 */
	const updateUser = (v: UserInfo) => {

		setLoading(true)
		request.admin.user.saveUser(v).then(res => {

			if (res.code === 0) {
				message.success('用户信息保存成功！')
			} else {
				message.error('用户信息保存失败！')
			}
			updateData()
		})
	}

	/**
	 * 更新用户数据
	 */
	const updateData = useCallback(() => {
		request.admin.user.getList({
			keyword: props.params.get('keyword') || undefined,
			pageIndex: parseInt(props.params.get('pageIndex') || '1') || 1,
			pageSize: 10,
		}).then(res => {
			console.log(res);
			setData(res)
			setTimeout(() => {
				setLoading(false)
			}, 300);
		})
	}, [props.params])

	useEffect(() => {

		updateData()

		request.admin.role.getListForCbx().then(res => {

			if (res.success) {
				setRoles(res.data)
			}
		})

	}, [props.params, updateData])

	return (
		<div>
			<div style={{ padding: '8px 0' }}>
				<Button type={editing ? 'primary' : 'default'} icon={editing ? 'close' : 'edit'} onClick={v => setEditing(!editing)}>编辑</Button>
				<Divider type='vertical'></Divider>

				<Button type={adding ? 'primary' : 'default'} icon={adding ? 'close' : 'plus'} onClick={() => { setAdding(!adding) }}>添加用户</Button>
				<Divider type='vertical'></Divider>

				<Search placeholder='搜索 登录名/昵称/手机号' defaultValue={props.params.get('keyword') || undefined} allowClear style={{ width: 280 }} enterButton onSearch={v => {
					props.onParams('keyword', v)
				}} />
			</div>
			{add()}
			<Table

				columns={columns}
				dataSource={data.rows.map(v => ({ ...v, key: v.id }))}
				scroll={{ x: 1000 }}
				loading={loading}
				pagination={{
					showTotal: (total) => `总共：${ total }人 `, total: data.total, defaultCurrent: data.pageIndex,
					onChange: v => { props.onParams('pageIndex', v) }
				}}
				bordered>
			</Table>
		</div>
	)
}

export default A;