import React, { useState, useEffect } from 'react';
import { Table, Button, Divider, Switch, Input, Typography, InputNumber, message, Popconfirm, Tooltip, Icon } from 'antd';
import request from '../../../request';
import css from './style.module.less'
import { CategoryGroup } from '../../../request/types';
import PopInput from '../../../cmps/popInput';

interface Props {
	type: CategoryGroup
}

export interface CateItem {

	categoryUsage: string
	children?: CateItem[]
	flagEnable: boolean
	id: string
	key: string
	name: string
	parentId: string
	sort?: number
}

const A: React.FC<Props> = (props) => {

	const [currentID, setcurrentID] = useState<string>('')
	/**
	 * 是否显示二级菜单
	 */
	const [showChildren, setshowChildren] = useState(true)
	/**
	 * 控制数值变化间隔，防止连续提交
	 */
	let timerID: any = null
	/**
	 * 网络请求状态
	 */
	const [loading, setLoading] = useState(false)
	const [openKeys, setOpenKeys] = useState<string[]>([])
	const [data, setData] = useState<CateItem[]>([])
	const [keywords, setKeywords] = useState('')

	const columns: any[] = [
		{
			title: <span>名称
				{
					openKeys.length > 0 &&
					<Tooltip title='收起'>
						<span style={{ padding: '0 8px', color: '#1890ff',height:'30px' }} onClick={() => {
							setOpenKeys([])
						}}><Icon type='up-circle'></Icon></span>
					</Tooltip>
				}
			</span>,
			dataIndex: 'name', fixed: 'left', width: 300,
			render: (text: string, record: any) =>
				<Typography.Paragraph style={{ display: 'inline' }} editable={{
					onChange: (v: string) => {
						if (v.trim().length === 0) {
							message.error('分类名不能为空！')
						} else {
							updateData({ ...record, name: v })
						}
					}
				}} >{text}</Typography.Paragraph>
		},
		{
			title: '序号', dataIndex: 'sort', align: 'center',
			render: (text: number, record: any) =>
				<InputNumber defaultValue={text} disabled={loading} min={0} max={1000000}
					onChange={v => {

						if (timerID) {
							clearTimeout(timerID)
						}

						timerID = setTimeout(() => {
							timerID = null;

							if (!/^\d+$/.test(`${ v }`)) {
								message.error('序号必须为数字格式！')
							} else {
								updateData({ ...record, sort: v })
							}
						}, 1000);

					}}></InputNumber>
		},
		{
			title: '状态', dataIndex: 'flagEnable', align: 'center', width: 200,
			render: (checked: boolean, record: any) =>
				<Switch checkedChildren='启用' unCheckedChildren='禁用' defaultChecked={checked} disabled={loading}
					onChange={v => { updateData({ ...record, flagEnable: v }) }}
				></Switch>
		},
		{
			title: '操作', dataIndex: 'parentId', width: 300,
			render: (id: string, record: CateItem) =>

				<div>
					{
						(!record.children || record.children.length === 0) &&
						<>
							<Popconfirm title='确定要删除该分类? 删除操作不可恢复！' okText='确定' cancelText='取消'
								onConfirm={() => deleteItem(record.id)}>
								<Button type='danger' icon='delete' >删除</Button>
							</Popconfirm>

						</>
					}
					{
						(!record.children || record.children.length === 0) && id === '0' &&
						<Divider type='vertical'></Divider>
					}

					{id === '0' &&
						<span>
							<PopInput onInput={v => {
								addItem(record.id, v)
								if (!openKeys.includes(record.key)) {
									setOpenKeys([...openKeys, record.key])
								}
							}}>
								<Button type='primary' icon='plus'>添加</Button>
							</PopInput>

						</span>
					}
				</div>
		},
	]

	/**
	 * 首次获取分类数据
	 */
	useEffect(() => {

		request.admin.cate.getTree({ categoryUsage: props.type, id: '0' }).then((data) => {

			setData(data)
			setOpenKeys(data.map(v => v.key))
		})
	}, [props.type])

	/**
	 * 更新分类数据，更新成功后刷新列表
	 * @param item 
	 */
	const updateData = (item: any) => {

		setLoading(true)

		request.admin.cate.saveData({
			categoryUsage: item.categoryUsage,
			flagEnable: item.flagEnable,
			id: item.id,
			name: item.name,
			parentId: item.parentId,
			sort: item.sort
		}).then(res => {
			setcurrentID(res.data)
			refreshData()
		})
	}

	/**
	 * 刷新列表数据
	 */
	const refreshData = () => {

		setLoading(true)
		setKeywords('')

		request.admin.cate.getTree({ categoryUsage: props.type, id: '0' }).then((data) => {

			setTimeout(() => { setLoading(false) }, 300);
			setData(data)
		})
	}

	/**
	 * 添加分类
	 */
	const addItem = (parentId: string, name: string) => {

		setKeywords('')
		updateData({
			categoryUsage: props.type,
			flagEnable: true,
			id: '0',
			name: name,
			parentId: parentId
		})
	}

	/**
	 * 删除分类
	 * @param id 分类ID
	 */
	const deleteItem = (id: string) => {

		request.admin.cate.deleteData(id).then(res => {
			setcurrentID('')
			refreshData()
		})
	}

	const includes = (v: CateItem) => {

		if (!keywords) {
			return true
		}

		if (v.name.includes(keywords)) {
			return true
		}

		let fd = false
		v.children && v.children.forEach(v => {
			if (v.name.includes(keywords)) {
				fd = true
				return
			}
		})

		return fd
	}

	const className = (v: CateItem) => {
		if (v.id === currentID) {
			return css['item-select']
		}

		if (v.children) {
			return css['item-head']
		}

		return ''
	}

	return (
		<div>
			<div style={{ padding: '6px 0' }}>

				<PopInput onInput={v => {
					addItem('0', v)
				}}>
					<Button type='primary' icon='plus' >添加分类</Button>
				</PopInput>
				<Divider type='vertical'></Divider>

				<Input.Search placeholder='分类查找' style={{ width: 240 }} enterButton allowClear onSearch={v => {
					setKeywords(v)
				}} />
				<Divider type='vertical'></Divider>

				<Switch
					checkedChildren='显示二级' unCheckedChildren='隐藏二级' defaultChecked={showChildren}
					onChange={() => setshowChildren(!showChildren)}>
				</Switch>
				<Divider type='vertical'></Divider>

				<Button disabled={loading} type='link' icon='reload'
					onClick={() => { refreshData() }}>刷新</Button>
			</div>

			<Table
				bordered
				size='small'
				loading={loading}
				scroll={{ x: 1000 }}
				expandedRowKeys={openKeys}
				onExpand={(v, r) => {
					if (openKeys.includes(r.key)) {
						setOpenKeys(openKeys.filter(v => v !== r.key))
					} else {
						setOpenKeys([...openKeys, r.key])
					}
				}}
				columns={columns}
				dataSource={showChildren ? data.filter(v => includes(v)).map(v => ({
					...v, children: v.children && v.children.filter(v => includes(v))
				})) :
					data.filter(v => includes(v)).map(v => ({ ...v, children: undefined }))}
				onRow={(r, i) => ({ onClick: () => { setcurrentID(r.id) } })}
				rowClassName={(v) => className(v)}
				style={{ backgroundColor: '#fff' }}
				pagination={false} />
		</div>
	)
}

export default A;