import React from 'react';
import { ufileDownUrl } from '../request';
import { CategoryList } from '../request/types';
import bk from '../assets/bk2.png';

export interface Props {
	/**
	 * 图片点击回调， select(img.src)=>void
	 */
	select?: (index: number) => void,

	data: CategoryList[]
}

const App: React.FC<Props> = (props) => {

	return (
		<div style={styles.root}>
			{props.data.map((v, i) => (

				<div style={{ flex: `${ v.width * 200 / v.heigth } ${ v.width * 200 / v.heigth }px`, maxWidth: v.width * 1.5, ...styles.item }}
					onClick={() => { props.select && props.select(i) }}
					key={v.id}>

					<img style={styles.itemImage}
						alt={v.name}
						src={ufileDownUrl + v.smallSha1}
						onMouseOut={e => {
							e.currentTarget.style.transform = 'scale(1,1)';
							e.currentTarget.style.transition = 'all 2s';
						}}
						onMouseOver={(e) => {
							e.currentTarget.style.transform = 'scale(1.2,1.2)';
							e.currentTarget.style.transition = 'all 2s';
						}}
						onMouseMove={(e) => {

							const v = e.currentTarget.parentElement!.clientHeight - e.currentTarget.height

							if (Math.abs(v) > 10) {
								const sc = (e.clientY - e.currentTarget.y) / e.currentTarget.parentElement!.clientHeight;

								e.currentTarget.style.transform = `translateY(${ v * sc }px) scale(1.1,1.1)`;
								e.currentTarget.style.transition = 'all 1s';
							}
						}}></img>
				</div>
			))}
		</div>
	)
}

const styles: { [key: string]: React.CSSProperties } = {
	root: {
		display: 'flex',
		flexWrap: 'wrap',
		cursor: 'pointer'
	},
	item: { maxHeight: 400, margin: 4, overflow: 'hidden', backgroundImage: `url(${bk})` },
	itemImage: { width: '100%', borderRadius: 2 },
}

export default App;