import React, { useEffect, useState, useRef } from 'react';
import { RouteComponentProps } from 'react-router';
import { Row, Col, Card, Descriptions, Button, Divider, Typography, Skeleton, Result, Tag, message } from 'antd';
import request, { ufileDownUrl, requestCache } from '../../request';
import { ProductDetail } from '../../request/types';
import { history } from '../..';
import { userInfo } from '../../store';
import OrderInfo from '../.././user/orderForm/orderInfo';

const A: React.FC<RouteComponentProps> = (props) => {

	const id = new URLSearchParams(props.location.search).get('id') || '0'
	const [data, setData] = useState<ProductDetail>(requestCache[`request.main.ui.product.detail_${ id }`])
	const orderInfo = useRef<OrderInfo>(null)

	useEffect(() => {

		request.main.ui.product.detail(id).then(res => {
			requestCache[`request.main.ui.product.detail_${ id }`] = res
			setData(res)
		})

	}, [id])

	/**
	 * 添加购物车
	 */
	const cartSave = () => {
		if (!userInfo.accessToken) {
			message.info('请登录后再添加。')
			history.push('/user/login?old=' + encodeURI(history.location.pathname + history.location.search))
			return
		}

		if (data) {
			request.main.ui.cart.saveData({
				price: data.price,
				productCount: 1,
				productId: data.id,
				productName: data.mainTitle,
				smallImage: data.sha1
			})
		}
	}

	const pay = () => {
		if (!userInfo.accessToken) {
			message.info('请登录后再购买！')
			history.push('/user/login?old=' + encodeURI(history.location.pathname + history.location.search))
			return
		}

		if (data) {
			request.main.ui.order.saveData([{ productId: data.id, productCount: 1 }]).then(res => {

				if (res.success) {
					orderInfo.current && orderInfo.current.show(res.data)
				}
			})
		}

	}

	/**
	 * 加载中
	 */
	if (!data) {
		return (<div style={{ padding: '20px 10%' }}><Skeleton active /></div>)
	}

	/**
	 * 加载失败
	 */
	if (!data.success) {
		return (
			<Result
				status='404'
				title='图片未找到！'
				subTitle='对不起，您查找的图片未找到。'
				extra={<Button type='primary' onClick={() => { history.goBack() }}>返回</Button>}
			/>
		)
	}

	return (
		<div>
			<Row>
				<Col md={12} xs={24}>
					<Card style={{ margin: 20 }} hoverable title={data.mainTitle}
						extra={<Button icon='star' type='link' onClick={() => {

							if (!userInfo.accessToken) {
								message.info('请登录后再添加。')
								history.push('/user/login?old=' + encodeURI(history.location.pathname + history.location.search))
								return
							}

							request.main.ui.history.saveCollection({
								productId: data.id,
								productName: data.mainTitle,
								smallImage: data.sha1
							})
						}}> 收藏</Button>}>
						<img style={{ width: '100%' }} alt={data.mainTitle} src={ufileDownUrl + data.sha1}></img>
					</Card>
				</Col>

				<Col md={12} xs={24}>
					<div style={{ margin: 20, border: 'solid #eee 1px', padding: 12 }}>
						<Descriptions title={'图片编号：' + data.serialNumber}

							column={1}>
							<Descriptions.Item label='添加时间'>{data.createTime}</Descriptions.Item>
							<Descriptions.Item label='图片格式'>{data.format.replace('.', '').toUpperCase()}</Descriptions.Item>
							<Descriptions.Item label='图片尺寸'>{data.width}x{data.height}PX</Descriptions.Item>
							<Descriptions.Item label='图片大小'>{data.size > 1000000 ?
								Math.round(data.size / 1000) / 1000 + ' M' :
								Math.round(data.size / 1000) + ' K'}
							</Descriptions.Item>
							<Descriptions.Item label='图片价格' ><Tag color='#e66' className='money_rmb'>{data.price}</Tag></Descriptions.Item>
						</Descriptions>

						<Divider dashed></Divider>
						<Row>
							<Col span={10} offset={1}>
								<Button type='primary' size='large' style={{ width: '100%' }} onClick={pay}>立即购买</Button>
							</Col>
							<Col span={10} offset={2}>
								<Button size='large' style={{ width: '100%' }} onClick={cartSave}>加入购物车</Button>
							</Col>
						</Row>
					</div>
				</Col>
			</Row>

			{/* <Typography.Title level={3} style={{ color: 'gray', margin: '40px 20px 16px' }}>相似图片</Typography.Title>
			<div style={{ padding: '0 20px' }}>
				<Images data={[]}></Images>
			</div> */}

			<Typography.Title level={3} style={{ color: 'gray', margin: '40px 20px 16px' }}>版权声明</Typography.Title>
			<div style={{ margin: 20 }}>
				本网站所有图片及影视素材均由本公司或版权所有人授权发布，如果您侵犯了该图片素材或影视素材的知识产权，本公司有权依据著作权侵权惩罚性赔偿标准或最高达50万元人民币的法定标准要求赔偿，且有权不以本网站发布的图片授权价格作为参考标准。
			</div>
			<OrderInfo ref={orderInfo}></OrderInfo>
		</div>
	)
}

export default A;