import React, { useState, useEffect, useCallback } from 'react';
import { Table, Button, message, Divider, Typography, } from 'antd';
import request, { ufileDownUrl } from '../../../request';
import moment from 'moment';
import Filter from './filter';
import Footer from './footer'
import { history } from '../../..';
import Edit from '../audit/edit';
/**
 * 格式化时间
 */
const tmformat = 'YYYY-MM-DD HH:mm:ss'

interface Props {
	onParams: (key: string, value: string | number) => void
	params: URLSearchParams
}

const A: React.FC<Props> = (props) => {

	const [data, setData] = useState([]);
	const [total, setTotal] = useState(0);
	const [selectedRowKeys, setselectedRowKeys] = useState([]);
	const [loading, setLoading] = useState(false);
	const [visible, setVisible] = useState(false);
	const [id, setId] = useState('');
	const [okText, setOkText] = useState('');
	const [cancelText, setCancelText] = useState('');
	/**
	 * 批量操作
	 */
	const opts: { [key: string]: (ids: string, queryData: () => void) => void } = {
		/**
			 * 详情
			 * @param value：商品id
			 */
		'detail': (value, queryData) => {
			history.push('/admin/picture/detail?id=' + value);
		},
		/**
	 * 下架
	 * @param value：下架商品id
	 */
		'down': (value, queryData) => {
			request.admin.product.unsell({ id: value }).then((res: any) => {
				if (res.code !== 0) {
					message.error(res.message || '下架失败')
					return;
				} else {
					message.success('下架成功');
					//重新刷新
					queryData();
				}

			})
		},
		/**
	 * 上架
	 * @param value：上架商品id
	 */
		'up': (value, queryData) => {
			request.admin.product.sell({ id: value }).then((res: any) => {
				if (res.code !== 0) {
					message.error(res.message || '上架失败')
					return;
				} else {
					message.success('上架成功');
					queryData();
				}

			})
		},
		/**
	 * 删除
	 * @param value：上架商品id
	 */
		'delete': (key, queryData) => {
			request.admin.product.deleteData({
				id: key
			}).then((res: any) => {
				if (res.code !== 0) {
					message.error(res.message || '删除失败')
					return;
				} else {
					message.success('删除成功');
					queryData();
				}

			})
		},
		/**
	 * 审核通过
	 * @param value：
	 */
		'audit': (key, queryData) => {
			request.admin.product.changeState({
				id: key, state: 1
			}).then((res: any) => {
				if (res.code !== 0) {
					message.error(res.message || '审核失败')
					return;
				} else {
					message.success('审核成功');
					//重新刷新
					queryData();
				}

			})
		},
		/**
	 * 审核不通过
	 * @param value：
	 */
		'auditnopass': (key, queryData) => {
			request.admin.product.changeState({
				id: key, state: -1
			}).then((res: any) => {
				if (res.code !== 0) {
					message.error(res.message || '审核失败')
					return;
				} else {
					message.success('审核成功');
					//重新刷新
					queryData();
				}

			})
		},
		/**
		 * 编辑
		 * @param value：商品id
		 */
		'edit': (value, queryData) => {
			setId(value);
			setCancelText('取消');
			setOkText('确定');
			setVisible(true);
			//重新刷新
			//	queryData();
		}
	}
	const columns: any = [
		{
			title: '图片预览', dataIndex: 'smallImage', width: 120,
			render: (smallImage: string, record: any) => (
				<div style={{ width: 120, height: 120, textAlign: 'center', minWidth: 120 }}>
					<img
						alt={record.mainTitle}
						src={ufileDownUrl  + smallImage}
						style={{ maxWidth: 120, maxHeight: '100%', cursor: 'Pointer' }} onClick={(e) => {
							e.preventDefault();
							opts['detail'](record.id, queryNewData)
						}} />
				</div>
			)
		},
		{
			title: '图片名称', dataIndex: 'mainTitle',
			render: (text: string, record: any) => (
				<div style={{ cursor: 'Pointer' }} onClick={(e) => {
					e.preventDefault();
					opts['detail'](record.productId, queryNewData)
				}}>
					<Typography.Paragraph
						style={{ minWidth: 120 }}
						ellipsis={{ rows: 1, expandable: true }}>{text}
					</Typography.Paragraph>
				</div>)
		},
		{
			title: '价格', dataIndex: 'price', align: 'center', width: 80,
			render: (price: number) => <div>￥&nbsp;{price}</div>
		},
		{
			title: '格式', dataIndex: 'format', align: 'center', width: 80,
			render: (format: string) => <div>{format.replace('.', '').toUpperCase()}</div>
		}, {
			title: '图片大小', dataIndex: 'size', align: 'center', width: 96,
			render: (size: number) => (
				<div>{size > 1048576 ?
					(Math.round(size / 1024) / 1024).toFixed(2) + ' M' :
					Math.round(size / 1024) + ' K'}
				</div>)
		},
		{
			title: '销售状态', dataIndex: 'sellStatus', width: 100,
			render: (sellStatus: number) => <div>{['下架', '未上架', '上架'][sellStatus + 1]}</div>
		},
		{
			title: '添加时间', dataIndex: 'createTime', align: 'center', width: 180,
			render: (createTime: string) => <div>{moment(createTime || '').format(tmformat)}</div>
		}, {
			title: '上架时间', dataIndex: 'sellTime', align: 'center', width: 180,
			render: (sellTime?: string) => <div>{(sellTime && moment(sellTime).format(tmformat)) || ''}</div>
		}, {
			title: '操作', dataIndex: 'id', fixed: 'right', align: 'center', width: 120,
			render: (id: string, record: any) => (
				<div>
					<div style={{ margin: 6 }}></div>
					<Button onClick={(e) => {
						e.preventDefault();
						opts['edit'](id, queryNewData)
					}} icon='edit'>编辑</Button>

					{
						(record.sellStatus === 0 || record.sellStatus === -1) &&
						record.checkState === 1 &&
						<><div style={{ margin: 6 }}></div>
							<Button onClick={(e) => {
								e.preventDefault();
								opts['up'](id, queryNewData)
							}} icon='arrow-up' type='primary'>上架</Button>
						</>
					}
					{
						record.sellStatus === 1 &&
						record.checkState === 1 &&
						<><div style={{ margin: 6 }}></div>
							<Button icon='arrow-down' onClick={(e) => {
								e.preventDefault();
								opts['down'](id, queryNewData)
							}} type='danger'>下架</Button>
						</>
					}

					<div style={{ margin: 6 }}></div>
					<Button onClick={(e) => {
						e.preventDefault();
						opts['detail'](id, queryNewData)
					}} icon='info'>详情</Button>
				</div>
			)
		}]

	/**
	* 多选按钮回调
	*/
	const rowSelection = {
		selectedRowKeys, //这里是起作用的关键
		onChange: (selectedRowKeys: any, selectedRows: any) => {
			setselectedRowKeys(selectedRowKeys);

		}
	}

	const requestList = (value: any) => {

		request.admin.product.getList(value).then((res: any) => {
			setTimeout(() => { setLoading(false) }, 300);
			if (res.code !== 0) {
				message.error(res.message || '返回数据有误')
				setData([]);
				setTotal(0);
			} else {
				setData(res.data.rows.map((v: any) => ({ key: v.id, ...v })));
				setTotal(res.data.total);
			}

		})
	}

	const queryNewData = useCallback(() => {
		requestList({
			categoryId: props.params.get('categoryId'),
			companyId: '',
			createEndTime: props.params.get('createEndTime'),
			createStartTime: props.params.get('createStartTime'),
			flagEnable: '',
			format: '',
			id: '',
			keyword: props.params.get('keyword'),
			mainTitle: '',
			name: '',
			pageIndex: parseInt(props.params.get('pageIndex') || '1') || 1,
			pageSize: 10,
			productType: '',
			sellEndTime: '',
			sellStartTime: '',
			flagDelete: '',
			sellStatus: props.params.get('sellStatus'),
			checkState: '',
			state: 0
		});
		setselectedRowKeys([]);
	}, [props.params])
	/**
	 * 首次获取分类数据
	 */
	useEffect(() => {
		queryNewData();
	}, [props.params, queryNewData])

	return (
		<div style={{ position: 'relative', padding: '0 20px 20px' }}>

			<Filter params={props.params} onParams={props.onParams}></Filter>
			<Divider></Divider>
			<Table loading={loading}
				dataSource={data}
				columns={columns}
				rowSelection={rowSelection}
				pagination={{
					showTotal: (total) => `总共：${ total } `, total: total,
					current: parseInt(props.params.get('pageIndex') || '1'),
					onChange: (v: number) => { props.onParams('pageIndex', v.toString()) }
				}}>

			</Table>
			<Footer disabled={selectedRowKeys.length === 0} onshelvesOpe={v => {
				opts[v] && opts[v](selectedRowKeys.join(), queryNewData)
			}}></Footer>
			<Edit okText={okText} cancelText={cancelText} visible={visible} id={id} setVisible={(v: boolean) => {
				setVisible(v);
			}} refreshData={() => {
				queryNewData();
			}} auditOpe={(id: string) => {
				opts['audit'](id, queryNewData)
			}}></Edit>
		</div>
	)
}

export default A;

interface States {
	categoryId?: string,
	checkState?: number,
	companyId?: string,
	createEndTime?: string,
	createStartTime?: string,
	flagDelete?: number,
	flagEnable?: string,
	format?: string,
	id?: string,
	keyword?: string,
	mainTitle?: string,
	name?: string,
	pageIndex?: number,
	pageSize?: number,
	productType?: string,
	sellEndTime?: string,
	sellStartTime: string,
	sellStatus?: number
	state?: number
}