import React, { useState, useEffect } from 'react'
import { Upload, Row, Col, Form, Icon, Button, message, Avatar } from 'antd';
import { FormComponentProps } from 'antd/lib/form';
import { history } from '../../';
import request, { ufileDownUrl, baseUrl } from '../../request';
import store, { userInfo } from '../../store';
import { RouteComponentProps } from 'react-router';
interface Props {
	setUserData: (headImage: any) => void
	userData: any
}

const R: React.FC<Props & FormComponentProps & RouteComponentProps> = (props) => {
	const [loading, setLoading] = useState(false);
	const [id, setId] = useState('');
	const [headImage, setHeadImage] = useState();
	const handleChange = (info: any) => {
		if (info.file.status === 'uploading') {
			setLoading(true)
			return;
		}
		if (info.file.status === 'done') {
			setHeadImage(info.file.response.original.sha1);
			setLoading(false)
		}
	};

	/**
	 * 首次获取分类数据
	 */
	useEffect(() => {
		setHeadImage(props.userData.headImage);
		setId(props.userData.id);
	}, [props.userData]);

	/**
	* 个人资料修改提交
	*/
	const handleSubmit = (e: React.FormEvent) => {
		e.preventDefault();
		if (headImage === '') {
			message.error('请上传头像，再保存');
			return;
		}
		if (id === '') {
			message.error('用户信息错误');
			return;
		}
		request.admin.user.headImage(
			{
				id: id,
				headImage: headImage,
			}
		).then((res: any) => {
			if (res.code !== 0) {
				message.error(res.message || '保存头像失败')
				return;
			} else {
				store.dispatch({
					type: 'user-update',
					data: {
						accessToken: userInfo.accessToken,
						userInfo: { ...userInfo, headImage: headImage }
					}
				})
				message.success('保存头像成功');
				props.setUserData(headImage);
				//刷新头像	   
				history.push('/user')
			}

		});
	};

	const uploadButton = (
		<div>
			<Icon type={loading ? 'loading' : 'plus'} />
			<div className='ant-upload-text'>上传头像</div>
		</div>
	);

	return (

		<div >
			<Row>
				<Col span={3}>
				</Col>
				<Col span={12} offset={1}>
					<Form onSubmit={handleSubmit} labelCol={{ span: 6 }} wrapperCol={{ span: 15 }}>
						<Form.Item label='头像'>
							<Upload
								name='avatar'
								listType='picture-card'
								accept='.png,.gif,.jpg,.jpeg,.bmp'
								className='avatar-uploader'
								showUploadList={false}
								action={baseUrl + '/admin/upload/photo'}
								onChange={handleChange} >
								{headImage ? <Avatar src={ufileDownUrl  + headImage} size={100} /> : uploadButton}
							</Upload>
							<div style={{color:'red'}}>目前只支持 PNG, GIF, JPG, JPEG, BMP 格式图片</div>
						</Form.Item>
						<Form.Item style={{ paddingLeft: 200 }}>
							<Button type='primary' size='large' htmlType='submit'  >保存</Button>
						</Form.Item>
					</Form>
				</Col>
				<Col span={7} offset={1}>
				</Col>

			</Row >

		</div>

	)
}

export default Form.create<Props & FormComponentProps>()(R);

