import React, { useState, useEffect, useCallback } from 'react';
import { Table, Button, message, Divider, Popconfirm } from 'antd';
import request, { ufileDownUrl } from '../../request';
import { history } from '../..';
import moment from 'moment';
import Search from './search'

interface Props {
	onParams: (key: string, value: string | number) => void
	params: URLSearchParams
}

const A: React.FC<Props> = (props) => {
	/**
	 * 批量操作
	 */
	const opts: { [key: string]: (ids: string, queryData: () => void) => void } = {
		/**
		 * 详情
		 * @param value：商品id
		 */
		'detail': (value, queryData) => {
			history.push('/main/detail?id=' + value)
		},
		/**
		 * 删除
		 * @param key:收藏记录id
		 */
		'delete': (key, queryData) => {
			let id = [{ id: key }];
			deletRecord(id);
		},

	}
	const deletRecord = (id: any) => {
		request.main.ui.history.deleteData(id).then((res: any) => {
			if (res.data.code !== 0) {
				message.error(res.message || '删除失败')
				return;
			} else {
				message.success('删除成功');
				queryRecordData();
			}

		})
	}
	const deletList = () => {
		let arr: any[] = [];
		selectedRowKeys.map((item: string) => (arr.push({ id: item })));
		deletRecord(arr);
	}

	/**
	* table列
	*/
	const columns: any = [
		{
			title: '图片预览', dataIndex: 'smallImage', width: 80,
			render: (smallImage: string, record: any) => (
				<div style={{ width: 80, height: 80, textAlign: 'center', minWidth: 80 }}>
					<img alt={record.mainTitle}
						src={ufileDownUrl  + smallImage}
						style={{ maxWidth: 80, maxHeight: '100%', cursor: 'Pointer' }}
						onClick={(e) => {
							e.preventDefault();
							opts['detail'](record.productId, queryRecordData)
						}} />
				</div>
			)
		},
		{
			title: '图片名称', dataIndex: 'productName', width: 150,
			render: (mainTitle: string, record: any) => (
				<div onClick={(e) => {
					e.preventDefault();
					opts['detail'](record.productId, queryRecordData)
				}} ><span style={{ cursor: 'Pointer' }}>{mainTitle}</span>
				</div>)
		},
		{
			title: '收藏时间', dataIndex: 'createDate', width: 160,
			render: (createDate: string) => (<div>{moment(createDate).format('YYYY-MM-DD HH:mm:ss')}</div>)
		},
		{
			title: '操作', dataIndex: 'id', align: 'center', width: 120,
			render: (id: string, record: any) => (
				<div>
					<div style={{ margin: 6 }}></div>

					<Popconfirm
						title='你确定要删除该条记录吗？'
						onConfirm={(e) => { confirm(e, record.id) }}
						onCancel={cancel}
						okText='确定'
						cancelText='取消'>
						<div style={{ margin: 6 }}></div>
						<Button type='danger' icon='delete' >删除</Button>
					</Popconfirm>
				</div>
			)
		}]

	const cancel = () => {
	}

	const [data, setData] = useState([]);
	const [total, setTotal] = useState(0);
	const [loading, setLoading] = useState(false);
	const [selectedRowKeys, setselectedRowKeys] = useState([]);
	/**
	 * 多选按钮回调
	 */
	const rowSelection = {
		selectedRowKeys,
		onChange: (selectedRowKeys: any, selectedRows: any) => {
			setselectedRowKeys(selectedRowKeys);
		}
	}
	const confirm = (e: any, key: any) => {
		opts['delete'](key, queryRecordData);
	}
	/**
	 * 
	 * @param value 获取收藏记录
	 */
	const queryUserCollectList = (value: any) => {
		request.main.ui.history.getUserCollectionList(value).then((res: any) => {
			setTimeout(() => { setLoading(false) }, 300);
			if (res.code !== 0) {
				message.error(res.message || '返回数据有误')
				setData([]);
				setTotal(0);
			} else {
				setData(res.data.rows.map((v: any) => ({ key: v.id, ...v })));
				setTotal(res.data.total);
			}

		})
	}
	const queryRecordData = useCallback(() => {
		queryUserCollectList(
			{
				createEndTime: props.params.get('createEndTime'),
				createDate: props.params.get('createDate'),
				flagEnable: '',
				id: '',
				keyword: props.params.get('keyword'),
				name: '',
				pageIndex: parseInt(props.params.get('pageIndex') || '1') || 1,
				pageSize: 10,
			});
		setselectedRowKeys([]);
	}, [props.params])
	/**
	 * 首次获取分类数据
	 */
	useEffect(() => {
		queryRecordData();
	}, [props.params, queryRecordData])
	return (
		<div style={{ position: 'relative', padding: '0 20px 20px' }}>
			<Search params={props.params} onParams={props.onParams}></Search>
			<Divider></Divider>
			<Table loading={loading}
				dataSource={data}
				columns={columns}
				rowSelection={rowSelection}
				pagination={{
					showTotal: (total) => `总共：${ total } `,
					total: total,
					current: parseInt(props.params.get('pageIndex') || '1'),
					onChange: (v: number) => { props.onParams('pageIndex', v.toString()) }
				}}>

			</Table>
			<div style={{ position: 'absolute', bottom: 40, left: 20, }}>
				<Popconfirm
					title='你确定要删除选择的记录吗？'
					onConfirm={(e) => { deletList() }}
					onCancel={cancel}
					okText='确定'
					cancelText='取消'>

					<Button type='primary' disabled={selectedRowKeys.length === 0}>批量删除</Button>
				</Popconfirm>
			</div >
		</div>
	)
}

export default A;