import React, { useState } from 'react';
import { Select, Button, Popconfirm } from 'antd';

interface Props {
	onshelvesOpe: (name: string) => void
	disabled: boolean
}

const A: React.FC<Props> = (props) => {
	const [opeName, setOpeName] = useState();
	const cancel = () => {
	}
	return (
		<div style={{ position: 'absolute', bottom: 40, left: 20, }}>
		
			<Select placeholder='批量操作' style={{ width: 150 }} defaultValue={opeName}
				onChange={(value: string) => { setOpeName(value) }} disabled={props.disabled}>
				<Select.Option value='up'>上架</Select.Option>
				<Select.Option value='down'>下架</Select.Option>
			</Select>&nbsp;
			<Popconfirm
				title='你确定要批量操作选择的图片吗？'
				onConfirm={(e) => { props.onshelvesOpe(opeName) }}
				onCancel={cancel}
				okText='确定'
				cancelText='取消'>
				<Button type='primary' disabled={props.disabled}>确定</Button>
			</Popconfirm>
		</div >
	)
}

export default A;