import React, { useState, useEffect } from 'react'
import { Tabs } from 'antd';
import Search from '../../cmps/search';
import bk from '../../assets/search_bk.jpeg';
import { history } from '../../index';
import Category from '../photo/category';
import { RouteComponentProps } from 'react-router';
import request, { requestCache } from '../../request';
import CImages from '../photo/categoryImages';

const App: React.FC<RouteComponentProps> = (props) => {

	const [cates, setCates] = useState<{ name: string, id: string }[]>(requestCache['request.main.ui.category.list_media'] || [])

	useEffect(() => {

		if (props.match.path !== '/main/design') {
			history.replace('/main/design')
		}

		request.main.ui.category.list('design').then(res => { 
			requestCache['request.main.ui.category.list_media'] = res
			setCates(res) })
	}, [props.match.path])

	let pms = new URLSearchParams(props.location.search)

	return (
		<div>
			<Search canEmpty background={bk} onSearch={v => { history.push(`search?keyword=${ encodeURI(v) }&categoryGroup=design`) }}></Search>

			<div style={styles.main}>
				<Tabs tabBarStyle={styles.tabs} defaultActiveKey={pms.get('id') || undefined} onChange={v => {
					pms.set('id', v)
					history.push(props.location.pathname + '?' + pms.toString())
				}}>
					{cates.map((v) => (
						<Tabs.TabPane tab={v.name} key={v.id}>
							<CImages id={v.id} select={v => {
								history.push(props.location.pathname.replace(/[^/]*$/, `category?id=${ v }`))
							}}></CImages>
						</Tabs.TabPane>
					))}
				</Tabs>
			</div>
			<div style={styles.main}>
				<Category id='design' pageIndex={parseInt(pms.get('cateIndex') || '1') || 1} selectIndex={v => {
					pms.set('cateIndex', v + '')
					history.push(props.location.pathname + '?' + pms.toString())
				}}></Category>
			</div>
		</div>
	)
}

const styles: { [key: string]: React.CSSProperties } = {
	main: { backgroundColor: '#fff', marginTop: 120, textAlign: 'center' },
	pagination: { padding: 12 }
}

export default App;