import React, { useState, useEffect } from 'react'
import LoginForm from './loginForm';
import bk from '../../assets/login_bk2.png';

import request from '../../request';
import { history } from '../..';
import { RouteComponentProps } from 'react-router';
import store from '../../store';

const Login: React.FC<RouteComponentProps> = (props) => {

	const [codeImage, setCodeImage] = useState('')
	const [validCode, setValidCode] = useState('')
	const [spinning, setSpinning] = useState(false)

	useEffect(() => {
		store.dispatch({ type: 'user-remove' })
		getCodeImage()
	}, [])

	const handleSubmit = (values: any) => {

		request.main.user.login({ ...values, validCode: validCode }).then((res) => {

			setTimeout(() => {

				if (res.success) {

					const path = new URLSearchParams(props.history.location.search).get('old') || '/user'
					history.push(path)
				} else {
					getCodeImage()
				}

			}, 300);
		})
	}

	/**
	 * 获取验证码
	 */
	const getCodeImage = () => {

		setSpinning(true)
		request.admin.sys.captcha().then((res) => {

			setTimeout(() => {
				setSpinning(false)
				setCodeImage(res.image)
				setValidCode(res.code)

			}, 300);
		})
	}
	return (

		<div style={{ ...styles.item, backgroundImage: `url(${ bk })` }}>
			<LoginForm codeImage={codeImage} spinning={spinning}
				getCodeImage={getCodeImage}
				handleSubmit={handleSubmit}></LoginForm>
		</div>
	)
}

export default Login;

const styles: { [key: string]: React.CSSProperties } = {
	item: { padding: '96px', minHeight: '100vh', backgroundSize: 'cover' }
}