import React from 'react';
import { Row, Col, DatePicker, Input, Icon, } from 'antd';
import moment from 'moment';
const tmformat = 'YYYY-MM-DD';

interface Props {
	onParams: (key: string, value: string | number) => void
	params: URLSearchParams
}

const A: React.FC<Props> = (props) => {

	return (
		<div style={{ paddingTop: 12 }}>
			<div style={{ padding: 6 }}><Icon type='search' />搜索：</div>
			<Row gutter={24}>
				<Col xl={4} lg={8} md={12} xs={24}>
					<DatePicker
						disabledDate={(v) => {
							const dt = props.params.get('createEndTime') &&
								moment(props.params.get('createEndTime') || undefined, tmformat)
							if (dt && v) {
								return v.valueOf() > dt.valueOf()
							}
							return false
						}}
						showTime
						format={tmformat}
						defaultValue={(props.params.get('createDate') &&
							moment(props.params.get('createDate') || undefined, tmformat)) || undefined}
						placeholder='收藏开始时间'
						onChange={(v, s) => { props.onParams('createDate', s) }}
						style={{ width: '100%', padding: 6 }}
					/>
				</Col>
				<Col xl={4} lg={8} md={12} xs={24}>
					<DatePicker
						disabledDate={(v) => {
							const dt = props.params.get('createDate') &&
								moment(props.params.get('createDate') || undefined, tmformat)
							if (dt && v) {
								return v.valueOf() < dt.valueOf()
							}
							return false
						}}
						showTime
						format={tmformat}
						defaultValue={(props.params.get('createEndTime') &&
							moment(props.params.get('createEndTime') || undefined, tmformat)) || undefined}
						placeholder='收藏结束时间'
						onChange={(v, s) => { props.onParams('createEndTime', s) }}
						style={{ width: '100%', padding: 6 }}
					/>
				</Col>

				<Col xl={6} lg={8} md={12} xs={24} >

					<Input.Search
						style={{ width: '100%', padding: 6 }}
						placeholder='关键字' allowClear enterButton
						defaultValue={props.params.get('keyword') || undefined}
						type='primary' onSearch={v => { props.onParams('keyword', v) }}
					/>
				</Col>
			</Row >
		</div>
	)
}

export default A;