import React, { useState, useEffect } from 'react'
import { Button, message } from 'antd';

import store from '../store';


interface Props {
	/**
	 * 背景图
	 */
	background: string,
	/**
	 * 小型局顶搜索框
	 */
	isTop?: boolean,

	/**
	 * 搜索点击
	 * @param keyword 输入框字符串
	 */
	onSearch?: (keyword: string) => void,

	/**
	 * 搜索框值
	 */
	value?: string,
	/**
	 * 是否可以为空
	 */
	canEmpty?: boolean
}

const App: React.FC<Props> = (props) => {

	const [value, setValue] = useState(props.value || '')

	/**
	 * 搜索点击
	 * @param v 
	 */
	const search = () => {

		if (value.trim().length === 0) {
			if (props.canEmpty) {
				props.onSearch && props.onSearch(value)
			} else {
				message.warn('请输入搜索关键字')
			}

		} else {

			if (value !== props.value) {
				props.onSearch && props.onSearch(value)
			}
		}
	}

	/**
	 * 回车事件
	 * @param e 
	 */
	const keydown = (e: React.KeyboardEvent) => {
		if (e.keyCode === 13) {
			search()
		}
	}

	const scrolled = (e: Event) => {

		if (props.isTop) {
			if (window.scrollY > 80 && store.getState().showSearch === false)
				store.dispatch({ type: 'showSearch-show' })
			if (window.scrollY <= 80 && store.getState().showSearch === true)
				store.dispatch({ type: 'showSearch-hide' })
		} else {
			if (window.scrollY > 480 && store.getState().showSearch === false)
				store.dispatch({ type: 'showSearch-show' })
			if (window.scrollY <= 480 && store.getState().showSearch === true)
				store.dispatch({ type: 'showSearch-hide' })
		}
	}

	useEffect(() => {
		setValue(props.value || '')
	}, [props.value])

	useEffect(() => {
		window.addEventListener('scroll', scrolled);

		return () => {
			window.removeEventListener('scroll', scrolled)
		}
	})


	if (props.isTop) {

		return (
			<div style={styles.mainT}>
				<input value={value} placeholder='搜你喜欢的...' style={styles.input}
					onChange={v => setValue(v.currentTarget.value)} onKeyDown={keydown}></input>

				<Button style={styles.search} type='primary' onClick={search} >搜索</Button>
			</div>
		)
	}

	return (

		<div style={{ backgroundImage: `url(${ props.background })`, ...styles.root }}>
			<div style={styles.main}>
				<input value={value} placeholder='搜你喜欢的...' style={styles.input} onChange={v => setValue(v.currentTarget.value)} onKeyDown={keydown} ></input>
				<Button style={styles.search} type='primary' onClick={search} >搜索</Button>
			</div>
		</div>
	)
}

const styles: { [key: string]: React.CSSProperties } = {
	root: {
		backgroundSize: 'cover',
		paddingTop: 240,
		height: 480
	},
	main: {
		maxWidth: 600,
		margin: '0 auto',
		backgroundColor: 'rgba(24,144,255,0.75)',
		display: 'flex',
		padding: 1,
		fontSize: 'large'
	},
	mainT: {
		margin: 16,
		backgroundColor: 'rgba(24,144,255,0.75)',
		display: 'flex',
		fontSize: 'large'
	},
	input: {
		borderWidth: 0,
		flexGrow: 1,
		background: 'rgba(255,255,255,0.75)',
		lineHeight: '48px',
		padding: '0 12px'
	},
	search: {
		width: 120,
		height: '48px',
		fontSize: 'large',
		borderRadius: 0
	}
}

export default App;