import React, { useState, useEffect, useRef, useCallback } from 'react';
import { RouteComponentProps } from 'react-router';
import Search from '../../cmps/search';
import { Pagination, Spin } from 'antd';
import Images from '../../cmps/images';
import { history } from '../../index';
import request, { requestCache } from '../../request';
import { CategoryList } from '../../request/types';

const A: React.FC<RouteComponentProps> = (props) => {



	const [pms, setPms] = useState<URLSearchParams>(new URLSearchParams())
	const [loading, setloading] = useState(true)
	const ref = useRef<HTMLDivElement>(null)
	const [data, setData] = useState<{ total: number, pageIndex: number, rows: CategoryList[] }>(
		requestCache[`request.main.ui.category.photo_${ pms.get('id') || '' }_${ 1 }`] || {
			total: 0,
			pageIndex: 1,
			rows: []
		})

	const loadmore = useCallback(() => {

		if (data.pageIndex * 20 >= data.total || data.pageIndex % 3 === 0 || loading) {
			return
		}

		if (ref.current && ref.current.getBoundingClientRect().bottom <= window.screen.availHeight) {

			setloading(true)

			request.main.ui.category.photo(pms.get('id') || '', data.pageIndex + 1).then(res => {
				setTimeout(() => {
					const dt = {
						total: res.total,
						pageIndex: res.pageIndex,
						rows: [...data.rows, ...res.rows]
					}
					setData(dt)
					requestCache[`request.main.ui.category.photo_${ pms.get('id') || '' }_${ data.pageIndex + 1 }`] = dt
					setloading(false)
					// pms.set('pageIndex', (pms.get('pageIndex') || '1').replace(/(\d*).*/, '$1-' + res.pageIndex))
					// window.history.replaceState({}, '', window.location.pathname + '?' + pms.toString())

				}, 1000);
			})
		}
	}, [data.pageIndex, data.rows, data.total, loading, pms])

	useEffect(() => {
		const l = new URLSearchParams(props.location.search)
		setPms(l)

		request.main.ui.category.photo(l.get('id') || '', parseInt(l.get('pageIndex') || '1') || 1).then(res => {
			setData(res)
			setloading(false)
		})
	}, [props.location.search])

	useEffect(() => {
		window.addEventListener('scroll', loadmore);
		return () => {
			window.removeEventListener('scroll', loadmore);
		}
	}, [loadmore])

	return (
		<div>
			<Search background='' isTop value={(pms && pms.get('keyword')) || ''} onSearch={v => {

				history.push(`search?keyword=${ encodeURI(v) }&categoryId=${ pms.get('id') || '' }`)
			}}></Search>

			<div style={styles.result}>帮您找到相关图片约 {data.total} 张。</div>

			<Images data={data.rows} select={v => {
				history.push(`/main/detail?id=${ data.rows[v].id }`)
			}}></Images>

			<div ref={ref} style={{ padding: 20, textAlign: 'center' }}>
				{
					(data.pageIndex * 20 >= data.total || data.pageIndex % 3 === 0) ?

						<Pagination defaultCurrent={data.pageIndex}
							pageSize={20}
							total={data.total} onChange={v => {
								pms.set('pageIndex', v + '')
								history.push(props.location.pathname + '?' + pms.toString())
								window.scrollTo(0, 0)
							}} /> :
						<Spin />
				}
			</div>
		</div>
	)
}

const styles: { [key: string]: React.CSSProperties } = {
	result: { margin: '16px 0 0', padding: '12px 20px', borderBottom: '#eee solid 1px', fontSize: 'small', color: 'gray' }
}

export default A;