import React from 'react';
import { Typography, InputNumber, Cascader, Checkbox } from 'antd';
import bk from '../../../../assets/bk.png';
import Keywords from './keywords';
import { ImageInfoItem } from '.';
import request from '../../../../request';
import { CascaderOptionType } from 'antd/lib/cascader';

interface Props {
	/**
	 * 是否启用表单检查，错误标红边框
	 */
	checkInfo: boolean
	/**
	 * 单图信息
	 */
	obj: ImageInfoItem
	/**
	 * 接口可选分类
	 */
	cates: any[]
	/**
	 * 更新单图信息
	 */
	onUpdate: (obj: ImageInfoItem, checked: boolean) => void
	/**
	 * 更新分类菜单
	 */
	onUpdateCate: (data: any[]) => void
}

const A: React.FC<Props> = (prop) => {

	const data = prop.obj

	/**
	 * 加载图片分类
	 */
	const loadData = (v?: CascaderOptionType) => {

		if (v) {
			v[v.length - 1].loading = true

			request.admin.cate.getSelectTree({ categoryUsage: v[0].value, id: '0' }).then((data) => {

				setTimeout(() => {

					v[0].loading = false
					v[0].children = data.length ? data.map(v => ({
						value: v.id,
						label: v.name,
						isLeaf: false,
						children: (v.children && v.children.length) ? v.children.map((s: any) => ({
							value: s.id,
							label: s.name,
							isLeaf: true,
						})) : [{
							value: '--null--',
							label: '缺少分类',
							isLeaf: true,
							disabled: true,
						}]
					})) : [{
						value: '--null--',
						label: '缺少分类',
						isLeaf: true,
						disabled: true,
					}]
					prop.onUpdateCate(prop.cates)
				}, 300);
			})
		}
	}

	return (
		<div style={{ display: 'flex', flexWrap: 'wrap', margin: '16px 0' }}>
			{

				data.showCheck &&
				<Checkbox checked={data.checked} style={{ margin: 12 }} onChange={v => {
					data.checked = v.target.checked
					prop.onUpdate(data, data.checked)
				}} />
			}

			{
				data.response && data.response.original &&
				<div style={{ ...styles.imageDiv, background: `url(${ bk })` }}>
					<img style={styles.image} alt={data.name} src={data.response.original.url}></img>
					<Typography.Title level={3} style={styles.imageTitle}>
						{data.response.original.width}x{data.response.original.height}
					</Typography.Title>
				</div>
			}

			<div style={styles.imageInfo}>
				<div style={styles.infoItem}>
					<b>标题：</b>
					<Typography.Paragraph style={{ display: 'inline' }} editable={{
						onChange: v => {
							data.title = v
							prop.onUpdate(data, false)
						}
					}}>{data.title}</Typography.Paragraph>
				</div>
				<div style={styles.infoItem}>
					<b><span style={{ color: 'red' }}>*</span>价格：</b>
					<InputNumber min={0.01} max={9999999}
						value={data.price}
						step={0.01}
						style={{ width: 200 }}
						
						formatter={v => `¥ ${ v }`} onChange={v => {
							data.price = Math.max(v || 0.01, 0.01)
							prop.onUpdate(data, false)
						}}></InputNumber>
				</div>
				<div style={prop.checkInfo && data.cate.length === 0 ?
					{ ...styles.infoItem, ...styles.infoError } :
					styles.infoItem}>
					<b><span style={{ color: 'red' }}>*</span>分类：</b>
					<Cascader options={prop.cates}
						value={data.cate}
						placeholder='请选择图片分类'
						loadData={loadData}
						style={{ width: 200 }} onChange={v => {
							data.cate = v
							prop.onUpdate(data, false)

						}}></Cascader>
				</div>
				<div style={prop.checkInfo && data.keywords.length === 0 ?
					{ ...styles.infoItem, ...styles.infoError } :
					styles.infoItem}>
					<b><span style={{ color: 'red' }}>*</span>关键字：</b>
					<Keywords keys={data.keywords} onUpdate={(v) => {

						data.keywords = v
						prop.onUpdate(data, false)
					}} />
				</div>
			</div>
		</div >
	)
}

const styles: { [key: string]: React.CSSProperties } = {
	imageInfo: { border: '#ddd solid 1px', padding: 10, flexGrow: 1 },
	imageDiv: { width: 200, height: 200, marginRight: 20, position: 'relative' },
	image: { maxWidth: '100%', maxHeight: '100%', border: 'dashed 1px #666' },
	imageTitle: { position: 'absolute', bottom: 0, left: 0, right: 0, textAlign: 'center', backgroundColor: 'rgba(34,34,34,0.53)', color: '#eee' },
	infoItem: { lineHeight: '36px', margin: 4 },
	infoError: { border: '1px solid #e22', padding: '2px 4px', borderRadius: 3 }
}

export default A;