import React, { useState } from 'react';
import { Breadcrumb, Icon, Steps } from 'antd';
import { Link } from 'react-router-dom';
import SelectPic from './selectPic';
import PicInfo, { ImageInfoItem } from './infos'
import MResult from './result';

const { Step } = Steps;

const A: React.FC = () => {

	const [current, setCurrent] = useState(0);
	const [files, setFiles] = useState<ImageInfoItem[]>([])
	const [modelInfo, setModelInfo] = useState<ImageInfoItem>({
		price: 1,
		cate: [],
		title: '',
		checked: false,
		keywords: [],
		uid: '',
		size: 0,
		name: '',
		type: '',
		showCheck: false,
	})
	const [cates, setCates] = useState([
		{
			value: 'photo',
			label: '摄影',
			isLeaf: false,
		},
		{
			value: 'design',
			label: '设计',
			isLeaf: false,
		},
		{
			value: 'media',
			label: '媒体',
			isLeaf: false,
		}
	])

	const steps = [{
		title: '上传图片',
		content: <SelectPic files={files} nextStep={(v) => {
			setFiles(v)
			setCurrent(1)

		}}></SelectPic>
	}, {
		title: '填写基本信息',
		content: <PicInfo files={files} cates={cates} modelInfo={modelInfo}
			onUpdateModel={v => {
				files.filter(v => v.checked)
					.forEach(s => {
						s.keywords = v.keywords
						s.title = v.title
						s.price = v.price
						s.cate = v.cate
					})
				setFiles([...files])
				setModelInfo(v)
			}}
			onUpdateCate={v => {
				setCates([...v])
			}}
			onUpdata={(v) => {

				setFiles([...v])
			}}
			preStep={() => {
				setCurrent(0)
			}}
			nextStep={() => {
				setCurrent(2)
			}}></PicInfo>
	}, {
		title: '提交信息',
		content: <MResult files={files}
			reStart={() => {
				setFiles([])
				setCurrent(0)
			}}
			retry={() => {
				setCurrent(1)
			}}></MResult>
	}]

	return (
		<div style={{ height: '100%' }}>
			<div style={{ height: 60, paddingLeft: 20 }}>
				<Breadcrumb style={{ lineHeight: '60px' }}>
					<Breadcrumb.Item><Link to='/'><Icon type='home' />&nbsp;首页</Link></Breadcrumb.Item>
					<Breadcrumb.Item>图片管理</Breadcrumb.Item>
					<Breadcrumb.Item>添加图片</Breadcrumb.Item>
				</Breadcrumb>
			</div>

			<div style={styles.content}>
				<Steps current={current} style={{ padding: 40 }}>
					{steps.map(item => (<Step key={item.title} title={item.title} />))}
				</Steps>

				<div>
					{steps[current].content}
				</div>
			</div>
		</div>
	)
}

const styles: { [key: string]: React.CSSProperties } = {
	content: { background: '#fff', height: 'calc(100vh - 60px)', overflow: 'scroll' },
}

export default A;