import React from 'react';
import { Card, Form, Input, Icon, Button, Spin, Divider } from 'antd';
import { FormComponentProps } from 'antd/lib/form';
import { Link } from 'react-router-dom';

interface Props extends FormComponentProps {
	codeImage: string
	spinning: boolean
	handleSubmit: (data: any) => void
	getCodeImage: () => void
}

const A: React.FC<Props> = (props) => {

	/**
	 * 提交表单
	 */
	const handleSubmit = (e: React.FormEvent) => {
		e.preventDefault();
		props.form.validateFields((err, values) => {
			if (!err) {
				props.handleSubmit(values)
			}
		});
	}
	/**
	 * 用户名
	 */
	const itemName = () => {
		const { getFieldDecorator } = props.form;

		return getFieldDecorator('userName', {

			rules: [{ required: true, message: '请输入用户名/手机号' }]
		})(<Input
			size='large'
			prefix={<Icon type='user' style={{ color: 'rgba(0,0,0,.25)' }} />}
			placeholder='手机号/用户名'></Input>)
	}

	/**
	 * 密码
	 */
	const itemPassword = () => {
		const { getFieldDecorator } = props.form;

		return getFieldDecorator('password', {

			rules: [{ required: true, message: '请输入登录密码' }]
		})(<Input
			size='large'
			type='password'
			prefix={<Icon type='lock' style={{ color: 'rgba(0,0,0,.25)' }} />}
			placeholder='请输入登录密码'></Input>)
	}

	/**
	 * 验证码
	 */
	const itemCode = () => {
		const { getFieldDecorator } = props.form;

		return (
			<div style={styles.codeRoot}>
				<div style={styles.codeLeft}>
					{getFieldDecorator('myCode', {
						initialValue: '',
						rules: [{ required: true, message: '请输入验证码' }],
					})(<Input size='large' placeholder='请输入验证码' />)}
				</div>
				<div style={styles.codeRight}>
					<Spin spinning={props.spinning}>
						<div style={{ height: 40 }} onClick={props.getCodeImage}>
							{props.codeImage ?
								<img alt='验证码' src={'data:image/png;base64,' + props.codeImage.replace(/[\r\n]/img, '')} style={styles.codeImage}></img> :
								<span>刷新验证码</span>}
						</div>
					</Spin>
				</div>
			</div>
		)
	}

	return (
		<Card style={styles.main} title='登录' extra={<Link to='/'>首页</Link>}>
			<Form onSubmit={handleSubmit}>
				<Form.Item >{itemName()}</Form.Item>
				<Form.Item >{itemPassword()}</Form.Item>
				<Form.Item >{itemCode()}</Form.Item>
				<Form.Item>
					<Button type='primary' size='large' htmlType='submit' style={{ width: '100%' }}>登录</Button>
				</Form.Item>

				<div style={{ textAlign: 'right' }}>
					<Link to='/user/findpassword'><b>找回密码</b></Link>
					<Divider type='vertical'></Divider>
					<Link to='/user/register'><b>注册</b></Link>
				</div>
			</Form>
		</Card>
	)
}

const styles: { [key: string]: React.CSSProperties } = {
	item: { padding: '96px', minHeight: '100vh', backgroundSize: 'cover' },
	main: {
		borderWidth: 0,
		maxWidth: 400, margin: '0 auto', backgroundColor: 'rgba(255,255,255,0.25)',
		boxShadow: '0 0 0 1px rgba(255,255,255,0.25) inset,0 1em 2em rgba(0,0,0,0.35)'
	},
	image: { height: '100%', borderRadius: 8 },
	codeImage: { height: '100%', borderRadius: 3, border: '1px solid #ddd' },
	codeRoot: { display: 'flex' },
	codeLeft: { flex: '100px 1' },
	codeRight: { marginLeft: 8 }
}

export default Form.create<Props>()(A);