import React from 'react';
import { Breadcrumb, Icon, Row, Col, Input, message, Select, Table, Button, Divider, Popconfirm } from 'antd';
import { Link } from 'react-router-dom';
import data from '../../data';
/**
 * 检索按钮
 */
function search(value: string) {
	//得到输入的数据
	if (value === '') {
		message.error('请输入关键词查询');
		return false;
	}

}

class order extends React.Component {
	onStatusChange = (value: string) => {

	}

	onBlur = (value: string) => {

	}
	onSearch = (val: string) => {

	}

	state = {
		items: [],
		total: 0,
		current: 1
	};
	/**
	 * 多选按钮回调
	 */
	rowSelection = {
		onChange: (selectedRowsKeys: any, selectedRows: any) => {
			this.setState({
				items: selectedRows
			}, () => {

			});
		}
	}
	/*
	取消订单
	**/
	cancel = () => {
		message.error('Click on No');
	}
	confirm = () => {
		message.success('Click on Yes');
	}
	columns = [
		{
			title: '订单号', dataIndex: 'id',
			render: (id: number) => <span>{id}</span>,
		},
		{
			title: '套餐名称', dataIndex: 'title',
			render: (title: string) => <span>{title}</span>,
		},
		{
			title: '总数', dataIndex: 'sum', width: 80,
			render: (sum: number) => <span style={styles.greencolor}> {sum}</span>
		},

		{
			title: '总剩余', dataIndex: 'remain', width: 80,
			render: (remain: number) => <span style={styles.redcolor}>{remain}</span>
		},
		{
			title: '价格', dataIndex: 'price', width: 80,
			render: (p: number) => <span style={styles.redcolor}><b className='money_rmb'></b> {p}</span>
		},
		{
			title: '状态', dataIndex: 'status', width: 80,
			render: (p: number) => <span> {p === 0 ? '正常' : '已到期'}</span>
		},
		{
			title: '到期时间', dataIndex: 'date', width: 180,
			render: (date: string) => <span >{date}</span>
		},
		{
			title: '操作', key: 'x',
			render: () => <span>

				<Divider type='vertical' /><Popconfirm
					title='你确定要删除该套餐吗？'
					onConfirm={this.confirm}
					onCancel={this.cancel}
					okText='确定删除'
					cancelText='放弃'>
					<Button >删除</Button>
				</Popconfirm>
			</span>,
		}
	]
	/**
	 * 翻页
	 */
	changePage = (page: number) => {
		this.setState({
			current: page,
		}, () => {

		})
	}
	render() {
		return (
			<div>
				<Breadcrumb style={styles.topItem}>
					<Breadcrumb.Item><Link to='/'><Icon type='home' />&nbsp;首页</Link></Breadcrumb.Item>
					<Breadcrumb.Item>已购套餐</Breadcrumb.Item>
				</Breadcrumb>
				<div style={styles.content}>
					<Row style={styles.selectrow}>
						<Col span={10} offset={1}>
						</Col>
						<Col span={3} offset={1}>
							<Select
								showSearch
								style={{ width: 200 }}
								placeholder='状态'
								onChange={this.onStatusChange}
								onBlur={this.onBlur}
								onSearch={this.onSearch}

							><Select.Option value='0'>已支付</Select.Option >
								<Select.Option value='1'>未支付</Select.Option >
							</Select>
						</Col>
						<Col span={5} offset={1}>
							<Input.Search placeholder='关键词' enterButton onSearch={value => search(value)} allowClear />
						</Col>

					</Row >

					<Table scroll={{ x: '100%' }}
						pagination={{
							pageSize: 10,
							total: this.state.total,
							onChange: this.changePage
						}}

						columns={this.columns}
						rowSelection={this.rowSelection}
						dataSource={data.paidData}
					/>

				</div>
			</div>
		)
	}
}
export default order;
const styles: { [key: string]: React.CSSProperties } = {
	selectrow: { padding: '8px', paddingTop: '14px' },
	topItem: { margin: '16px' },
	content: { marginTop: 16, background: '#fff' },
	footer: { display: 'flex', padding: 12, alignItems: 'center', textAlign: 'right' },
	greencolor: { color: 'black', fontWeight: 'bolder' },
	redcolor: { color: 'red', fontWeight: 'bolder' },
	footerItem2: { flexGrow: 1, color: 'red', margin: '0 12px' }
}
