import React, { useState, useEffect } from 'react';
import { Result, Button, Spin } from 'antd';
import { ImageInfoItem } from './infos'
import request from '../../../request';

interface Props {
	/**
	 * 图片信息
	 */
	files: ImageInfoItem[]
	/**
	 * 保存信息失败，重试
	 */
	retry: () => void
	/**
	 * 上传完成，继续添加新图
	 */
	reStart: () => void
}

const A: React.FC<Props> = (props) => {

	const [msg, setMsg] = useState<{ code: number, message: string }>({ code: 0, message: '' })

	useEffect(() => {

		request.admin.product.saveData(props.files.map(v => ({
			categoryId: v.cate[v.cate.length - 1],
			keyword: v.keywords.join(','),
			mainTitle: v.title,
			original: v.response.original,
			middle: v.response.middle,
			small: v.response.small,
			price: v.price
		}))).then((res) => {

			setTimeout(() => {
				if (res.success) {
					setMsg({ code: 1, message: res.message || '添加图片完成!' })
				} else {
					setMsg({ code: -1, message: res.message || '保存信息出错!' })
				}
			}, 300);
		})

	}, [props.files])

	return (
		<div style={{ textAlign: 'center', minHeight: 120 }}>
			{
				msg.code === -1 &&
				<Result
					status='error'
					title={msg.message}
					extra={<Button type='primary' key='console' style={{ width: 160 }} onClick={() => {
						props.retry()
					}}> 返回重试</Button>}
				/>
			}
			{
				msg.code === 0 && <Spin tip='上传中 ...'></Spin>
			}
			{
				msg.code === 1 &&
				<Result
					status='success'
					title={msg.message}
					extra={<Button type='primary' key='console' style={{ width: 160 }} onClick={() => {
						props.reStart()
					}}> 继续添加</Button>}
				/>
			}
		</div>
	)
}

export default A;