import React from 'react'
import { Layout, Menu, Icon } from 'antd';
import { history } from '..';
import { SelectParam } from 'antd/lib/menu';
import SubMenu from 'antd/lib/menu/SubMenu';
import { RouteComponentProps } from 'react-router';

import store, { UserInfo, userInfo } from '../store';
import { ufileDownUrl } from '../request';
import { connect } from 'react-redux';
import userdefaultimg from '../assets/default.jpg';
import { UserRoute } from '../routes';


const { Content, Sider } = Layout;
interface Props {
	user: UserInfo

}
class SiderDemo extends React.Component<RouteComponentProps & Props> {
	state = {
		collapsed: false,
	};

	/**
	 * 展开(收起)左侧菜单
	 */
	onCollapse = (collapsed: boolean) => {
		this.setState({ collapsed });
	};

	componentDidMount() {

		if (!store.getState().user.accessToken) {
			history.replace('/user/login?old=' + encodeURI(history.location.pathname + history.location.search))
		}
	}

	/**
	 * 菜单选中处理
	 */
	selectItem = (item: SelectParam) => {

		history.push(item.key)
	}

	render() {

		if (!store.getState().user.accessToken) {
			return <div>未登录</div>
		}

		const path = this.props.location.pathname
			.replace(this.props.match.path, '')
			.split(/[\\/]/)
			.filter(v => v.length > 0)
			.join('-')
		const keys = path.length > 0 ? [path] : ['base-userinfo']

		const oKeys = keys.map(v => v.split('-'))[0]

		return (
			<Layout style={styles.root}>
				<Sider theme='light' style={styles.left} collapsible
					collapsed={this.state.collapsed}
					onCollapse={this.onCollapse}>
					<div style={{
						...styles.logoDiv, backgroundImage: `url(${ this.props.user.headImage === null ?
							userdefaultimg :
							ufileDownUrl + this.props.user.headImage })`
					}}>
						<img src={userdefaultimg} style={styles.logo} alt='head' />
					</div>
					{
						this.state.collapsed === false &&
						<div style={{ textAlign: 'center' }}>昵称：{this.props.user.nickName}</div>
					}

					<Menu defaultSelectedKeys={keys}
						defaultOpenKeys={oKeys}
						mode='inline'
						onSelect={this.selectItem}
						style={{ borderRight: 0, marginTop: 12 }}>

						{
							userInfo.userGroup === 'sys' &&
							<Menu.Item key='/admin/home' style={{ backgroundColor: '#1890ff', color: '#eee' }}>
								<Icon type='solution' /><span>后台管理</span>
							</Menu.Item>
						}

						<SubMenu key='base' title={<span><Icon type='user' /><span>个人中心</span></span>}>
							<Menu.Item key='/user/base/userinfo'>我的资料</Menu.Item>
							<Menu.Item key='/user/base/password'>修改密码</Menu.Item>
						</SubMenu>
						<Menu.Item key='/user/orderForm'>
							<Icon type='account-book' /><span>我的订单</span>
						</Menu.Item>
						<Menu.Item key='/user/favorites'>
							<Icon type='star' /><span>我的收藏</span>
						</Menu.Item>
						<Menu.Item key='/user/browseRecord'>
							<Icon type='history' /><span>浏览记录</span>
						</Menu.Item>
						<Menu.Item key='/user/download'>
							<Icon type='cloud-download' /><span>下载记录</span>
						</Menu.Item>

						<Menu.Item key='/user/login' style={styles.logoOut}>
							<Icon type='export' /><span >退出登录</span>
						</Menu.Item>
					</Menu>
				</Sider>
				<Layout>
					<Content >
						<UserRoute></UserRoute>
					</Content>
				</Layout>
			</Layout>
		);
	}
}
export default connect((state: any) => ({ user: state.user }))(SiderDemo);

const styles: { [key: string]: React.CSSProperties } = {
	root: { height: '100vh' },
	left: { height: '100%', overflow: 'scroll', borderRight: '1px solid #eee' },
	right: { margin: '0 16px', overflow: 'scroll' },
	logo: {
		width: '100%',
		opacity: 0,
	},
	logoDiv: {
		padding: '10%', cursor: 'pointer', borderRadius: '50% 50%',
		backgroundSize: 'cover', backgroundClip: 'content-box'
	},
	logoOut: { color: '#eee', fontWeight: 'bold', backgroundColor: '#1890ff' }
}