import React, { useState, useEffect } from 'react';
import { Table, Switch, Layout, Button, Divider, Typography, message, Popconfirm } from 'antd';
import css from './style.module.less';
import Search from 'antd/lib/input/Search';
import request from '../../../request';
import PopInput from '../../../cmps/popInput';
import { RolePeople } from '../../../request/types';

const { Sider } = Layout;

interface Props {
	currentRole: string
	updatePermission: (id: string, name: string) => void
	onDelete: () => void
}

const A: React.FC<Props> = (props) => {

	const [people, setPeople] = useState<RolePeople[]>([])
	const [searchPeople, setSearchPeople] = useState('')

	const colmesLeft: any[] = [
		{
			title: '角色名称', dataIndex: 'name',
			render: (text: string, r: RolePeople) => <Typography.Paragraph editable={{
				onChange: (v: string) => {

					if (v === text) {
						return
					}

					if (!v.trim().length) {
						message.error('角色名不能为空！')
						return
					}
					if (people.some(p => p.name === v)) {
						message.error('角色名已存在！')
						return
					}

					r.name = v
					savePeople(r)
				}
			}}>{text}</Typography.Paragraph>
		},
		{
			title: '启用', dataIndex: 'flagEnable', align: 'center',
			render: (enable: boolean, r: RolePeople) =>
				<Switch checkedChildren='启用' unCheckedChildren='禁用' defaultChecked={enable} onChange={v => {
					r.flagEnable = !enable
					savePeople(r)
				}} />
		},
		{
			title: '操作', key: 'action', align: 'center',
			render: ({ id }: { id: string }) =>
				<div>
					<Popconfirm
						title='你确定要删除该角色吗？'
						okText='确定'
						cancelText='取消'
						onConfirm={(e) => {
							e && e.preventDefault();
							props.onDelete()
							request.admin.role.deleteRole(id).then(res => {
								request.admin.role.getList(searchPeople).then(res => {
									setPeople(res.data)

								})
							})
						}}>

						<Button type='danger' icon='delete' >删除</Button>
					</Popconfirm>
				</div>
		}
	]

	/**
	 * 角色保存
	 */
	const savePeople = (p: RolePeople) => {

		request.admin.role.saveRole(p).then(res => {
			if (res.success) {
				message.success('角色信息已保存！')

				request.admin.role.getList(searchPeople).then(res => {
					setPeople(res.data)
				})
			} else {
				message.success('角色信息保存失败！')
			}
		})
	}
	/**
	 * 新建角色
	 * @param v 角色名
	 */
	const addPeople = (v: string) => {
		request.admin.role.saveRole({
			name: v,
			flagEnable: true
		}).then(res => {

			if (res.success) {
				message.success('新建角色完成，点击修改角色信息！')
				request.admin.role.getList('').then(res2 => {
					setPeople(res2.data)
					props.updatePermission(res.id, (people.find(v => v.id === res.id) || { name: '' }).name)
				})

			} else {
				message.error('新建角色失败！')
			}
		})
	}

	useEffect(() => {
		request.admin.role.getList(searchPeople).then(res => {
			setPeople(res.data)
		})
	}, [searchPeople])

	return (
		<Sider theme='light' width={400}>
			<div style={{ padding: '10px 12px', border: '1px #eee solid' }}>
				<PopInput onInput={addPeople}>
					<Button type='primary' icon='plus' >添加角色</Button>
				</PopInput>

				<Divider type='vertical'></Divider>
				<Search placeholder='角色名' allowClear style={{ width: 240 }} enterButton onSearch={v => {
					setSearchPeople(v)
				}} />
			</div>
			<Table
				columns={colmesLeft}
				dataSource={people}
				defaultExpandAllRows
				showHeader={false}
				onRow={(v, i) => ({
					onClick: (e) => {
						e.preventDefault()
						if (people[i].id !== props.currentRole) {
							props.updatePermission(people[i].id, people[i].name)
						}
					}
				})
				}
				rowClassName={(v, i) => {

					return people[i].id === props.currentRole ? css['role-select'] : '';
				}}
				pagination={false}
				style={{ borderLeft: '1px #eee solid', borderRight: '1px #eee solid' }}>

			</Table>
		</Sider>
	)
}

export default A;