import React from 'react';
import ReactDOM from 'react-dom';
import './index.less';
import { createHashHistory } from 'history';
import { LocaleProvider } from 'antd';
import zh_CN from 'antd/lib/locale-provider/zh_CN';
import moment from 'moment';
import 'moment/locale/zh-cn';
import { Provider } from 'react-redux';
import { MainRT, allPaths } from './routes';
import store from './store';

moment.locale('zh-cn');

export const history = createHashHistory();

const ps: { [key: string]: number } = {}
let _tm: any = 0

history.listen(v => {

	setTimeout(() => {

		if (ps[v.pathname]) {
			window.scrollTo(0, ps[v.pathname])
		} else {
			window.scrollTo(0, 0)
		}
	}, 100);

	document.title = '华光享易 - ' + (allPaths.find(s => s.path.includes(v.pathname)) || { title: '' }).title || ''
})

window.addEventListener('scroll', (p) => {

	clearTimeout(_tm)

	_tm = setTimeout(() => {
		ps[history.location.pathname] = window.scrollY
	}, 101);
})

let beginTime = 0;

window.onunload = function() {
	const differTime = new Date().getTime() - beginTime;

	if (differTime <= 5) {

		localStorage.clear()
	}
}
window.onbeforeunload = function() {
	beginTime = new Date().getTime();
};

ReactDOM.render(
	<Provider store={store}>
		<LocaleProvider locale={zh_CN}>
			<MainRT></MainRT>
		</LocaleProvider>
	</Provider>,

	document.getElementById('root'));
