import React from 'react';
import { Layout, Breadcrumb, Icon, Tabs } from 'antd';
import { Link, RouteComponentProps } from 'react-router-dom';
import MTable from './table';
import { history } from '../../..';

const { Header, Content } = Layout;

const A: React.FC<RouteComponentProps> = (props) => {

	let pms = new URLSearchParams(props.location.search)

	return (
		<Layout >
			<Header style={{ backgroundColor:'rgba(0,0,0,0)', padding: 20 }}>
				<Breadcrumb >
					<Breadcrumb.Item><Link to='/'><Icon type='home' />&nbsp;首页</Link></Breadcrumb.Item>
					<Breadcrumb.Item>图片分类管理</Breadcrumb.Item>
				</Breadcrumb>

			</Header>
			<Content style={{ padding: '0 16px 20px', backgroundColor: '#fff' }}>

				<Tabs activeKey={pms.get('type') || 'photo'} onChange={v => {
					pms.set('type', v)
					history.replace(props.location.pathname + '?' + pms.toString())
				}}>
					<Tabs.TabPane tab='摄影' key='photo'>
						<MTable type='photo'></MTable>
					</Tabs.TabPane>
					<Tabs.TabPane tab='设计' key='design'>
						<MTable type='design'></MTable>
					</Tabs.TabPane>
					<Tabs.TabPane tab='媒体' key='media'>
						<MTable type='media'></MTable>
					</Tabs.TabPane>
				</Tabs>
			</Content>
		</Layout>
	)
}

export default A;