import React from 'react'
import { Row, Col, Form, Input, Icon, Button, Breadcrumb, message } from 'antd';
import { FormComponentProps } from 'antd/lib/form';
import { Link } from 'react-router-dom';
import request from '../../request';
import BackItem from '../../cmps/backItem';
import { history } from '../..';
class R extends React.Component<FormComponentProps> {

	state = {
		confirmDirty: false,
		loading: false
	};

	/**
	 * 密码修改提交 
	 */
	handleSubmit = (e: React.FormEvent) => {
		e.preventDefault();
		this.props.form.validateFieldsAndScroll((err, values) => {
			if (!err) {
				request.admin.user.changePassword(values).then((res: any) => {
					if (res.code !== 0) {
						message.error(res.message || '修改密码失败')
						return;
					} else {
						message.success('修改密码成功,请重新登录');
						history.replace('/user/login');
					}
				})
			}
		});
	};

	/**
	 * 验证密码
	 */
	handleConfirmBlur = (e: any) => {
		const value = e.target.value;

		this.setState({ confirmDirty: this.state.confirmDirty || !!value });
	};

	/**
	 * 验证旧密码
	 */
	itemName = () => {
		const { getFieldDecorator } = this.props.form;
		return getFieldDecorator('oldPassword', {
			rules: [{ required: true, message: '请输入旧密码' }]
		})(<Input
			size='large'
			type='password'
			prefix={<Icon type='lock' style={{ color: 'rgba(0,0,0,.25)' }} />}
			placeholder='旧密码'></Input>)
	}

	/**
	 * 密码验证
	 */
	itemPass = () => {
		const { getFieldDecorator } = this.props.form;
		return getFieldDecorator('newPassword', {
			rules: [{
				required: true, message: '请输入新密码',
				//（6～16位数字／字母组合，区分大小写！）pattern: /^[a-zA-Z0-9]{6,16}$/
			}, {
				validator: (rule, value, callback) => {
					const form = this.props.form;
					if (value && this.state.confirmDirty) {
						form.validateFields(['confirmNewPassword'], { force: true });
					}
					if (value && value === form.getFieldValue('oldPassword')) {
						callback('旧密码和新密码一致，请重新输入新密码！');
					}
					callback();
				}
			}]
		})(<Input
			size='large'
			type='password'
			prefix={<Icon type='lock' style={{ color: 'rgba(0,0,0,.25)' }} />}
			placeholder='新密码'
			//placeholder='6～16位数字／字母组合，区分大小写！'
		></Input>)
	}

	/**
	 * 再次密码验证
	 */
	itemComfim = () => {
		const { getFieldDecorator } = this.props.form;
		return getFieldDecorator('confirmNewPassword', {
			rules: [{
				required: true,
				message: '请输入确认新密码',
				//（6～16位数字／字母组合，区分大小写！）pattern: /^[a-zA-Z0-9]{6,16}$/
			}, {
				validator: (rule, value, callback) => {

					const form = this.props.form;
					if (value && value !== form.getFieldValue('newPassword')) {
						callback('两次密码不一致');
					} else {
						callback();
					}
				}
			}]
		})(<Input
			size='large'
			type='password'
			onBlur={this.handleConfirmBlur}
			prefix={<Icon type='lock' style={{ color: 'rgba(0,0,0,.25)' }} />}
			placeholder='确认新密码'
			//placeholder='6～16位数字／字母组合，区分大小写！'
		></Input>)
	}

	render() {

		return (
			<div>
				<Breadcrumb style={styles.topItem}>
					<Breadcrumb.Item><BackItem /></Breadcrumb.Item>
					<Breadcrumb.Item><Link to='/'><Icon type='home' />&nbsp;首页</Link></Breadcrumb.Item>
					<Breadcrumb.Item>个人中心</Breadcrumb.Item>
					<Breadcrumb.Item>修改密码</Breadcrumb.Item>
				</Breadcrumb>
				<div style={styles.content}>
					<Row>
						<Col span={3}>
						</Col>
						<Col span={12} offset={1}>
							<Form onSubmit={this.handleSubmit} labelCol={{ span: 5 }} wrapperCol={{ span: 12 }}>
								<Form.Item label='旧密码'>
									{this.itemName()}
								</Form.Item>
								<Form.Item label='新密码'>
									{this.itemPass()}
								</Form.Item>
								<Form.Item label='确认新密码'>
									{this.itemComfim()}
								</Form.Item>
								<Form.Item style={{ paddingLeft: 250 }}>
									<Button type='primary' size='large' htmlType='submit' >确认修改</Button>
								</Form.Item>
							</Form>
						</Col>
						<Col span={3} offset={1}>

						</Col>

					</Row >

				</div>
			</div>
		)
	}
}

export default Form.create()(R);

const styles: { [key: string]: React.CSSProperties } = {
	topItem: { margin: '16px' },
	content: { margin: 16, padding: 16, background: '#fff' }
}