import React, { useRef, useState } from 'react';
import { Popover, Button, Input, Divider } from 'antd';
import { PopoverProps } from 'antd/lib/popover';

interface Props extends PopoverProps {
	onInput: (v: string) => void
}

const A: React.FC<Props> = (props) => {

	const [visible, setvisible] = useState(false)
	const ref = useRef<Input>(null)

	return (
		<Popover visible={visible} onVisibleChange={v => {
			setvisible(v)
		}} content={<>
			<Input ref={ref} placeholder='名称'></Input>
			<div style={{ textAlign: 'right', marginTop: 6 }}>
				<Button type='primary' size='small' onClick={(e) => {
					e && e.preventDefault()
					setvisible(false)
					props.onInput(ref.current!.input.value || '')

				}}>确定</Button>
				<Divider type='vertical'></Divider>
				<Button type='default' size='small' onClick={(e) => {
					e && e.preventDefault()
					setvisible(false)
				}}>取消</Button>
			</div>
		</>} trigger='click' {...props}>
			{props.children}
		</Popover>
	)
}

export default A;