import React, { useState, useEffect, useCallback } from 'react';
import { Table, message, Divider, Button } from 'antd';
import request, { ufileDownUrl } from '../../request';
import { history } from '../../index';
import moment from 'moment';
import Search from './search'
interface Props {
	onParams: (key: string, value: string | number) => void
	params: URLSearchParams
}
/**
 * 批量操作
 */
const opts: { [key: string]: (ids: string, queryData: () => void) => void } = {
	/**
	 * 详情
	 * @param value：商品id
	 */
	'detail': (value, queryData) => {
		history.push('/main/detail?id=' + value)
	},
	/**
	 * 删除
	 * @param 
	 */
	'delete': (key, queryData) => {
		request.admin.product.deleteData({
			id: key
		}).then((res: any) => {
			if (res.code !== 0) {
				message.error(res.message || '删除失败')
				return;
			} else {
				message.success('删除成功');
				queryData();
			}

		})
	},

}

const A: React.FC<Props> = (props) => {
	const [data, setData] = useState([]);
	const [total, setTotal] = useState(0);
	const [loading, setLoading] = useState(false);

	const columns = [
		{
			title: '图片预览', dataIndex: 'smallImage', width: 80,
			render: (smallImage: string, record: any) => (
				<div style={{ width: 80, height: 80, textAlign: 'center', minWidth: 80 }}>
					<img alt={record.mainTitle}
						src={ufileDownUrl  + smallImage}
						style={{ maxWidth: 80, maxHeight: '100%', cursor: 'Pointer' }}
						onClick={(e) => {
							e.preventDefault();
							opts['detail'](record.productId, queryDownLoadData)
						}} />
				</div>
			)
		},
		{
			title: '图片名称', dataIndex: 'productName', width: 150,
			render: (mainTitle: string, record: any) => (
				<div onClick={(e) => {
					e.preventDefault();
					opts['detail'](record.productId, queryDownLoadData)
				}} ><span style={{ cursor: 'Pointer' }}>{mainTitle}</span>
				</div>)
		},
		{
			title: '下载时间', dataIndex: 'createTime', width: 230,
			render: (createTime: string) => (<div>{moment(createTime).format('YYYY-MM-DD HH:mm:ss')}</div>)
		},
		{
			title: '操作', dataIndex: 'createTime', width: 230,
			render: (id: string, record: any) => (<div>
				<Button icon='download' type='primary' onClick={(e) => {
					request.main.ui.order.download(record.orderId).then((res) => {

						try {
							const des = new Headers(res.headers).get('content-disposition') as string;
							const blob = new Blob([res.data])
							const downloadElement = document.createElement('a');
							const href = window.URL.createObjectURL(blob); //创建下载的链接
							downloadElement.href = href;
							downloadElement.download = des.split(';')[1].split('=')[1]; //下载后文件名
							document.body.appendChild(downloadElement);
							downloadElement.click(); //点击下载
							document.body.removeChild(downloadElement); //下载完成移除元素
							window.URL.revokeObjectURL(href); //释放掉blob对象
							setTimeout(() => {
								queryDownLoadData();
							}, 300);
						} catch (e) {
						}
					})
				}} >下载</Button>
			</div>)
		},
	]

	const requestList = (value: any) => {
		request.main.ui.history.getDownList(value).then((res: any) => {
			setTimeout(() => { setLoading(false) }, 300);
			if (res.code !== 0) {
				message.error(res.message || '返回数据有误')
				setData([]);
				setTotal(0);
			} else {
				setData(res.data.rows.map((v: any) => ({ key: v.id, ...v })));
				setTotal(res.data.total);
			}

		})
	}

	const queryDownLoadData = useCallback(() => {
		requestList(
			{
				flagEnable: '',
				id: '',
				keyword: props.params.get('keyword') === null ? '' : props.params.get('keyword'),
				name: '',
				pageIndex: parseInt(props.params.get('pageIndex') || '1') || 1,
				pageSize: 10,
			});
	}, [props.params])
	/**
	 * 首次获取分类数据
	 */
	useEffect(() => {
		queryDownLoadData();
	}, [props.params, queryDownLoadData])

	return (
		<div>
			<Search params={props.params} onParams={props.onParams}></Search>
			<Divider></Divider>
			<Table loading={loading}
				dataSource={data}
				columns={columns}
				pagination={{
					showTotal: (total) => `总共：${ total } `,
					total: total,
					current: parseInt(props.params.get('pageIndex') || '1'),
					onChange: (v: number) => { props.onParams('pageIndex', v.toString()) }
				}}>

			</Table>
		</div>
	)
}

export default A;
