import React, { useState, useEffect, useCallback } from 'react';
import { Link } from 'react-router-dom';
import { FormComponentProps } from 'antd/lib/form';
import { Breadcrumb, Icon, Tabs, message } from 'antd';
import Info from './info';
import Images from './images';
import request from '../../request';
import BackItem from '../../cmps/backItem';
const A: React.FC<FormComponentProps> = () => {
	const [data, setData] = useState([]);

	const queryData = useCallback(() => {
		request.admin.user.getUserInfo().then((res: any) => {

			console.log('==>',res);
			
			if (res.code !== 0) {
				message.error(res.message || '返回数据有误')
				setData([]);
			} else {
				setData(res.data);
			}
		})
	}, [])
	/**
	 * 首次获取分类数据
	 */
	useEffect(() => {
		queryData();
	}, [queryData])

	return (

		<div >
			<Breadcrumb style={styles.topItem}>
				<Breadcrumb.Item><BackItem /></Breadcrumb.Item>
				<Breadcrumb.Item><Link to='/'><Icon type='home' />&nbsp;首页</Link></Breadcrumb.Item>
				<Breadcrumb.Item>个人中心</Breadcrumb.Item>
				<Breadcrumb.Item>我的资料</Breadcrumb.Item>
			</Breadcrumb>
			<div style={styles.content}>
				<Tabs >
					<Tabs.TabPane tab='修改头像' key='headImage'>
						<Images userData={data} setUserData={(imgurl: string) => {
							let nData = { ...data, headImage: imgurl };
							setData(nData);
						}}  ></Images>
					</Tabs.TabPane>
					<Tabs.TabPane tab='修改个人资料' key='info'>
						<Info userData={data} setUserData={(values: any) => {
							let nData = {
								...data, userName: values.userName,
								mobile: values.mobile,
								nickName: values.nickName,
								trueName: values.trueName,
							};
							setData(nData);
						}} ></Info>
					</Tabs.TabPane>
				</Tabs>

			</div>
		</div>
	)
}

const styles: { [key: string]: React.CSSProperties } = {
	root: { backgroundColor: '#fff', },
	topItem: { margin: 16 },
	content: { marginTop: 16, background: '#fff' },
}

export default A;
