import React from 'react';
import { Link, RouteComponentProps } from 'react-router-dom';
import { Breadcrumb, Icon } from 'antd';
import List from './list';
import { history } from '../../../index';
const A: React.FC<RouteComponentProps> = (props) => {
	let pms = new URLSearchParams(props.location.search)
	return (

		<div >
			<Breadcrumb style={{ margin: 16 }}>
				<Breadcrumb.Item><Link to='/'><Icon type='home' />&nbsp;首页</Link></Breadcrumb.Item>
				<Breadcrumb.Item>图片管理</Breadcrumb.Item>
				<Breadcrumb.Item>图片管理</Breadcrumb.Item>
			</Breadcrumb>
			<div style={styles.content}>
				<List params={pms} onParams={(k: string, v: any) => {
					if (v) pms.set(k, `${v}`)
					else pms.delete(k);

					history.replace(props.location.pathname + '?' + pms.toString())
				}} ></List>
			</div>
		</div>
	)
}

const styles: { [key: string]: React.CSSProperties } = {
	root: { backgroundColor: '#fff', },
	content: { marginTop: 16, background: '#fff' },
}

export default A;
