import React from 'react'
import { Card, Form, Input, Icon, Button, message } from 'antd';
import { FormComponentProps } from 'antd/lib/form';
import { Link } from 'react-router-dom';
import request from '../../request';
import { history } from '../..';
class R extends React.Component<FormComponentProps> {
	state = {
		codeImage: '',
		spinning: false,
		confirmDirty: false,
		loading: false,
		liked: true,
		code: '',
		count: 60,
	};

	/**
	 * 提交注册
	 */
	handleSubmit = (e: React.FormEvent) => {
		e.preventDefault();
		this.props.form.validateFields((err, values) => {
			if (!err) {
				request.main.user.backPassword({
					code: values.code,
					moblie: values.moblie,
					password: values.password
				}).then((res: any) => {
					
					setTimeout(() => {
						this.setState({
							loading: false
						});
					}, 300);
					if (res.code !== 0 && res != null) {
						//message.error(res.message)
						message.success('找回密码失败');
						return;
					} else {
						message.success('找回密码成功,请登录');
						history.replace('/user/login')
					}
				})
			}
		});
	};

	/**
	 * 验证密码
	 */
	handleConfirmBlur = (e: any) => {
		const value = e.target.value;

		this.setState({ confirmDirty: this.state.confirmDirty || !!value });
	};

	/**
	 * 手机号
	 */
	itemName = () => {
		const { getFieldDecorator } = this.props.form;
		return getFieldDecorator('moblie', {
			rules: [{ required: true, message: '请输入正确的手机号！', pattern: /^(1[3-9])\d{9}$/ }]
		})(<Input
			size='large'
			prefix={<Icon type='user' style={{ color: 'rgba(0,0,0,.25)' }} />}
			placeholder='手机号'></Input>)
	}

	/**
	 * 验证码
	 */
	itemCode = () => {
		const { getFieldDecorator } = this.props.form;
		return (
			<div style={styles.codeRoot}>
				<div style={styles.codeLeft}>
					{getFieldDecorator('code', {
						rules: [
							{ required: true, message: '请输入正确的验证码！' },
							{
								validator: (rule, value, callback) => {
									const form = this.props.form;
									if (value !== this.state.code) {
										form.validateFields(['code'], { force: true });
									}
									callback();
								}
							}
						]
					})(<Input
						size='large'
						onBlur={this.handleConfirmBlur}
						prefix={<Icon type='safety-certificate' style={{ color: 'rgba(0,0,0,.25)' }} />}
						placeholder='验证码'></Input>)}
				</div>
				<div style={styles.codeRight}>
					<Button size='large' style={{ width: '100%' }} onClick={this.getSendSms}>
						{
							this.state.liked ?
								<span>获取验证码</span>
								:
								<span className='count_second'>{this.state.count + 's'}</span>
						}</Button>
				</div>
			</div>
		)
	}
	countDown() {
		const { count } = this.state;
		if (count === 1) {
			this.setState({
				count: 60,
				liked: true,
			});
		} else {
			this.setState({
				count: count - 1,
				liked: false,
			});
			setTimeout(this.countDown.bind(this), 1000);
		}
	};
	/**
	 * 获取验证码
	 */
	getSendSms = () => {

		this.props.form.validateFields(['moblie'], { force: true });

		let phone = this.props.form.getFieldValue('moblie');
		if (phone === undefined || phone === '') {
			//message.error('请先输入手机号');
			return
		} else if (!(/^(1[3-9])\d{9}$/.test(phone))) {
			//message.error('手机号格式不正确')
			return
		}
		if (!this.state.liked) {
			return
		}
		this.countDown();
		request.main.sms.send({ phoneNumber: phone }).then((res: any) => {
			
			setTimeout(() => {
				this.setState({
					loading: false
				});
			}, 300);
			if (res.data.code !== 0) {
				message.error(res.message || '获取验证码有误')
				this.setState({
					code: ''
				});
			} else {
				this.setState({
					code: res.data
				});
			}
		})

	}
	itemPass = () => {
		const { getFieldDecorator } = this.props.form;
		return getFieldDecorator('password', {
			rules: [{
				required: true,
				message: '请输入新密码',
				//pattern:（6～16位数字／字母组合，区分大小写！） /^[a-zA-Z0-9]{6,16}$/
			}, {

			}]
		})(<Input
			size='large'
			type='password'
			prefix={<Icon type='lock' style={{ color: 'rgba(0,0,0,.25)' }} />}
			placeholder='新密码'
			//placeholder='6～16位数字／字母组合，区分大小写！'
		></Input>)
	}

	render() {
		return (

			<div style={styles.item}>
				<Card style={styles.main} title='找回密码' extra={<Link to='/user/login'>登录</Link>}>
					<Form onSubmit={this.handleSubmit}>
						<Form.Item >
							{this.itemName()}
						</Form.Item>
						<Form.Item >
							{this.itemCode()}
						</Form.Item>
						<Form.Item >
							{this.itemPass()}
						</Form.Item>
						<Form.Item>
							<Button type='primary' size='large' htmlType='submit' style={{ width: '100%' }}>确定</Button>
						</Form.Item>
					</Form>
				</Card>
			</div>
		)
	}
}

export default Form.create()(R);

const styles: { [key: string]: React.CSSProperties } = {
	item: { textAlign: 'center', margin: '24px auto' },
	main: { width: '100%', margin: '0 auto', maxWidth: 480, textAlign: 'left' },
	codeRoot: { display: 'flex' },
	codeLeft: { flex: '100px 1' },
	codeRight: { width: '120px', marginLeft: 8 }
}