import React, { useState, useEffect, useCallback, useRef } from 'react';
import { Popconfirm, Divider, Avatar, Table, Button, message } from 'antd';
import request, { ufileDownUrl } from '../request';
import { CartInfo } from '../request/types';
import OrderInfo from '.././user/orderForm/orderInfo';
import { history } from '../';
interface Props {
	onParams: (key: string, value: string | number) => void
	params: URLSearchParams
}

const A: React.FC<Props> = (props) => {

	const [data, setData] = useState<CartInfo[]>([]);
	const [loading, setLoading] = useState(false);
	const [selectedRowKeys, setselectedRowKeys] = useState<string[]>([]);
	const orderInfo = useRef<OrderInfo>(null)

	/**
	 * 多选按钮回调
	 */
	const rowSelection = {
		selectedRowKeys,
		onChange: (selectedRowKeys: any, selectedRows: any) => {
			setselectedRowKeys(selectedRowKeys);
		}
	}
	/**
	 * 表格底部信息统计
	 */
	function footer() {
		return (
			<div style={styles.footer}>
				<div style={styles.delCart}>
					<Popconfirm
						title='你确定要删除该条记录吗？'
						onConfirm={(e) => { toOnDeleteClick(selectedRowKeys.map(v => ({ id: v }))) }}
						okText='确定'
						cancelText='取消'>

						<Button type='primary' size='large' disabled={selectedRowKeys.length === 0}>删除选中图片</Button>
					</Popconfirm>
				</div>

				<div style={styles.footerItem2}>已选图片数量: <b style={{ color: '#f22e00' }}>{selectedRowKeys.length}</b></div>
				<Divider type='vertical'></Divider>
				<div >支付总价:￥<b style={styles.delCart}>{data.filter(v => selectedRowKeys.includes(v.productId)).reduce((p, v) => p + v.price, 0)} </b>元</div>
				<Divider type='vertical'></Divider>
				<div>
					<Popconfirm title='确定提交订单？' cancelText='取消' okText='确定'
						disabled={selectedRowKeys.length === 0} onConfirm={() => {

							request.main.ui.order.saveData(selectedRowKeys.map(v => ({ productId: v, productCount: 1 }))).then(res => {
								if (res.success) {
									orderInfo.current && orderInfo.current.show(res.data)

									queryNewData()
								}
							})
						}}>
						<Button type='primary' size='large' disabled={selectedRowKeys.length === 0}>提交订单</Button>
					</Popconfirm>

				</div>
				<OrderInfo ref={orderInfo}></OrderInfo>
			</div>
		)
	}

	const toOnDeleteClick = (key: any) => {

		request.main.ui.cart.deleteData(key).then((res) => {
			if (res.success) {

				message.success('删除成功');
				requestList();
				setselectedRowKeys([]);
			}
		})
	}
	const requestList = () => {
		setLoading(true)
		request.main.ui.cart.getList().then((res) => {
			setTimeout(() => {
				setData(res.map((v) => ({ key: v.productId, ...v })));
				setLoading(false)
			}, 300);
		})
	}
	const queryNewData = useCallback(() => {
		requestList();
		setselectedRowKeys([]);
	}, [])
	/**
	* 首次获取分类数据
	*/
	useEffect(() => {
		queryNewData();
	}, [props.params, queryNewData])
	const columns: any[] = [
		{
			title: '商品名称', dataIndex: 'productName', width: 150,
			render: (productName: string, record: any) => (<div style={{ cursor: 'pointer' }} onClick={(e) => {
				e.preventDefault();
				history.push('/main/detail?id=' + record.productId);
			}} >{productName}</div>)
		}, {
			title: '图片预览', dataIndex: 'smallImage', align: 'center', width: 160,
			render: (smallImage: string, record: any) => (
				<div style={{ width: 120, height: 120, textAlign: 'center', minWidth: 120, cursor: 'pointer' }} onClick={(e) => {
					e.preventDefault();
					history.push('/main/detail?id=' + record.productId);
				}} >
					<Avatar shape='square'
						src={ufileDownUrl  + smallImage} size={100}
						style={{ maxWidth: '100%', maxHeight: '100%' }} />
				</div>
			)
		},
		{
			title: '价格', dataIndex: 'price',
			render: (price: number) => <span><b className='money_rmb'></b>{price}</span>
		},
		{
			title: '操作', key: 'x',
			render: (id: string, record: any) => (<Popconfirm
				title='你确定要删除该条记录吗？'
				onConfirm={(e) => { toOnDeleteClick([{ id: record.productId }]) }}
				okText='确定'
				cancelText='取消'>

				<Button type='danger' icon='delete' >删除</Button>
			</Popconfirm>
			)
		}
	]
	return (
		<div>
			<Table
				loading={loading}
				dataSource={data}
				columns={columns}
				rowSelection={rowSelection}
				scroll={{ x: '100%' }}
				footer={footer}
				pagination={false}>

			</Table>
		</div>
	)
}
const styles: { [key: string]: React.CSSProperties } = {
	topItem: { margin: '16px' },
	delCart: { fontSize: 'large', color: '#f22e00', fontWeight: 'bold' },
	content: { background: '#fff' },
	footer: { display: 'flex', padding: 12, alignItems: 'center', textAlign: 'right' },

	footerItem2: { flexGrow: 1 }
}
export default A;