import React, { useState, useEffect } from 'react';
import { Layout, Drawer, Menu } from 'antd';
import { Button } from 'antd/lib/radio';
import MenuItem from 'antd/lib/menu/MenuItem';


const { Sider, Content } = Layout;
const A: React.FC = () => {

	const [visible, setVisible] = useState(false)
	const [width, setWidth] = useState(document.body.clientWidth)

	const handleW = () => {
		setWidth(document.body.clientWidth)
	}

	useEffect(() => {
		window.addEventListener('resize', handleW)
		return () => {
			window.removeEventListener('resize', handleW)
		};
	}, [])

	return (
		<>
			{
				width > 576 ?
					<div>
						<Layout>
							<Sider collapsible theme='light'>
								<Menu defaultSelectedKeys={['1']}>
									{
										new Array(18).fill(1).map((v, i) => <MenuItem key={i}>菜单 {i}</MenuItem>)
									}
								</Menu>
							</Sider>
							<Content>content</Content>
						</Layout>
					</div> :
					<div style={{ position: 'relative'}}>
						<Button onClick={() => setVisible(true)} style={{position:'absolute'}}>展开</Button>
						<Layout style={{height:'100%'}}>
							<Content>content</Content>
						</Layout>
						<Drawer
							placement="left"
							closable={false}
							onClose={() => setVisible(false)}
							visible={visible}
							width={200}
							bodyStyle={{ padding: 0 }}
						>
							<Menu defaultSelectedKeys={['1']}>
								{
									new Array(18).fill(1).map((v, i) => <MenuItem key={i}>菜单 {i}</MenuItem>)
								}
							</Menu>
						</Drawer>
					</div>
			}
		</>
	)
}

//const styles: { [key: string]: React.CSSProperties } = {
//}

export default A;