import React, { useState, useRef, useCallback, useEffect } from 'react';
import { RouteComponentProps } from 'react-router';
import Search from '../../cmps/search';
import { Radio, Divider, Menu, Icon, Dropdown, Pagination, Spin } from 'antd';
import RadioGroup from 'antd/lib/radio/group';
import Images from '../../cmps/images';
import { history } from '../../index';
import { CategoryList } from '../../request/types';
import request from '../../request';

const A: React.FC<RouteComponentProps> = (props) => {

	const [data, setData] = useState<{ total: number, pageIndex: number, rows: CategoryList[] }>({
		total: 0,
		pageIndex: 1,
		rows: []
	})

	const [pms, setPms] = useState<URLSearchParams>(new URLSearchParams())
	const [loading, setloading] = useState(true)
	const ref = useRef<HTMLDivElement>(null)

	const loadmore = useCallback(() => {

		if (data.pageIndex * 20 >= data.total || data.pageIndex % 3 === 0 || loading) {
			return
		}

		if (ref.current && ref.current.getBoundingClientRect().bottom <= window.screen.availHeight) {

			setloading(true)

			request.main.ui.product.advanceQuery({
				categoryGroup: pms.get('categoryGroup') || undefined,
				categoryId: pms.get('categoryId') || undefined,
				format: pms.get('format') || undefined,
				keyword: pms.get('keyword') || undefined,
				pageIndex: data.pageIndex + 1,
				pageSize: 20,
				sort: pms.get('sort') || undefined
			}).then(res => {
				setTimeout(() => {
					setData({
						total: res.total,
						pageIndex: res.pageIndex,
						rows: [...data.rows, ...res.rows]
					})
					setloading(false)
					// pms.set('pageIndex', (pms.get('pageIndex') || '1').replace(/(\d*).*/, '$1-' + res.pageIndex))
					// window.history.replaceState({}, '', window.location.pathname + '?' + pms.toString())

				}, 1000);
			})
		}
	}, [data.pageIndex, data.rows, data.total, loading, pms])

	useEffect(() => {
		const l = new URLSearchParams(props.location.search)
		setPms(l)

		request.main.ui.product.advanceQuery({
			categoryGroup: l.get('categoryGroup') || undefined,
			categoryId: l.get('categoryId') || undefined,
			format: l.get('format') || undefined,
			keyword: l.get('keyword') || undefined,
			pageIndex: parseInt(l.get('pageIndex') || '1') || 1,
			pageSize: 20,
			sort: l.get('sort') || undefined
		}).then(res => {
			setData(res)
			setloading(false)
		})
	}, [props.location.search])

	useEffect(() => {
		window.addEventListener('scroll', loadmore);
		return () => {
			window.removeEventListener('scroll', loadmore);
		}
	}, [loadmore])

	const menu = (
		<Menu defaultSelectedKeys={[pms.get('format') || '']} onClick={v => {
			if (v.key !== '-') pms.set('format', v.key)
			else pms.delete('format')
			pms.delete('pageIndex')
			history.push(props.location.pathname + '?' + pms.toString())

		}}>
			<Menu.Item key='-'>默认</Menu.Item>
			<Menu.Item key='PNG'>PNG</Menu.Item>
			<Menu.Item key='GIF'>GIF</Menu.Item>
			<Menu.Item key='JPEG'>JPEG</Menu.Item>
			<Menu.Item key='ICON'>ICON</Menu.Item>
			<Menu.Item key='OTHER'>其它</Menu.Item>
		</Menu>
	);

	return (
		<div>

			<Search background='' isTop value={pms.get('keyword') || ''} canEmpty onSearch={v => {
				if (v) pms.set('keyword', v)
				else pms.delete('keyword')
				pms.delete('pageIndex')
				history.push(props.location.pathname + '?' + pms.toString())

			}}></Search>

			<div style={styles.result}>帮您找到相关图片约 {data.total} 张。</div>

			<div style={{ padding: 16 }}>

				<RadioGroup defaultValue={pms.get('sort') || ''} onChange={v => {

					if (v.target.value) pms.set('sort', v.target.value)
					else pms.delete('sort')
					pms.delete('pageIndex')

					history.push(props.location.pathname + '?' + pms.toString())
				}}>
					<Radio value='new'>最新</Radio>
					<Radio value='hot'>最热</Radio>
					<Radio value=''>默认</Radio>
				</RadioGroup>
				<Divider type='vertical'></Divider>
				<Dropdown overlay={menu}>
					<span > 图片格式 <Icon type='down' /></span>
				</Dropdown>
			</div>

			<Images data={data.rows} select={v => {
				history.push(`/main/detail?id=${ data.rows[v].id }`)
			}}></Images>

			<div ref={ref} style={{ padding: 20, textAlign: 'center' }}>
				{
					(data.pageIndex * 20 >= data.total || data.pageIndex % 3 === 0) ?

						<Pagination defaultCurrent={data.pageIndex}
							pageSize={20}
							total={data.total} onChange={v => {
								pms.set('pageIndex', v + '')
								history.push(props.location.pathname + '?' + pms.toString())
								window.scrollTo(0, 0)
							}} /> :
						<Spin />
				}
			</div>
		</div>
	)
}

const styles: { [key: string]: React.CSSProperties } = {
	result: {
		margin: '16px 0 0', padding: '12px 20px',
		borderBottom: '#eee solid 1px', fontSize: 'small', color: 'gray'
	}
}

export default A;