import React, { useEffect, useState } from 'react';
import { ShoppingCart, UserInfo } from '../../store';
import request, { ufileDownUrl } from '../../request';
import { CartInfo } from '../../request/types';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { Button, Divider, Avatar, Popover, List } from 'antd';
import { history } from '../..';

interface Props {
	shoppingCart: ShoppingCart
	user: UserInfo
	path: any
}

const A: React.FC<Props> = (props) => {

	const [data, setData] = useState<CartInfo[]>([])
	const [loading, setloading] = useState(false)

	/**
	 * 购物车移除
	 * @param id 
	 */
	const cartRemove = (id: string) => {

		setloading(true)
		request.main.ui.cart.deleteData([{ id }]).then(res => {

			if (res.success) {
				request.main.ui.cart.getList().then(res => {
					setTimeout(() => {
						setloading(false)
						setData(res)
					}, 300);
				})
			} else {
				setloading(false)
			}
		})
	}

	/**
	 * 项渲染
	 * @param item 
	 */
	const rdItem = (item: CartInfo) => (
		<List.Item >
			<div style={{
				width: 160, height: 100,
				backgroundImage: `url(${ ufileDownUrl + item.smallImage })`,
				backgroundSize: 'cover'
			}} onClick={(e) => {
				e.preventDefault();
				history.push('/main/detail?id=' + item.productId);
			}}>

			</div>
			<div style={{ textAlign: 'center', width: 100, color: '#f22e00' }}>
				<span className='money_rmb'>{item.price}</span>
			</div>
			<Button type='danger' shape='circle' icon='delete' onClick={() => {
				cartRemove(item.productId)
			}}></Button>

		</List.Item>
	)

	useEffect(() => {
		if (props.user.accessToken) {
			request.main.ui.cart.getList().then(res => {
				setData(res)
			})
		}
	}, [props.shoppingCart.count, props.user.accessToken])

	if (props.user.accessToken) {
		return (
			<span>
				<Popover title='我的购物车' placement='bottomRight' content={
					<List loading={loading} style={{ width: 300, cursor: 'pointer' }}
						dataSource={data}
						size='small'
						renderItem={rdItem}
						footer={<div>
							<span style={{ lineHeight: '30px' }}>共 <b style={{ color: '#f22e00' }}>{data.length}</b> 张图片
								<Divider type='vertical'></Divider>
								
								共计 <b style={{ color: '#f22e00' }} className='money_rmb'>{data.reduce((p, c) => p + c.price, 0)}</b>元 </span>
							<Button style={{ float: 'right' }} type='primary' onClick={v => {
								history.push('/shoppingcart')
							}}>去购物车</Button>
						</div>}></List>
				}>
					<Link to='/shoppingcart'>
						<Button loading={props.shoppingCart.loading} type='link' icon='shopping-cart'>
							({props.shoppingCart.count})
						</Button>
					</Link>
				</Popover>

				<Divider type='vertical' />
				<Link to='/user'>
					<Avatar src={ufileDownUrl + props.user.headImage} />
					<Divider type='vertical' />
					{props.user.nickName}
				</Link>
			</span>
		)
	}

	return (
		<span>
			<Button type='link' icon='shopping-cart' color='gray' onClick={() => {

				history.push('/user/login?old=' + encodeURI('/shoppingcart'))
			}}>(0)</Button>
			<Divider type='vertical' />
			<Button type='link' onClick={() => { history.push('/user/login', props.path) }}>登录</Button>
			<Divider type='vertical' />
			<Button type='link' onClick={() => { history.push('/user/register', props.path) }}>注册</Button>
		</span>
	)
}

export default connect((state: any) => ({ shoppingCart: state.shoppingCart, user: state.user }))(A);
