import React from 'react';

import Test from './test';

import AdminBK from './admin/adminbk';
import Main from './main';
import Admin from './admin';
import { Router, Route, Switch } from 'react-router';

import { BackTop } from 'antd';
import ALogin from './admin/login';
import FindPwd from './user/login/findpassword';
import User from './user';
import ShoppingCart from './shoppingCart';

// Main
import Login from './user/login';
import Register from './user/login/register';

// Main home
import MainCate from './main/cate';
import Media from './main/media';
import Design from './main/design';
import Search from './main/search';
import MainDetail from './main/detail';
import Photo from './main/photo';

//Admin
import Home from './admin/home';
import Company from './admin/info/company';

// Admin order
import AdminDetail from './admin/order/detail';
import OrderList from './admin/order/list';
import Statisstics from './admin/order/statistics'

// Admin picture
import AddPicture from './admin/picture/addPic';
import Manager from './admin/picture/manager';
import PDetail from './admin/picture/detail';
import Audit from './admin/picture/audit';

// Admin system
import Cate from './admin/system/cate';
import Role from './admin/system/role';
import SystemUser from './admin/system/user';

// User
import Download from './user/download';
import Favorites from './user/favorites';
import BrowseRecord from './user/browseRecord';
import OrderForm from './user/orderForm';
import Discount from './user/discount';

// User packages
import Paymented from './user/packages/paymented';
import Unpaid from './user/packages/unpaid';

// User user
import Info from './user/user/info';
import UserInfo from './user/user/userinfo';
import Images from './user/user/images';
import Password from './user/user/password';
import { history } from '.';
import { rts } from './store';

/**
 * 路由权限控制
 * @param key 
 */
function RT(key: string) {

	if (rts.includes(key)) {
		return key
	}

	return '/admin/back'
}

const AdminOrder = () => (
	<Switch>
		<Route path={RT('/admin/order/detail')} component={AdminDetail}></Route>
		<Route path={RT('/admin/order/list')} component={OrderList}></Route>
		<Route path={RT('/admin/order/statistics')} component={Statisstics}></Route>
		<Route component={AdminBK}></Route>
	</Switch>
)

const AdminPicture = () => (
	<Switch>
		<Route path={RT('/admin/picture/detail')} component={PDetail}></Route>
		<Route path={RT('/admin/picture/manager')} component={Manager}></Route>
		<Route path={RT('/admin/picture/audit')} component={Audit}></Route>
		<Route path={RT('/admin/picture/addpicture')} component={AddPicture}></Route>
		<Route component={AdminBK}></Route>
	</Switch>
)

const AdminSystem = () => (
	<Switch>
		<Route path={RT('/admin/system/cate')} component={Cate}></Route>
		<Route path={RT('/admin/system/user')} component={SystemUser}></Route>
		<Route path={RT('/admin/system/role')} component={Role}></Route>
		<Route component={AdminBK}></Route>
	</Switch>
)

const UserPackages = () => (
	<Switch>
		<Route path={'/user/packages/paymented'} component={Paymented}></Route>
		<Route path={'/user/packages/unpaid'} component={Unpaid}></Route>
	</Switch>
)

const UserUser = () => (
	<Switch>
		<Route path={'/user/base/info'} component={Info}></Route>
		<Route path={'/user/base/userinfo'} component={UserInfo}></Route>
		<Route path={'/user/base/images'} component={Images}></Route>
		<Route path={'/user/base/password'} component={Password}></Route>
		<Route component={UserInfo}></Route>
	</Switch>
);

//** 后台路由
export const AdminRoute = () => (
	<Switch>
		<Route path={RT('/admin/company')} component={Company}></Route>
		<Route path={RT('/admin/picture')} >{AdminPicture}</Route>
		<Route path={RT('/admin/system')}>{AdminSystem}</Route>
		<Route path={RT('/admin/order')}>{AdminOrder}</Route>
		<Route path={'/admin/home'} component={Home}></Route>
		<Route component={AdminBK}></Route>
	</Switch >
)
// 首页路由
export const MainHomeRoute = () => (
	<Switch >
		<Route path={'/main/photo'} component={Photo} />
		<Route path={'/main/design'} component={Design} />
		<Route path={'/main/login'} component={Login} />
		<Route path={'/main/register'} component={Register} />
		<Route path={'/main/media'} component={Media} />
		<Route path={'/main/search'} component={Search} />
		<Route path={'/main/detail'} component={MainDetail} />
		<Route path={'/main/category'} component={MainCate} />
		<Route component={Photo}></Route>
	</Switch>
)
//用户路由
export const UserRoute = () => (
	<Switch>
		<Route path={'/user/download'} component={Download}></Route>
		<Route path={'/user/discount'} component={Discount}></Route>

		<Route path={'/user/favorites'} component={Favorites}></Route>
		<Route path={'/user/browseRecord'} component={BrowseRecord}></Route>
		<Route path={'/user/base'}>{UserUser}</Route>
		<Route path={'/user/orderForm'} component={OrderForm}></Route>
		<Route path={'/user/packages'}>{UserPackages}</Route>
		<Route>{UserUser}</Route>
	</Switch>
)
//主路由
export const MainRT = () => (
	<Router history={history}>
		<Switch>
			<Route path={'/user/login'} component={Login}></Route>
			<Route path={'/user/register'} component={Register}></Route>
			<Route path={'/user/findpassword'} component={FindPwd}></Route>
			<Route path={'/user'} component={User} />

			<Route path={'/shoppingcart'} component={ShoppingCart}></Route>
			<Route path={'/admin/login'} component={ALogin}></Route>
			<Route path={'/admin'} component={Admin} />
			<Route path={'/main'} component={Main} />
			<Route path={'/test'} component={Test} />
			<Route path={'/admin/back'} component={AdminBK} />
			<Route component={Main} />
		</Switch>

		<BackTop></BackTop>
	</Router>
)


