import store from '../store';
import { message } from 'antd';
import { OrderList, OrderInfo, CartInfo, CartSave, CategoryList, ProductDetail, ResponseData, AdminLogin, AdvanceQuery } from './types';
import { CategoryGroup } from './types';
import r, { request } from '.';

export const main = {
	/**
	 * 短信服务
	 */
	sms: {
		/**
		 * 发送短信验证码phoneNumber
		 * @param data 
		 */
		async send(data: {
			phoneNumber: string,
		}) {
			return await request.post<ResponseData>('/api/sms/send', data);
		},

	},
	/**
	 * 用户相关
	 */
	user: {
		/**
		 * 用户登录
		 * @param data 
		 */
		async login(data: AdminLogin) {

			store.dispatch({ type: 'user-remove' })

			return await request.post<ResponseData & { accessToken: string }>('/ui/client/login', data)
				.then<{ success: boolean }>(res => {

					if (-1 === res.data.code) {

						message.error(res.data.message || '登录信息验证失败！')
						return { success: false }
					}

					store.dispatch({ type: 'user-update', data: { accessToken: res.data.accessToken, userInfo: res.data.data } })

					return { success: true }
				});
		},
		/**
		 * 用户注册
		 * @param data 
		 */
		async register(data: {
			code: string,
			moblie: string,
			password: string,
		}) {
			return await request.post<ResponseData>('/ui/client/register', data);
		},
		/**
		 *找回密码
		 *
		 * //POST /ui/client/backPassword
		 * @param id 
		 */
		async backPassword(data: {
			code?: string,
			moblie?: string,
			password?: string,
		}) {
			return await request.post<ResponseData>('/ui/client/backPassword', data)
				.then(res => {
					if (res.data.code === 0) {
						message.success('找回密码成功！')
					}
					return res.data
				});
		},
	},

	ui: {

		/**
		 *个人订单
		 */
		order: {
			/**
			 *从购物车删除
			 * @param data 
			 */
			async deleteData(data: [{
				id: string,
			}]) {

				return await request.post<ResponseData>('/ui/order/deleteData', data).then(res => {
					r.main.ui.cart.getCount()
					return res
				});
			},
			/**
			 *个人订单
			 * @param data 
			 */
			async getList(pageIndex: number) {

				return await request.post<ResponseData>('/ui/order/getList', { pageIndex, pageSize: 100 })
					.then<OrderList>(res => {

						if (res.data.code === 0) {
							return res.data.data
						} else {
							message.error(res.data.message || '订单查询失败！')
							return { rows: [], pageIndex: 1, pageSize: 0, total: 0 }
						}
					});
			},
			/**
			 *图片下单
			 * @param data 
			 */
			async saveData(data:
				{
					discountAmount?: number,
					discountRate?: string,
					productCount: number,
					productId: string,
					productName?: string,
					productPrice?: number,
					smallImageSha1?: string,
				}[]
			) {

				return await request.post<ResponseData>('/ui/order/saveData', data)
					.then<{ success: boolean, data?: OrderInfo }>(res => {
						if (res.data.code === 0) {
							r.main.ui.cart.getCount()
							return {
								success: true,
								data: res.data.data
							}
						} else {
							message.error('下单失败！')
							return { success: false }
						}
					});
			},

			/**
			 *个人订单
			 * @param data 
			 */
			async download(id: string) {

				return await request.get('/ui/order/download?orderId=' + id, { responseType: 'blob' });
			},
		},
		/**
		 *购物车
		 */
		cart: {
			/**
			 *从购物车删除
			 * @param data 
			 */
			async deleteData(data: [{
				id: string,
			}]) {

				return await request.post<ResponseData>('/ui/cart/deleteData', data)
					.then(res => {
						if (res.data.code === 0) {
							r.main.ui.cart.getCount()
							return { success: true }
						} else {
							message.error(res.data.message || '购物车删除失败！')
							return { success: false }
						}
					});
			},
			/**
			 * 购物车列表
			 * @param data 
			 */
			async getList() {

				return await request.post<ResponseData>('/ui/cart/getList')
					.then<CartInfo[]>(res => {

						if (res.data.code === 0) {
							return res.data.data
						} else {
							return []
						}
					});
			},
			/**
			 * 购物车列表数
			 * @param data 
			 */
			async getCount() {

				return await request.post<ResponseData>('/ui/cart/getCount').then(res => {

					if (res.data.code === 0) {
						store.dispatch({ type: 'shoppingCart-login', data: res.data.data || 0 })
						return { success: true }
					} else {
						store.dispatch({ type: 'shoppingCart-login', data: 0 })
						return { success: false }
					}
				});
			},
			/**
			 *添加购物车
			 * @param data 
			 */
			async saveData(data: CartSave) {

				return await request.post<ResponseData>('/ui/cart/saveData', data).then(res => {

					if (res.data.code === 0) {
						message.success(res.data.message || '添加购物车成功！')
						r.main.ui.cart.getCount()
						return { success: true }
					} else {
						message.error(res.data.message || '添加购物车失败！')
						return { success: false }
					}
				});
			},
		},
		/**
	 * 浏览记录
	 */
		history: {
			/**
			 * 我的下载记录
			 * @param data 
			 */
			async getDownList(data: {
				flagEnable?: string,
				id?: string,
				keyword?: string,
				name?: string,
				pageIndex?: number,
				pageSize?: number,
			}) {
				return await request.post<ResponseData>('/ui/history/getDownList', data).then(res => {
					return res.data;
				});
			},
			/**
			 * 我的浏览历史
			 * @param data 
			 */
			async getHistoryList(data: {
				createEndTime?: string,
				createStartTime?: string,
				flagEnable?: boolean
				id?: string,
				keyword?: string,
				name?: string,
				pageIndex?: number,
				pageSize?: number,
			}) {
				return await request.post<ResponseData>('/ui/history/getHistoryList', data).then(res => {
					return res.data;
				});
			},
			/**
			 * 删除
			 * @param data 
			 */
			async deleteHistoryData(data: [{
				id: string,
			}]) {

				return await request.post<ResponseData>('/ui/history/deleteHistoryData', data);
			},
			/**
			 * 添加
			 * @param data 
			 */
			async saveHistoryData(data: any) {

				return await request.post<ResponseData>('/ui/history/saveHistoryData', data);
			},

			/**
			 * 我的收藏
			 * @param data 
			 */
			async getUserCollectionList(data: {
				collectionUsage?: string,
				createDate?: string,
				createEndTime?: string,
				flagEnable?: boolean
				id?: string,
				keyword?: string,
				name?: string,
				pageIndex?: number,
				pageSize?: number,
				productName?: string,
			}) {
				return await request.post<ResponseData>('/ui/history/getCollection', data).then(res => {
					return res.data;
				});
			},
			/**
			 * 删除产品收藏记录
			 * @param data 
			 */
			async deleteData(data: [{
				id: string,
			}]) {

				return await request.post<ResponseData>('/ui/history/deleteCollection', data);
			},
			/**
			 * 产品收藏
			 * @param data 
			 */
			async saveCollection(data: {
				productId: string,
				productName: string,
				smallImage: string
			}) {

				return await request.post<ResponseData>('/ui/history/saveCollection', data)
					.then(res => {
						if (res.data.code === 0) {
							message.success(res.data.message || '添加收藏成功！')
						} else {
							message.error(res.data.message || '添加收藏失败！')
						}
					});
			},
		},
		category: {
			/**
			 * 一级分类
			 * @param data 
			 */
			async list(categoryGroup: CategoryGroup) {

				return await request.post<ResponseData>('/ui/category/getList', { categoryGroup })
					.then<{ name: string, id: string }[]>(res => {

						if (res.data.code === 0) {
							return res.data.data
						} else {
							return []
						}
					});
			},
			/**
			 * 二级分类
			 * @param data 
			 */
			async sublist(id: string) {

				return await request.post<ResponseData>('/ui/category/getSubList', { id, pageSize: 30 })
					.then<{ total: number, pageIndex: number, rows: CategoryList[] }>(res => {

						if (res.data.code === 0) {
							return res.data.data
						} else {
							return {
								total: 0,
								rows: [],
								pageIndex: 1
							}
						}
					});
			},
			/**
			 * 分类图片
			 * @param data 
			 */
			async photo(id: string, pageIndex: number) {

				return await request.post<ResponseData>('/ui/category/getPhoto', { id, pageIndex, pageSize: 20 })
					.then<{ total: number, pageIndex: number, rows: CategoryList[] }>(res => {

						if (res.data.code === 0) {
							return res.data.data
						} else {
							return {
								total: 0,
								rows: [],
								pageIndex: 1
							}
						}
					});
			},
		},
		product: {
			/**
			 * 详细
			 * @param data 
			 */
			async detail(id: string) {

				return await request.post<ResponseData>('/ui/product/getDetail', { id })
					.then<ProductDetail>(res => {

						if (res.data.code === 0) {

							r.main.ui.history.saveHistoryData({
								productName: res.data.data.mainTitle,
								smallImage: res.data.data.sha1,
								productId: res.data.data.id
							});

							return {
								success: true,
								...res.data.data
							}
						}

						return {
							success: false
						}
					});
			},
			/**
			 * 二级分类
			 * @param data 
			 */
			async advanceQuery(data: AdvanceQuery) {

				return await request.post<ResponseData>('/ui/product/advanceQuery', data)
					.then<{ total: number, pageIndex: number, rows: CategoryList[] }>(res => {

						if (res.data.code === 0) {
							return res.data.data
						} else {
							return {
								total: 0,
								rows: [],
								pageIndex: 1
							}
						}
					});
			},
			/**
			 * 精选图片
			 * @param data 
			 */
			async getRecommend(categoryGroup: string, pageIndex: number) {

				return await request.post<ResponseData>('/ui/product/getRecommend', { categoryGroup, pageIndex, pageSize: 12 })
					.then<{ total: number, pageIndex: number, rows: CategoryList[] }>(res => {

						if (res.data.code === 0) {
							return res.data.data
						} else {
							return {
								total: 0,
								rows: [],
								pageIndex: 1
							}
						}
					});
			},

		},

		wx: {
			/**
			 * 支付订单
			 * @param data 
			 */
			async pay(id: string) {

				return await request.post<ResponseData>('/ui/wx/pay', { id })
					.then<{ success: boolean, data: string }>(res => {

						if (res.data.code === 0) {
							return { success: true, data: res.data.data }
						} else {
							message.error(res.data.message || '支付码请求失败，请新提交！')
							return { success: false, data: '' }
						}
					});
			},
			/**
			 * 支付订单状态
			 * @param data 
			 */
			async payStatus(id: string) {

				return await request.post<ResponseData>('/ui/wx/payStatus', { id })
					.then<number>(res => {

						return res.data.code
					});
			},

		}
	}

}