import React, { useState } from 'react';
import { Row, Col, DatePicker, Input, Select, Icon, Cascader} from 'antd';
import moment from 'moment';
import request from '../../../request';
import { CascaderOptionType } from 'antd/lib/cascader';

const tmformat = 'YYYY-MM-DD';

interface Props {
	onParams: (key: string, value: string | number) => void
	params: URLSearchParams
}

const A: React.FC<Props> = (props) => {
	const [cates] = useState([
		{
			value: 'photo',
			label: '摄影',
			isLeaf: false,
		},
		{
			value: 'design',
			label: '设计',
			isLeaf: false,
		},
		{
			value: 'media',
			label: '媒体',
			isLeaf: false,
		}
	]);
	// const [signleData, setSignleData] = useState(
	// 	{
	// 		categoryId: [''],
	// 		categoryName: '',
	// 	}

	// );
	/**
	 * 加载图片分类
	 */
	const loadData = (v?: CascaderOptionType) => {

		if (v) {
			v[0].loading = true

			request.admin.cate.getTree({ categoryUsage: v[0].value, id: '0' }).then((data) => {

				setTimeout(() => {

					v[0].loading = false
					v[0].children = data.length ? data.map(v => ({
						value: v.id,
						label: v.name,
						isLeaf: false,
						children: (v.children && v.children.length) ? v.children.map((s: any) => ({
							value: s.id,
							label: s.name,
							isLeaf: true,
						})) : [{
							value: '--null--',
							label: '缺少分类',
							isLeaf: true,
							disabled: true,
						}]
					})) : [{
						value: '--null--',
						label: '缺少分类',
						isLeaf: true,
						disabled: true,
					}]
				}, 100);
			})
		}
	}
	return (
		<div style={{ paddingTop: 12 }}>

			<div style={{ padding: 6 }}><Icon type='search' />条件搜索：</div>
			<Row gutter={24}>

				<Col xl={4} lg={8} md={12} xs={24}>
					<Select onChange={(value: string) => props.onParams('sellStatus', value)}
						placeholder='销售状态' style={styles.selStyle}
						defaultValue={props.params.get('sellStatus') || undefined} allowClear>
						<Select.Option value='0'>未上架</Select.Option>
						<Select.Option value='1'>已上架</Select.Option>
						<Select.Option value='-1'>已下架</Select.Option>
					</Select>
				</Col>

				<Col xl={4} lg={8} md={12} xs={24}>
					<DatePicker
						disabledDate={(v) => {
							const dt = props.params.get('createEndTime') &&
								moment(props.params.get('createEndTime') || undefined, tmformat)
							if (dt && v) {
								return v.valueOf() > dt.valueOf()
							}
							return false
						}}
						showTime
						format={tmformat}
						defaultValue={(props.params.get('createStartTime') &&
							moment(props.params.get('createStartTime') || undefined, tmformat)) || undefined}
						placeholder='添加开始时间'
						onChange={(v, s) => { props.onParams('createStartTime', s) }}
						style={styles.selStyle}
					/>
				</Col>
				<Col xl={4} lg={8} md={12} xs={24}>
					<DatePicker
						disabledDate={(v) => {
							const dt = props.params.get('createStartTime') &&
								moment(props.params.get('createStartTime') || undefined, tmformat)
							if (dt && v) {
								return v.valueOf() < dt.valueOf()
							}
							return false
						}}
						showTime
						format={tmformat}
						defaultValue={(props.params.get('createEndTime') &&
							moment(props.params.get('createEndTime') || undefined, tmformat)) || undefined}
						placeholder='添加结束时间'
						onChange={(v, s) => { props.onParams('createEndTime', s) }}
						style={styles.selStyle}
					/>
				</Col>
				<Col xl={4} lg={8} md={12} xs={24}>
					<Cascader options={cates} 
						placeholder='图片分类'
						loadData={loadData}
						style={{ width: '100%', paddingTop: 6 }} onChange={v => {
							// setSignleData({
							// 	...signleData, categoryId: v,
							// });

							props.onParams('categoryId', v[v.length - 1]);
						}}></Cascader>
				</Col>
				<Col xl={4} lg={8} md={12} xs={24} >
					<Input.Search
						style={styles.selStyle}
						placeholder='关键字' allowClear enterButton
						defaultValue={props.params.get('keyword') || undefined}
						type='primary' onSearch={v => { props.onParams('keyword', v) }}
					/>
				</Col>
			</Row >
		</div>
	)
}

const styles: { [key: string]: React.CSSProperties } = {
	selStyle: {  width: '100%', padding: 6 , },
}

export default A;