import React, { useState, useEffect } from 'react';
import { Modal, InputNumber, message, Typography, Cascader, Input } from 'antd';
import request, { ufileDownUrl } from '../../../request';
import { CascaderOptionType } from 'antd/lib/cascader';
import Keywords from '../addPic/infos/keywords';
interface Props {
	setVisible: (visible: boolean) => void
	refreshData: () => void
	auditOpe: (id: string) => void
	visible: boolean,
	id: string,
	okText: string,
	cancelText: string,
}
interface signleDataItem {
	categoryId: string,
	description: string,
	id: string,
	keyword: string,
	mainTitle: string,
	middle: {
		exif: string,
		format: string,
		height: number,
		name: string,
		sha1: string,
		size: number,
		url: string,
		width: number,
	},
	name: string,
	original: {
		exif: string,
		format: string,
		height: number,
		name: string,
		sha1: string,
		size: number,
		url: string,
		width: number,
	},
	price: number,
	small: {
		exif: string,
		format: string,
		height: number,
		name: string,
		sha1: string,
		size: number,
		url: string,
		width: number,
	}
}

const A: React.FC<Props> = (props) => {

	const [data, setData] = useState();

	useEffect(() => {

		request.admin.product.getById({ id: props.id }).then((res: any) => {
			if (res.success) {
				setData(res);
				let keywords: string[] = [];
				if (res.keyword !== '' && res.keyword !== null) {
					keywords = res.keyword.indexOf(',') > -1 ? res.keyword.split(',') : [res.keyword];
				}
				let series: string[] = [res.categoryUsage, res.parentCategoryId, res.categoryId];

				let categoryName = '';
				if (res.categoryUsage === 'photo') {
					categoryName += '摄影';
				} else if (res.categoryUsage === 'design') {
					categoryName += '设计';
				} else if (res.categoryUsage === 'media') {
					categoryName += '媒体';
				}
				setSignleData({
					categoryId: series,
					categoryName: categoryName + '/' + res.parentCategoryName + '/' + res.categoryName,
					description: res.description,
					id: res.id,
					keyword: keywords,
					mainTitle: res.mainTitle,
					price: res.price,
					name: res.name,
				});
			}

		})

	}, [props.id, props.visible]);

	/**
	 * 图片编辑
	 */
	const updatePic = () => {
		if(signleData.price<0.01){
			message.error('价格不能低于1分,请重新填写价格');
			return;
		}
		request.admin.product.editData({
			categoryId: signleData.categoryId[signleData.categoryId.length - 1],
			description: signleData.description == null ? '' : signleData.description,
			id: signleData.id,
			keyword: signleData.keyword.join(),
			mainTitle: signleData.mainTitle,
			price: signleData.price,
			name: '',
		}).then((res: any) => {

			if (res.code !== 0) {
				message.error(res.message || '返回数据有误')
				return;
			} else {
				let keywords: string[] = [];
				setSignleData({
					categoryId: keywords,
					categoryName: '',
					description: '',
					id: '',
					keyword: keywords,
					mainTitle: '',
					price: 0,
					name: '',
				});
				props.setVisible(false)
				if (props.okText === '确定') {
					message.success('图片编辑成功');
					props.refreshData();
				} else {
					props.auditOpe(props.id);
				}

			}

		})
	}
	const [cates] = useState([
		{
			value: 'photo',
			label: '摄影',
			isLeaf: false,
		},
		{
			value: 'design',
			label: '设计',
			isLeaf: false,
		},
		{
			value: 'media',
			label: '媒体',
			isLeaf: false,
		}
	])
	const [signleData, setSignleData] = useState(
		{
			categoryId: [''],
			categoryName: '',
			description: '',
			id: '',
			keyword: [''],
			mainTitle: '',
			price: 0,
			name: '',
		}

	);
	/**
	 * 加载图片分类
	 */
	const loadData = (v?: CascaderOptionType) => {

		if (v) {
		//	v[v.length - 1].loading = true

			request.admin.cate.getTree({ categoryUsage: v[0].value, id: '0' }).then((data) => {

				setTimeout(() => {

					//v[0].loading = false
					v[0].children = data.length ? data.map(v => ({
						value: v.id,
						label: v.name,
						isLeaf: false,
						children: (v.children && v.children.length) ? v.children.map((s: any) => ({
							value: s.id,
							label: s.name,
							isLeaf: true,
						})) : [{
							value: '--null--',
							label: '缺少分类',
							isLeaf: true,
							disabled: true,
						}]
					})) : [{
						value: '--null--',
						label: '缺少分类',
						isLeaf: true,
						disabled: true,
					}]
				}, 300);
			})
		}
	}
	return (
		<div >
			<Modal
				title="图片编辑"
				visible={props.visible}
				okText={props.okText}
				cancelText={props.cancelText}
				onOk={updatePic}
				width={800}
				okType='primary'
				maskClosable={false}
				onCancel={() => { props.setVisible(false) }}>

				{data && <div>
					<div style={styles.imageDiv}>
						<img style={styles.image} alt={signleData.mainTitle} src={ufileDownUrl + data.sha1}></img>
					</div>
					<div style={styles.imageInfo}>
						<div style={styles.infoItem}>
							<b>标题：</b>
							<Typography.Paragraph style={{ display: 'inline' }} editable={{
								onChange: v => {
									setSignleData({
										...signleData, mainTitle: v,
									});
								}
							}}>{signleData.mainTitle}</Typography.Paragraph>
						</div>
						<div style={styles.infoItem}>
							<b>价格：</b>
							<InputNumber min={0} max={9999999}
								value={signleData.price}
								style={{ width: 200 }}
								formatter={v => `¥ ${ v }`} onChange={v => {
									setSignleData({
										...signleData, price: v || 0,
									});
								}}></InputNumber>
						</div>
						<div style={styles.infoItem}>
							<b>分类：</b>
							<Input size='default' value={signleData.categoryName} style={{ width: 220,border :'0px' }} />
							&nbsp;<Cascader options={cates}
								defaultValue={signleData.categoryId}
								value={signleData.categoryId}
								placeholder='请选择图片分类'
								loadData={loadData}
								style={{ width: 220 }} onChange={v => {

									setSignleData({
										...signleData, categoryId: v,
									});
								}}></Cascader>
						</div>
						<div style={styles.infoItem}>
							<b>关键字：</b>
							<Keywords keys={signleData.keyword} onUpdate={(v) => {

								setSignleData({
									...signleData, keyword: v,
								});
							}} />
						</div>
					</div>
				</div>
				}

			</Modal>

		</div >
	)
}

export default A;
const styles: { [key: string]: React.CSSProperties } = {
	imageInfo: { border: '#ddd solid 1px', padding: 10, flexGrow: 1 },
	imageDiv: { width: 175, height: 175, margin: 5, marginRight: 20, float: 'left' },
	image: { maxWidth: '100%', maxHeight: '100%', border: 'dashed 1px #666' },
	imageTitle: { position: 'absolute', bottom: 0, left: 0, right: 0, textAlign: 'center', backgroundColor: 'rgba(34,34,34,0.53)', color: '#eee' },
	infoItem: { lineHeight: '36px', margin: 4 },
	infoError: { border: '1px solid #e22', padding: '2px 4px', borderRadius: 3 }
}