import React, { useState } from 'react';
import { Tag, Input, Icon, message } from 'antd';

interface Props {
	/**
	 * 关键词
	 */
	keys: string[]
	/**
	 * 更新关键词
	 */
	onUpdate: (keys: string[]) => void
}

const A: React.FC<Props> = (props) => {

	const [showInput, setShowInput] = useState(false)

	const handleInputConfirm = (s: string) => {

		setShowInput(false)
		if (s.includes(',')) {
			message.error('包含非法字符( , )')
			return
		}

		if (s.trim().length < 1) {
			message.error('至少包含一个字符！')
			return
		}

		if (props.keys.includes(s)) {
			message.error('关键字重复！')
			return
		}
		props.onUpdate([...props.keys, s])
	}

	return (
		<span>

			{props.keys.map((v, i) => <Tag key={i} closable onClose={() => {
				props.onUpdate(props.keys.filter(s => s !== v))
			}}>{v}</Tag>)}

			{props.keys.length < 5 ?
				showInput ?
					<Input autoFocus type='text' size='small' style={{ width: 78 }}
						onBlur={v => {
							handleInputConfirm(v.currentTarget.value);
						}}
						onPressEnter={v => {
							handleInputConfirm(v.currentTarget.value);
						}}></Input> :

					<Tag onClick={() => {
						setShowInput(true)
					}} style={{ background: '#fff', borderStyle: 'dashed' }}>
						<Icon type='plus' /> 添加关键字 </Tag> :
				null}
		</span>
	)
}

export default A;