import React, { useState, useEffect, useCallback } from 'react';
import { Table, Button, Card, Form, message } from 'antd';
import { FormComponentProps } from 'antd/lib/form';
import { history } from '../../..';
import request from '../../../request';
import Filter from './filter';
import moment from 'moment';
/**
 * 格式化时间
 */
const tmformat = 'YYYY-MM-DD HH:mm:ss'
/**
 * 批量操作
 */
const opts: { [key: string]: (ids: string, queryData: () => void) => void } = {
	/**
	 * 详情
	 * @param value：商品id
	 */
	'detail': (value: any, queryData) => {
		history.push('/admin/order/detail?id=' + value.id);
	},
	/**
	 * 取消订单
	 * @param value：
	 */
	'cancel': (key, queryData) => {
		request.admin.product.changeState({
			id: key, state: -1
		}).then((res: any) => {
			if (res.code !== 0) {
				message.error(res.message || '取消订单失败')
				return;
			} else {
				message.success('取消订单成功成功');
				//重新刷新
				queryData();
			}

		})
	}
}
interface Props extends FormComponentProps {
	onParams: (key: string, value: string | number) => void
	params: URLSearchParams
}
const A: React.FC<Props> = (props) => {

	const [data, setData] = useState([]);
	const [total, setTotal] = useState(0);
	const [loading, setLoading] = useState(false);

	const requestOrderList = (value: any) => {
		request.admin.order.getList(value).then((res: any) => {
			setTimeout(() => { setLoading(false) }, 300);
			if (res.code !== 0) {
				message.error(res.message || '返回数据有误')
				setData([]);
				setTotal(0);
			} else {
				setData(res.data.rows.map((v: any) => ({ key: v.id, ...v })));
				setTotal(res.data.total);
			}

		})
	}
	const queryOrderData = useCallback(() => {
		requestOrderList(
			{
				buyerId: '',
				flagEnable: '',
				orderTimeEnd: props.params.get('orderTimeEnd') === null ? '' : props.params.get('orderTimeEnd'),
				id: props.params.get('id') === null ? '' : props.params.get('id'), 
				keyword: props.params.get('keyword') === null ? '' : props.params.get('keyword'),
				name: '',
				orderStatus: parseInt(props.params.get('orderStatus') || '101'),
				orderTimeStart: props.params.get('orderTimeStart') === null ? '' : props.params.get('orderTimeStart'),
				pageIndex: parseInt(props.params.get('pageIndex') || '1') || 1,
				pageSize: 10,
			});

	}, [props.params])
	/**
	 * 首次获取
	 * 订单状态 0未付款,1已付款,2已下载,-4取消交易 -5撤销申请
	 */
	useEffect(() => {
		queryOrderData();
	}, [queryOrderData]);

	const columns: any[] = [
		{
			title: '订单编号', dataIndex: 'orderNo', align: 'center', width: 150,
		},
		{
			title: '下单人', dataIndex: 'userName', align: 'center', width: 120,
		},
		{
			title: '下单时间', dataIndex: 'createTime', align: 'center', width: 200,
			render: (createTime?: string) => (<div>{(createTime && moment(createTime).format(tmformat)) || ''}</div>)
		},
		{
			title: '数量', dataIndex: 'productCount', align: 'center', width: 65,
		},
		{
			title: '总价', dataIndex: 'productAmountTotal', align: 'center', width: 100,
			render: (productAmountTotal: number) => (<div className='money_rmb'>{productAmountTotal}</div>)
		},
		// {
		// 	title: '折扣金额', dataIndex: 'agioAmount', align: 'center', width: 100,
		// 	render: (agioAmount: number) => (<div>-￥{agioAmount}</div>)
		// },
		{
			title: '实付金额', dataIndex: 'orderAmountTotal', align: 'center', width: 100,
			render: (orderAmountTotal: number) => (<div style={{ color: 'red' ,fontWeight:'bolder'}} className='money_rmb'>{orderAmountTotal}</div>)
		},
		{
			title: '付款方式', dataIndex: 'payChannel', align: 'center', width: 100,
			render: (payChannel: number) => (<div>{['', '微信', '支付宝'][payChannel]}</div>)
		},
		{
			title: '订单状态', dataIndex: 'orderStatus', align: 'center', width: 100,

			render: (payChannel: number) => (
				<div>{['未付款', '已付款', '已下载'][payChannel]}
					{payChannel === -4 ? '取消交易' : null}
					{payChannel === -5 ? '撤销申请' : null}
				</div>)
		},
		{
			title: '付款时间', dataIndex: 'payTime', align: 'center', width: 200,
			render: (payTime?: string) => (<div>{(payTime && moment(payTime).format(tmformat)) || ''}</div>)
		},
		{
			title: '操作', key: 'action', align: 'center',
			render: (id: string, record: any) => (
				<div>
					<Button onClick={(e) => {
						e.preventDefault();
						opts['detail'](id, queryOrderData)
					}} icon='info'>订单详情</Button>
					<div style={{ margin: 6 }}></div>

				</div>
			)
		}
	]
	return (
		<div style={{ position: 'relative', padding: '0 20px 20px' }}>
			<Card style={{ margin: 16 }} title='条件搜索' >

				<Filter params={props.params} onParams={props.onParams}></Filter>

			</Card>
			<Table loading={loading}
				style={{ backgroundColor: '#fff', margin: 16 }}
				columns={columns}

				dataSource={data}
				pagination={{
					showTotal: (total) => `总共：${ total } `,
					total: total,
					current: parseInt(props.params.get('pageIndex') || '1'),
					onChange: (v: number) => { props.onParams('pageIndex', v.toString()) }
				}}
				bordered>
			</Table>

		</div>
	)
}

export default Form.create<Props>()(A);