import React, { useState, useEffect, useCallback, useRef } from 'react';
import { message, Table, Button, Divider, Popconfirm, Tabs, Pagination, Card } from 'antd';
import request, { ufileDownUrl } from '../../request';
import { OrderList, CartInfo, } from '../../request/types';
import OrderInfo from './orderInfo';
import moment from 'moment';
import { history } from '../..';

const { TabPane } = Tabs;

interface Props {
	onParams: (key: string, value: string | number) => void
	params: URLSearchParams
}

const A: React.FC<Props> = (props) => {

	const deletOrder = (id: any) => {
		request.main.ui.order.deleteData(id).then((res: any) => {
			if (res.data.code !== 0) {
				message.error(res.message || '删除失败')
				return;
			} else {
				message.success('删除成功');
				queryRecordData();
			}

		})
	}

	const columns: any[] = [
		{
			title: '订单号', dataIndex: 'orderNo', width: 120,
			render: (id: number) => <span>{id}</span>,
		},
		{
			title: '总价', dataIndex: 'money', align: 'center', width: 120,
			render: (productAmountTotal: number) => <span className='money_rmb'>{productAmountTotal}</span>
		},
		{
			title: '实付金额', dataIndex: 'orderAmountTotal', align: 'center', width: 100,
			render: (orderAmountTotal: number) => (<div style={styles.redcolor} className='money_rmb'>{orderAmountTotal}</div>)
		},
		{
			title: '图片数', dataIndex: 'detail.length', align: 'center', width: 80,
		},
		{
			title: '预览图', dataIndex: 'detail[0].smallImage', align: 'center', width: 80,

			render: (text: string) => (
				<img alt='预览图'
					src={ufileDownUrl + text}
					style={{ maxWidth: 60, maxHeight: 60 }}>
				</img>)
		},
		{
			title: '下单时间', dataIndex: 'createTime',
			render: (createTime: string) => (<div>{moment(createTime).format('YYYY-MM-DD HH:mm:ss')}</div>)
		},
		{
			title: '操作', key: 'x', align: 'center', width: 240,
			render: (id: string, record: any) => (
				<div style={{ minWidth: 240 }}>
					{
						record.orderStatus === 0 ?
							<Button icon='pay-circle' type='primary' onClick={(e) => {

								if (orderInfo.current) {
									orderInfo.current.show(record)
									setTimeout(() => {
										orderInfo.current && orderInfo.current.pay()
									}, 300);
								}
							}} >支付</Button> :
							<Button icon='download' type='primary' onClick={(e) => {
								request.main.ui.order.download(record.id).then((res) => {

									try {
										const des = new Headers(res.headers).get('content-disposition') as string;
										const blob = new Blob([res.data])
										const downloadElement = document.createElement('a');
										const href = window.URL.createObjectURL(blob); //创建下载的链接
										downloadElement.href = href;
										downloadElement.download = des.split(';')[1].split('=')[1]; //下载后文件名
										document.body.appendChild(downloadElement);
										downloadElement.click(); //点击下载
										document.body.removeChild(downloadElement); //下载完成移除元素
										window.URL.revokeObjectURL(href); //释放掉blob对象
										setTimeout(() => {
											queryRecordData();
										}, 300);
									} catch (e) {

									}
								})
							}} >下载</Button>
					}

					<Divider type='vertical'></Divider>
					<Popconfirm
						title='你确定要删除该订单吗？'
						onConfirm={() => deletOrder(id)}
						okText='确定删除'
						cancelText='放弃'>
						<Button type='danger' icon='delete' >删除</Button>

					</Popconfirm>
				</div>),
		}
	]

	const [data, setData] = useState<OrderList>({
		total: 0,
		pageIndex: parseInt(props.params.get('pageIndex') || '1') || 1,
		pageSize: 0,
		rows: []
	});

	const orderInfo = useRef<OrderInfo>(null)

	/**
	 * 图片查看~
	 */
	const expandedRowRender = (record: any) => (
		<div style={{ display: 'flex', flexWrap: 'wrap' }}>
			{
				(record.detail as CartInfo[]).map(item =>
					<Card
						hoverable
						key={item.productId}
						style={{ margin: 6 }}
						onClick={() => {
							history.push(`/main/detail?id=${ item.productId }`)
						}}

						cover={<img alt={item.productName}
							src={ufileDownUrl + item.smallImage}
							style={{ height: 160, width: 'auto' }}></img>}>
						<Card.Meta title={<span className='money_rmb'>{item.price}</span>} description={item.productName} style={{ fontWeight: 'bolder' }} />
					</Card>)
			}
		</div>
	)
	/**
	 * 
	 * @param value 获取收藏记录
	 */
	const queryOrderList = useCallback(() => {

		request.main.ui.order.getList(data.pageIndex).then((res) => {

			setData({
				total: res.total,
				pageIndex: res.pageIndex,
				pageSize: res.pageSize,
				rows: res.rows.map(v => ({ ...v, key: v.id }))
			})
		})
	}, [data.pageIndex])
	const queryRecordData = useCallback(() => {
		queryOrderList()
	}, [queryOrderList])
	/**
	* 首次获取分类数据
	*/
	useEffect(() => {
		queryRecordData();
	}, [props.params, queryRecordData])

	return (
		<div style={{ position: 'relative', padding: '0 20px 20px' }}>

			<Tabs defaultActiveKey={props.params.get('tabs') || '0'} tabBarExtraContent={
				<Pagination defaultCurrent={data.pageIndex} pageSize={data.pageSize} total={data.total}
					onChange={v => { props.onParams('pageIndex', v) }} />}>
				<TabPane tab='未支付' key='0'>
					<Table
						dataSource={data.rows.filter(v => v.orderStatus === 0)}
						columns={columns}
						expandedRowRender={expandedRowRender}
						pagination={false} />
				</TabPane>
				<TabPane tab='已支付' key='1'>
					<Table
						dataSource={data.rows.filter(v => v.orderStatus === 1)}
						columns={columns}
						expandedRowRender={expandedRowRender}
						defaultExpandedRowKeys={[props.params.get('current_0') || '']}
						pagination={false} />
				</TabPane>
				<TabPane tab='已下载' key='2'>
					<Table
						dataSource={data.rows.filter(v => v.orderStatus === 2)}
						columns={columns}
						expandedRowRender={expandedRowRender}
						pagination={false} />
				</TabPane>
			</Tabs>

			<OrderInfo ref={orderInfo}></OrderInfo>
		</div>
	)
}
export default A;
const styles: { [key: string]: React.CSSProperties } = {
	selectrow: { padding: '8px', paddingTop: '14px' },
	topItem: { margin: '16px' },
	content: { marginTop: 16, background: '#fff' },
	footer: { display: 'flex', padding: 12, alignItems: 'center', textAlign: 'right' },
	redcolor: { color: 'red', fontWeight: 'bolder' },
	footerItem2: { flexGrow: 1, color: 'red', margin: '0 12px' }
}
