import React from 'react';
import { Breadcrumb, Icon, Tabs } from 'antd';
import { Link, RouteComponentProps } from 'react-router-dom';
import Normal from './normal';
import Admin from './admin';
import { history } from '../../../index';

const { TabPane } = Tabs;

const A: React.FC<RouteComponentProps> = (props) => {

	let pms = new URLSearchParams(props.location.search)

	return (
		<div>
			<Breadcrumb style={{ margin: 16 }}>
				<Breadcrumb.Item><Link to='/'><Icon type='home' />&nbsp;首页</Link></Breadcrumb.Item>
				<Breadcrumb.Item>系统管理</Breadcrumb.Item>
				<Breadcrumb.Item>用户管理</Breadcrumb.Item>
			</Breadcrumb>

			<div style={{ backgroundColor: '#fff', padding: 20 }}>
				<Tabs activeKey={pms.get('type') === 'admin' ? 'admin' : 'normal'} onChange={v => {
					pms.set('type', v)
					history.replace(props.location.pathname + '?' + pms.toString())
				}}>
					<TabPane tab='注册用户' key='normal'>
						<Normal params={pms} onParams={(k, v) => {

							if (v) pms.set(k, `${ v }`)
							else pms.delete(k)

							history.replace(props.location.pathname + '?' + pms.toString())
						}}></Normal>
					</TabPane>
					<TabPane tab='系统用户' key='admin'>
						<Admin params={pms} onParams={(k, v) => {

							if (v) pms.set(k, `${ v }`)
							else pms.delete(k)

							history.replace(props.location.pathname + '?' + pms.toString())
						}}></Admin>
					</TabPane>
				</Tabs>
			</div>
		</div>
	)
}

export default A;

