import React, { useState, useEffect } from 'react';
import { Breadcrumb, Icon, Table, Switch, Layout, Divider, message, Button } from 'antd';
import { Link } from 'react-router-dom';
import Search from 'antd/lib/input/Search';
import MenuPaths from './menuPaths';
import People from './people';
import request from '../../../request';
import { RoleItem, RolePermission } from '../../../request/types';

const { Content } = Layout;

const A: React.FC = () => {

	const [selectRole, setSelectRole] = useState<RolePermission>({ roleId: '', menuIds: [] });
	const [loading, setLoading] = useState(false)
	const [search, setSearch] = useState('')
	const [items, setItems] = useState<RoleItem[]>([])
	const [keys, setkeys] = useState<string[]>(items.length > 0 ? (items[0].children || []).map(v => v.key) : [])
	const [name, setName] = useState('')

	const columns: any[] = [
		{
			title: '权限名称', dataIndex: 'name',
			render: (text: string) => <MenuPaths path={text}></MenuPaths>
		},
		{
			title: '权限设置', dataIndex: 'id', align: 'center',
			render: (id: string, r: RoleItem, i: number) =>
				<Switch checkedChildren='启用' unCheckedChildren='禁用'
					checked={selectRole.menuIds.some(v => v.id === id)} onChange={() => {

						if (selectRole.menuIds.some(v => v.id === id)) {

							selectRole.menuIds = selectRole.menuIds.filter(v => v.id !== id)
						} else {
							selectRole.menuIds.push({ id })
						}

						request.admin.role.saveRolePermission(selectRole).then(res => {

							if (res.success) {
								updatePermission(selectRole.roleId, name)
								message.success('保存权限设置成功！')
							} else {
								message.success('保存权限设置失败！')
							}
						})
					}}></Switch>
		}
	];

	/**
	 * 权限查找
	 * @param key 
	 * @param item 
	 */
	const includes = (key: string, item: RoleItem): boolean => {

		if (item.name.includes(key)) {
			return true
		}

		if (item.children) {

			return item.children.some(v => includes(key, v))
		}

		return false
	}

	/**
	 * 权限顶部工具条
	 */
	const rightTop = () => {

		return (
			<div style={{ padding: '10px 12px', border: '1px #eee solid', borderBottom: '' }}>
				<Button type='link'>[ {name} ] </Button>
				<Divider type='vertical'></Divider>
				<Search placeholder='权限名' style={{ width: 240 }}   allowClear enterButton onSearch={k => {
					setSearch(k)
				}} />
			</div>
		)
	}

	const updatePermission = (id: string, name: string) => {

		setName(name)
		setLoading(true)
		request.admin.role.getRolePermission(id).then(res => {

			if (res.success) {
				setTimeout(() => {

					setSelectRole(res.data)
					setLoading(false)
				}, 300);
			} else {
				setLoading(false)
			}
		})
	}

	useEffect(() => {
		request.admin.role.getMenuTree().then(res => {
			setItems(res)
		})
	}, [])

	return (
		<div>
			<Breadcrumb style={{ margin: 16 }}>
				<Breadcrumb.Item><Link to='/'><Icon type='home' />&nbsp;首页</Link></Breadcrumb.Item>
				<Breadcrumb.Item>系统管理</Breadcrumb.Item>
				<Breadcrumb.Item>角色管理</Breadcrumb.Item>
			</Breadcrumb>

			<Layout style={{ backgroundColor: '#fff', padding: 20 }}>
				<People
					updatePermission={updatePermission}
					currentRole={selectRole.roleId}
					onDelete={() => {
						setName('')
					}} ></People>

				<Content style={{ paddingLeft: 20 }}>

					{rightTop()}
					<Table
						loading={loading}
						columns={columns}
						dataSource={name.length === 0 ? [] :
							items.filter(v => includes(search, v)).map(v => ({
								...v,
								children: (v.children || []).length === 0 ? undefined :
									(v.children || []).filter(s => includes(search, s)).map(v => ({
										...v,
										children: (v.children || []).length === 0 ? undefined :
											(v.children || []).filter(s => includes(search, s))
									}))
							}))}
						expandedRowKeys={keys}
						onExpand={(v, r) => {

							if (keys.includes(r.key)) {
								setkeys(keys.filter(v => v !== r.key))
							} else {
								setkeys([...keys, r.key])
							}
						}}
						pagination={false}
						showHeader={false}
						bordered>
					</Table>
				</Content>
			</Layout>
		</div>
	)
}

export default A;

