import React from 'react';
import { Icon } from 'antd';

interface Props {
	path: string
}

const A: React.FC<Props> = (props) => {

	const path = props.path.split('>').filter(v => v.length > 0)

	if (path.length === 1) {
		return <span>{path[0]}</span>
	}

	return <span>{
		path.map((v, i) => (
			<span key={i}>
				{i !== 0 ? <Icon type='right' /> : ''}
				<span style={i + 1 === path.length ? styles.last : styles.normal}>{v}
				</span>
			</span>
		))}
	</span>
}

const styles: { [key: string]: React.CSSProperties } = {
	normal: { marginLeft: 8, color: '#448' },
	last: { marginLeft: 8, color: '#fff', padding: '3px 6px', backgroundColor: 'rgba(72,136,255, 0.66)', borderRadius: 3 }
}

export default A;