import React, { useEffect, useState } from 'react'
import { Layout, Menu, Button, Input } from 'antd';
import css from './style.module.less';
import './style.module.less';
import { RouteComponentProps, Link } from 'react-router-dom';

import { history } from '..';
import HeaderRight from './headercmps';
import HeaderRightSmall from './headercmps/small';
import { footer } from '../request';
import { MainHomeRoute } from '../routes';
import { connect } from 'react-redux';

const { Content, Footer, Header } = Layout;
const { Search } = Input;

const App: React.FC<RouteComponentProps & { showSearch: boolean }> = (props) => {

	/**
	 * 根路径变动一下
	 */
	useEffect(() => {

		if (props.match.path !== '/main') {
			history.replace('/main')
		}
	})

	let pms = new URLSearchParams(props.location.search)
	const [keyword, setKeyword] = useState(pms.get('keyword') || '')
	let paths = props.location.pathname.split(/[\\/]/).filter(v => v.length > 0)
	const keys = paths.length > 1 ? [paths[1]] : ['photo']

	console.log(props);

	return (

		<Layout>
			<Header className={css['head']}>
				<div className={css['logo']}>
					{
						props.location.pathname !== '/main/photo' &&
							props.location.pathname !== '/main/media' &&
							props.location.pathname !== '/main/design' ?
							<Button icon='arrow-left' shape='circle' type='ghost' style={{ margin: '0 20px' }}
								onClick={() => { history.goBack() }}></Button> :
							<span style={{ margin: '0 20px' }}> </span>
					}

					<img alt='Logo'></img>
				</div>

				<Menu
					theme='light'
					mode='horizontal'
					defaultSelectedKeys={keys}
					style={styles.menu}>
					<Menu.Item key='photo'><Link to='/main/photo'>摄影</Link></Menu.Item>
					<Menu.Item key='design'><Link to='/main/design'>设计</Link></Menu.Item>
					<Menu.Item key='media'><Link to='/main/media'>媒体</Link></Menu.Item>
				</Menu>
				{
					props.showSearch ?
						<div className={css['search']}>
							<Search placeholder="搜你喜欢的..."
								style={{ width: '100%' }}
								value={keyword}
								onChange={v => setKeyword(v.currentTarget.value)}
								onSearch={v => {
									pms.set('keyword', keyword)
									history.push(props.location.pathname + '?' + pms.toString())
								}}
								enterButton />
						</div> :
						<div style={{ flexGrow: 1 }}></div>
				}

				<div className={css['header-big']}>
					<HeaderRight path={props.location}></HeaderRight>
				</div>
				<div className={css['header-small']}>
					<HeaderRightSmall path={props.location}></HeaderRightSmall>
				</div>
			</Header>
			<Content style={styles.content}>
				<MainHomeRoute></MainHomeRoute>
			</Content>
			<Footer style={styles.footer} dangerouslySetInnerHTML={{ __html: footer }}></Footer>
		</Layout >
	)
};

const styles: { [key: string]: React.CSSProperties } = {

	menu: { lineHeight: '60px', flexShrink: 1, borderBottomWidth: 0, fontSize: 'large' },
	content: { backgroundColor: '#fff', marginTop: 64 },
	footer: { textAlign: 'center', padding: 0 }
};

export default connect((state: any) => ({ showSearch: state.showSearch }))(App);;