import React from 'react';
import { Row, Col, DatePicker, Input, Select, } from 'antd';
import moment from 'moment';
const tmformat = 'YYYY-MM-DD';

interface Props {
	onParams: (key: string, value: string | number) => void
	params: URLSearchParams
}

const A: React.FC<Props> = (props) => {
	return (
		<div style={{ paddingTop: 12 }}>
			<Row gutter={24}>
				<Col xl={4} lg={8} md={12} xs={24}>
					<Select
						style={styles.selStyle} placeholder='订单状态'
						onChange={(value: string) => props.onParams('orderStatus', value)}
						defaultValue={props.params.get('orderStatus') || undefined} allowClear>

						<Select.Option value='0'>未付款</Select.Option>
						<Select.Option value='1'>已付款</Select.Option>
						<Select.Option value='2'>已下载</Select.Option>
						<Select.Option value='-4'>取消交易</Select.Option>
						<Select.Option value='-5'>撤销申请</Select.Option>
					</Select>
				</Col>
				<Col xl={4} lg={8} md={12} xs={24}>
					<DatePicker
						disabledDate={(v) => {
							const dt = props.params.get('orderTimeEnd') &&
								moment(props.params.get('orderTimeEnd') || undefined, tmformat)
							if (dt && v) {
								return v.valueOf() > dt.valueOf()
							}
							return false
						}}
						showTime
						format={tmformat}
						defaultValue={(props.params.get('orderTimeStart') &&
							moment(props.params.get('orderTimeStart') || undefined, tmformat)) || undefined}
						placeholder='订单开始时间'
						onChange={(v, s) => { props.onParams('orderTimeStart', s) }}
						style={{ width: '100%', padding: 6 }}
					/>
				</Col>
				<Col xl={4} lg={8} md={12} xs={24}>
					<DatePicker
						disabledDate={(v) => {
							const dt = props.params.get('orderTimeStart') &&
								moment(props.params.get('orderTimeStart') || undefined, tmformat)
							if (dt && v) {
								return v.valueOf() < dt.valueOf()
							}
							return false
						}}
						showTime
						format={tmformat}
						defaultValue={(props.params.get('orderTimeEnd') &&
							moment(props.params.get('orderTimeEnd') || undefined, tmformat)) || undefined}
						placeholder='订单结束时间'
						onChange={(v, s) => { props.onParams('orderTimeEnd', s) }}
						style={{ width: '100%', padding: 6 }}
					/>
				</Col>
				<Col xl={6} lg={8} md={12} xs={24} >

					<Input.Search
						style={{ width: '100%', padding: 6 }}
						placeholder='订单编号' allowClear enterButton
						defaultValue={props.params.get('id') || undefined}
						type='primary' onSearch={v => { props.onParams('id', v) }}
					/>
				</Col>
				{/* <Col xl={6} lg={8} md={12} xs={24} >

					<Input.Search
						style={{ width: '100%', padding: 6 }}
						placeholder='关键字' allowClear enterButton
						defaultValue={props.params.get('keyword') || undefined}
						type='primary' onSearch={v => { props.onParams('keyword', v) }}
					/>
				</Col> */}
			</Row >
		</div>
	)
}

const styles: { [key: string]: React.CSSProperties } = {
	selStyle: { width: '100%', padding: 6, },
}
export default A;