import React, { useEffect } from 'react';
import { Link, RouteComponentProps } from 'react-router-dom';
import { Breadcrumb, Icon } from 'antd';
import List from './list';
import { history } from '../index';
import store from '../store';
import BackItem from '../cmps/backItem';
const A: React.FC<RouteComponentProps> = (props) => {
	let pms = new URLSearchParams(props.location.search)

	useEffect(() => {

		if (!store.getState().user.accessToken) {
			history.replace('/user/login?old=' + encodeURI(history.location.pathname + history.location.search))
		}
	}, [props.history.location])

	if (!store.getState().user.accessToken) {
		return <div>未登录</div>
	}

	return (

		<div style={{ padding: 20 }}>
			<Link style={{ float: 'right' }} to='/user/orderForm'><b>个人订单</b></Link>
			<Breadcrumb style={{ marginLeft: 16 }}>

				<Breadcrumb.Item><BackItem /></Breadcrumb.Item>
				<Breadcrumb.Item><Link to='/'><Icon type='home' />&nbsp;首页</Link></Breadcrumb.Item>
				<Breadcrumb.Item>我的购物车</Breadcrumb.Item>
			</Breadcrumb>

			<div style={styles.content}>
				<List params={pms} onParams={(k: string, v: any) => {

					if (v) pms.set(k, `${ v }`)
					else pms.delete(k)

					history.replace(props.location.pathname + '?' + pms.toString())
				}} ></List>
			</div>
		</div>
	)
}

const styles: { [key: string]: React.CSSProperties } = {
	root: { backgroundColor: '#fff', },
	content: { marginTop: 16, background: '#fff' },
}

export default A;
