import React from 'react';
import { Modal, List, Divider, Typography, Descriptions, Icon, Skeleton, Result } from 'antd';
import request, { ufileDownUrl } from '../../request';
import { OrderInfo, CartInfo, } from '../../request/types';
import { history } from '../..';

interface State {
	visible: boolean
	info: OrderInfo
	loading: boolean
	step: number
	imgUrl: string
	timer?: number
	gState: boolean
}
/**
 * 项渲染
 * @param item 
 */
const rdItem = (item: CartInfo) => (
	<List.Item >
		<div style={{
			width: 320, height: 200,
			backgroundImage: `url(${ ufileDownUrl + item.smallImage })`,
			backgroundSize: 'cover'
		}}>

		</div>
		<div style={{ marginLeft: 20 }}>
			<span className='money_rmb'>{item.price}</span>
		</div>

	</List.Item>
)

class A extends React.Component {

	state: State = {
		visible: false,
		loading: false,
		timer: undefined,
		gState: true,
		imgUrl: '',
		step: 0,
		info: {
			count: 0,
			createTime: '-',
			detail: [],
			id: '',
			money: 0,
			orderNo: '-'
		}
	}

	/**
	 * 提交支付
	 */
	show = (data?: OrderInfo) => {
		this.setState({ visible: true, info: data })
	}

	/**
	 * 支付页面
	 */
	pay = () => {
		this.setState({ step: 1, loading: true })
		request.main.ui.wx.pay(this.state.info.id).then(res => {

			setTimeout(() => {

				if (res.success) {
					this.setState({ loading: false, imgUrl: `data:image/jpeg;base64,${ res.data }` })
				}
			}, 300);

			if (this.state.timer) {
				clearInterval(this.state.timer)
			}

			this.setState({
				timer: setInterval(() => {

					if (this.state.gState) {
						this.setState({ gState: false })

						request.main.ui.wx.payStatus(this.state.info.id).then(res => {
							if (res !== 0) {

								if (res === -1) {
									this.setState({ gState: true, step: 2 })
								} else {
									this.setState({ gState: true, step: 3 })
								}

								if (this.state.timer) {
									clearInterval(this.state.timer)
								}
							} else {
								this.setState({ gState: true })
							}
						})
					}
				}, 500)
			})
		})
	}

	render() {

		/**
		 * 产生订单
		 */
		if (0 === this.state.step) {
			return (
				<Modal
					title="订单详情"
					visible={this.state.visible}
					okText='微信支付'
					cancelText='取消'
					onOk={this.pay}
					maskClosable={false}
					onCancel={() => { this.setState({ visible: false }) }}>
					<List style={{ width: 400 }}
						dataSource={this.state.info.detail}
						size='small'
						renderItem={rdItem}
					></List>
					<Divider></Divider>
					<Descriptions column={1}>
						<Descriptions.Item label="下单时间">{this.state.info.createTime}</Descriptions.Item>
						<Descriptions.Item label="单号">
							<Typography.Paragraph copyable>{this.state.info.orderNo}</Typography.Paragraph>
						</Descriptions.Item>
						<Descriptions.Item label="总价"><span className='money_rmb'>{this.state.info.money}</span></Descriptions.Item>
					</Descriptions>

				</Modal>
			)
		}
		/**
		 * 请求支付码
		 */
		if (this.state.loading) {
			return (
				<Modal
					title={<Icon type="loading" />}
					visible={this.state.visible}
					okText={false}
					cancelText='取消'
					maskClosable={false}
					onCancel={() => { this.setState({ visible: false }) }}>
					<Skeleton active />
				</Modal>
			)
		}

		/**
		 * 支付码页面
		 */
		if (1 === this.state.step) {
			return (
				<Modal
					title="支付订单"
					visible={this.state.visible}
					okButtonProps={{ style: { display: 'none' } }}
					cancelText='取消'
					maskClosable={false}
					width={400}
					onCancel={() => {
						this.setState({ visible: false })
						if (this.state.timer) {
							clearInterval(this.state.timer)
						}
					}}>

					<img style={{ width: '100%' }} alt='支付码' src={this.state.imgUrl}></img>

				</Modal>
			)
		}

		/**
		 * 支付码失败
		 */
		if (2 === this.state.step) {
			return (
				<Modal
					title={false}
					visible={this.state.visible}
					okText='重新支付'
					cancelText='关闭'
					maskClosable={false}
					onOk={() => this.pay()}
					onCancel={() => { this.setState({ visible: false }) }}>

					<Result
						status='error'
						title='支付失败！'
						subTitle='支付失败，请重试！'
					/>
				</Modal>
			)
		}
		/**
		 * 支付码成功
		 */
		if (3 === this.state.step) {
			return (
				<Modal
					title={false}
					visible={this.state.visible}
					okText='前往下载图片'
					cancelText='关闭'
					maskClosable={false}
					onOk={() => {
						this.setState({ visible: false })
						setTimeout(() => {
							history.push(`/user/orderForm?tabs=1&current_0=${ this.state.info.id }`)
						}, 300);
					}}
					onCancel={() => { this.setState({ visible: false }) }}>

					<Result
						status='success'
						title='支付成功！'
						subTitle='交易完成，请在个人中心下载图片！'
					/>
				</Modal>
			)
		}
	}
}

export default A;