import React from 'react';
import { Form, Input, Icon, Button, Spin } from 'antd';
import { FormComponentProps } from 'antd/lib/form';



interface Props extends FormComponentProps {
	codeImage: string
	spinning: boolean
	handleSubmit: (data: any) => void
	getCodeImage: () => void
}

const A: React.FC<Props> = (props) => {

	/**
	 * 提交表单
	 */
	const handleSubmit = (e: React.FormEvent) => {
		e.preventDefault();
		props.form.validateFields((err, values) => {
			if (!err) {
				props.handleSubmit(values)
			}
		});
	}
	/**
	 * 用户名
	 */
	const itemName = () => {
		const { getFieldDecorator } = props.form;

		return getFieldDecorator('userName', {

			rules: [{ required: true, message: '请输入用户名/手机号' }]
		})(<Input
			size='large'
			prefix={<Icon type='user' style={{ color: 'rgba(0,0,0,.25)' }} />}
			placeholder='手机号/用户名'></Input>)
	}

	/**
	 * 密码
	 */
	const itemPassword = () => {
		const { getFieldDecorator } = props.form;

		return getFieldDecorator('password', {

			rules: [{ required: true, message: '请输入登录密码' }]
		})(<Input
			size='large'
			type='password'
			prefix={<Icon type='lock' style={{ color: 'rgba(0,0,0,.25)' }} />}
			placeholder='请输入登录密码'></Input>)
	}

	/**
	 * 验证码
	 */
	const itemCode = () => {
		const { getFieldDecorator } = props.form;

		return (
			<div style={styles.codeRoot}>
				<div style={styles.codeLeft}>
					{getFieldDecorator('myCode', {
						initialValue: '',
						rules: [{ required: true, message: '请输入验证码' }],
					})(<Input size='large' placeholder="请输入验证码" />)}
				</div>
				<div style={styles.codeRight}>
					<Spin spinning={props.spinning}>
						<div style={{ height: 40 }} onClick={props.getCodeImage}>
							{props.codeImage ?
								<img alt='验证码' src={'data:image/png;base64,' + props.codeImage}
									style={styles.codeImage}></img> :
								<span>刷新验证码</span>}
						</div>
					</Spin>
				</div>
			</div>
		)
	}

	return (
		<>
			<div style={styles.mainLeft}>
				<div><b>华光享易图片交易系统</b></div>
				<div><b>v 1.0</b></div>
			</div>
			<div style={styles.mainSd}>
				<div style={styles.mainSdIn}>
					<Button type='primary' size='large' htmlType='submit' style={{ width: '100%' }}>登录</Button>
				</div>
			</div>
			<div style={styles.main} >
				<div style={{ lineHeight: '40px', fontSize: 'large', color: '#fff' }}>登录</div>
				<div style={{ height: 2, backgroundColor: 'rgba(238,238,238, 0.4)' }}></div>
				<Form onSubmit={handleSubmit} style={{ marginTop: 20, opacity: 0.85 }}>
					<Form.Item >{itemName()}</Form.Item>
					<Form.Item >{itemPassword()}</Form.Item>
					<Form.Item >{itemCode()}</Form.Item>
					<Form.Item>
						<Button type='primary' size='large' htmlType='submit' style={{ width: '100%' }}>登录</Button>
					</Form.Item>
				</Form>
			</div>
		</>
	)
}

const styles: { [key: string]: React.CSSProperties } = {
	item: { padding: '96px', minHeight: '100vh', backgroundSize: 'cover' },
	mainLeft: {
		position: 'absolute',
		top: 'calc(50% - 180px)',
		left: 40,
		padding: '20px 40px',
		borderWidth: 0,
		right: 420,
		height: 360,
		textAlign: 'center',
		fontSize: '4vw',
		color: '#eee'
	},
	main: {
		position: 'absolute',
		top: 'calc(50% - 180px)',
		right: 60,
		padding: '20px 40px',
		borderWidth: 0,
		width: 360,
		height: 360,
		backgroundColor: 'rgba(64,64,64,0.25)',
		boxShadow: '0 0 0 1px rgba(255,255,255,0.25) inset,0 0.75em 1.5em rgba(0,0,0,0.35)'
	},
	image: { width: '100%', borderRadius: 8 },
	codeImage: { height: '100%', borderRadius: 3, border: '1px solid #ddd' },
	codeRoot: { display: 'flex' },
	codeLeft: { flex: '100px 1' },
	codeRight: { marginLeft: 8 },
	mainSd: {
		position: 'absolute',
		top: 'calc(50% + 216px)',
		width: 360,
		right: 60,
		height: 'calc(33vh - 216px)',
		overflow: 'hidden',
		opacity: 0.5,
		pointerEvents: 'none'
	},
	mainSdIn: {
		transform: 'scaleY(-1)',
		padding: 40,
		backgroundColor: 'rgba(96,96,96,0.5)',

	}
}

export default Form.create<Props>()(A);