import React, { useEffect, useState } from 'react'
import css from './style.module.less';
import bk from '../../assets/bk.png';
import request, { ufileDownUrl, requestCache } from '../../request';
import { CategoryList } from '../../request/types';
import { Typography, Icon, Spin } from 'antd';
import { history } from '../..';

interface Props {
	id: string
	pageIndex: number
	selectIndex: (p: number) => void
}

const App: React.FC<Props> = (props) => {

	const [data, setData] = useState<{ total: number, pageIndex: number, rows: CategoryList[] }>(requestCache[`request.main.ui.product.getRecommend_${ props.id }_${ props.pageIndex }`] || {
		total: 0,
		pageIndex: 1,
		rows: []
	})

	const [loading, setLoading] = useState(false)

	/**
	 * 下一组
	 */
	const reload = () => {

		if (data.pageIndex * 12 < data.total) {
			props.selectIndex(data.pageIndex + 1)
		} else {
			props.selectIndex(1)
		}
	}

	useEffect(() => {
		setLoading(true)
		request.main.ui.product.getRecommend(props.id, props.pageIndex).then(res => {
			requestCache[`request.main.ui.product.getRecommend_${ props.id }_${ props.pageIndex }`] = res
			setTimeout(() => {
				setData(res)
				setLoading(false)
			}, props.pageIndex === 1 ? 0 : 300);
		})
	}, [props.id, props.pageIndex])
	return (
		<>
			<Typography.Title level={2} style={{ marginLeft: 12, paddingLeft: 6 }}><span>图片精选</span>
				{
					data.total > 12 && (<span className={css['reload']} onClick={reload}>
						<span>换一组</span>
						<Icon type='reload'></Icon></span>)
				}
			</Typography.Title>
			<Spin spinning={loading}>
				<div className={css['category']}>
					{data.rows.map(v => (
						<div style={{ backgroundImage: `url(${ ufileDownUrl + v.smallSha1 })` }} key={v.id} onClick={() => {
							history.push('/main/detail?id=' + v.id)
						}}>
							<img src={bk} alt=''></img>
						</div>
					))}
				</div>
			</Spin>
		</>
	)
}

export default App;

