import React, { useEffect, useState } from 'react';
import { RouteComponentProps } from 'react-router';
import { Row, Col, Descriptions, Button, Skeleton, Result, Card, List } from 'antd';
import request, { ufileDownUrl } from '../../../request';
import { history } from '../../..';
import moment from 'moment';
/**
 * 格式化时间
 */
const tmformat = 'YYYY-MM-DD HH:mm:ss'

const A: React.FC<RouteComponentProps> = (props) => {

	const [data, setData] = useState();
	const [loading, setLoading] = useState(false);

	useEffect(() => {
		const id = new URLSearchParams(props.location.search).get('id') || '0'
		setTimeout(() => { setLoading(false) }, 300);
		request.admin.order.getById({ id: id }).then(res => setData(res));
	}, [props.location.search])
	/**
	 * 加载中
	 */
	if (!data) {
		return (<div style={{ padding: '20px 10%' }}><Skeleton active /></div>)
	}

	/**
	 * 加载失败
	 */
	if (!data.success) {
		return (
			<Result
				status='404'
				title='您查找的订单未找到'
				subTitle='对不起，您查找的订单未找到。'
				extra={<Button type='primary' onClick={() => { history.goBack() }}>返回</Button>}
			/>
		)
	}
	/**
	 * 项渲染
	 * @param item 
	 */
	const orderItem = (item: any) => (
		<List.Item >
			<div onClick={(e) => {
				e.preventDefault();
				history.push('/admin/picture/detail?id=' + item.productId);
			}} style={{
				width: 160, maxHeight: 200, minHeight: 120,
			}} ><img
					alt={item.productName}
					src={ufileDownUrl + item.productPictureSha1}
					style={{ maxWidth: 160, maxHeight: '100%', cursor: 'Pointer' }} />  </div>
			<div style={{ marginLeft: 20, marginRight: 20 }}>
				名称：<span>{item.productName}</span>
			</div>
			<div style={{ marginLeft: 20, marginRight: 20 }}>
				数量：<span>{item.productCount}</span>
			</div>
			<div style={{ marginLeft: 20, marginRight: 20 }}>
				价格：<span className='money_rmb'>{item.productPrice}</span>
			</div>
			<div style={{ marginLeft: 20, marginRight: 20 }}>
				总价：<span style={{ fontWeight:'bolder'}} className='money_rmb'>{item.productAmount}</span>
			</div>
		</List.Item>
	)
	return (
		<div style={{ backgroundColor: '#FFFFFF' }}>
			<Row>
				<Col md={2} xs={24}>
				</Col>
				<Col md={18} xs={24} >
					<Card style={{ margin: 20 }} hoverable title={'订单号：' + data.orderNo} extra={<Button type='link'
						onClick={() => { history.goBack() }}>返回</Button>}>
						<List style={{ marginBottom: '15px' }} loading={loading}
							dataSource={data.orderDetail}
							bordered
							renderItem={orderItem}
						></List>
						<Descriptions column={2} bordered size="default">
							<Descriptions.Item label='订单编号'>{data.orderNo}</Descriptions.Item>
							<Descriptions.Item label='下单时间'>{(data.createTime && moment(data.createTime).format(tmformat)) || ''}</Descriptions.Item>
							<Descriptions.Item label='下单人'>{data.userName}</Descriptions.Item>
							<Descriptions.Item label='付款方式' >{['余额', '微信', '支付宝'][data.payChannel]}</Descriptions.Item>
							<Descriptions.Item label='第三方支付流水号' >{data.escrowTradeNo}</Descriptions.Item>
							<Descriptions.Item label='付款时间'>{(data.payTime && moment(data.payTime).format(tmformat)) || ''}</Descriptions.Item>
							<Descriptions.Item label='总价'><span className='money_rmb'>{data.productAmountTotal}</span></Descriptions.Item>
							<Descriptions.Item label='折扣金额' ><span className='money_rmb'>{data.agioAmount}</span></Descriptions.Item>
							<Descriptions.Item label='实付金额'><span style={{ fontSize: 20, color: 'red' }} className='money_rmb'>{data.orderAmountTotal}</span></Descriptions.Item>
						</Descriptions>
					</Card>
				</Col>
				<Col md={2} xs={24}>
				</Col>
			</Row>
		</div>
	)
}

export default A;