import React, { useEffect, useState } from 'react';
import { Row, Col, Divider } from 'antd';
import css from './vImages.module.less';

interface Item {
	src: string,
	count: number,
	title: string
}

interface Props {
	items: Item[],
	/**
	 * 24格整除，栏目列数
	 */
	count: 1 | 2 | 3 | 4 | 6 | 8 | 12 | 24
}

const A: React.FC<Props> = (p) => {

	const [array, setArray] = useState<{ p: number, data: Item, sum: number }[]>(
		p.items.map(v => ({ p: -1, data: v, sum: 0 })))

	useEffect(() => {

		const item = array.find(v => v.p === -1)
		const pos = array.findIndex(v => v.p === -1)

		if (item) {
			let img = new Image()

			img.src = item.data.src;
			img.onload = (e) => {

				const items = new Array(p.count)
					.fill(0)
					.map((v, i) => array
						.filter(v => v.p === i)
						.reduce((p, c) => p + c.sum, 0))

				const index = items.indexOf(Math.min(...items))

				array.filter(v => v.p === 0).reduce((p, c) => p + c.sum, 0)

				array[pos].p = index;
				array[pos].sum = img.height / img.width;

				setArray([...array])
			}
			img.onerror = (e) => {

				array[pos].p = -2;
				setArray(array)
			}
		}
	})

	return (
		<Row>
			{
				new Array(p.count).fill(24 / p.count).map((v, i) => (
					<Col span={v} key={i}>

						{array.filter(v => v.p === i).map(v =>
							<div className={css['item']} key={v.data.src}>
								<img className={css['image']} alt='' src={v.data.src}></img>
								<div className={css['title']}>
									<div>
										<div>{v.data.title}</div>
										<div className={css['title-bottom']}>
											<Divider></Divider>
											<div>{v.data.count}</div>
										</div>

									</div>
								</div>
							</div>
						)}
					</Col>
				))
			}
		</Row>
	)
}

export default A;