import React, { useEffect, useRef } from 'react';
import Highcharts from 'highcharts';
import { Breadcrumb, Icon, message } from 'antd';
import { Link } from 'react-router-dom';
import request from '../../request';
const A: React.FC = () => {

	const ref = useRef<HTMLDivElement>(null)

	useEffect(() => {

		const chart = Highcharts.chart(ref.current!, {

			title: {
				text: '图片数据统计'
			},
			subtitle: {
				text: '数据来源: ~ ~ ~'
			},
			xAxis: {
				categories: [
					'最近一月销售额', '当天销售额', '最近一月图片上架数',
					'当天图片上架数', '最近一月用户注册数', '当天用户注册数',
					'最近一月图片下载数', '当天图片下载数']
			},
			yAxis: {
				title: {
					text: '数'
				}
			},
			plotOptions: {
				line: {
					dataLabels: {
						// 开启数据标签
						enabled: true
					},
					// 关闭鼠标跟踪，对应的提示框、点击事件会失效
					enableMouseTracking: false
				}
			},
			series: [
				{
					name: '统计',
					type: 'area',
					data: []

				},

			]
		})
		let series: number[] = new Array(8);
		request.admin.tongji.getList().then((res: any) => {

			if (res.code !== 0) {
				message.error(res.message || '返回数据有误')
				series = [];
			} else {
				series = [];

				series.push(res.data.monthRevenue);
				series.push(res.data.dayRevenue);
				series.push(res.data.monthSell);
				series.push(res.data.daySell);
				series.push(res.data.monthRegisterUser);
				series.push(res.data.dayRegisterUser);
				series.push(res.data.monthDownLoad);
				series.push(res.data.dayDownLoad);
			}
			chart.series[0].update({
				name: '统计',
				type: 'area',
				data: series
			});

			chart.reflow();
		});

	}, []);

	return (
		<div>

			<Breadcrumb style={{ margin: 16 }}>
				<Breadcrumb.Item><Link to='/'><Icon type='home' />&nbsp;首页</Link></Breadcrumb.Item>
				<Breadcrumb.Item>订单管理</Breadcrumb.Item>
				<Breadcrumb.Item>数据统计</Breadcrumb.Item>
			</Breadcrumb>

			<div ref={ref} style={{ margin: 16 }} ></div>
		</div>
	)
}

export default A;