import React from 'react'
import { Row, Col, Form, Input, Button, message } from 'antd';
import { FormComponentProps } from 'antd/lib/form';
import request from '../../request';
import store, { userInfo } from '../../store';
interface Props {
	setUserData: (headImage: any) => void
	userData: any
}
class R extends React.Component<Props & FormComponentProps> {
	state = {
		loading: false,
		data: [],
		id: this.props.userData.id,
	};
	/**
	 * 个人资料修改提交
	 */
	handleSubmit = (e: React.FormEvent) => {
		e.preventDefault();
		this.props.form.validateFields((err, values) => {
			if (!err) {
				request.admin.user.updateUser(
					{
						userName: values.userName,
						mobile: values.mobile,
						nickName: values.nickName,
						trueName: values.trueName,
						id: this.state.id,
					}
				).then((res: any) => {
					if (res.code !== 0) {
						message.error(res.message || '个人资料修改失败')
						return;
					} else {
						message.success('个人资料修改成功');
						store.dispatch({
							type: 'user-update',
							data: {
								accessToken: userInfo.accessToken,
								userInfo: { ...userInfo, nickName: values.nickName }
							}
						})
						this.props.setUserData(values);
					}
				});
			}
		});
	};

	/**
	 * 验证昵称
	 */
	itemNickName = () => {
		const { getFieldDecorator } = this.props.form;
		return getFieldDecorator('nickName', {
			rules: [{ required: true, message: '请输入昵称,1-20个字符!',min:1,max:20 }],
			initialValue: this.props.userData.nickName
		})(<Input
			size='large'
			placeholder='昵称'></Input>)
	}
	itemUserName = () => {
		const { getFieldDecorator } = this.props.form;
		return getFieldDecorator('userName', {
			rules: [{ required: true, message: '请输入用户名!' }],
			initialValue: this.props.userData.userName
		})(<Input readOnly
			size='large'
			placeholder='用户名' style={{border:'0px'}}></Input>)
	}

	itemTrueName = () => {
		const { getFieldDecorator } = this.props.form;
		return getFieldDecorator('trueName', {
			rules: [{ required: true, message: '请输入真实姓名,1-20个字符!',min:1,max:20  }],
			initialValue: this.props.userData.trueName
		})(<Input
			size='large'
			placeholder='真实姓名'></Input>)
	}
	/**
	 * 手机号码
	 */
	itemMobile = () => {
		const { getFieldDecorator } = this.props.form;
		return getFieldDecorator('mobile', {
			rules: [
				{
					required: true,
					message: '请输入正确的手机号码!',
					pattern: /^(1[3-9])\d{9}$/,
				},
			],
			initialValue: this.props.userData.mobile
		})(<Input
			size='large' readOnly
			placeholder='手机号码'></Input>);
	}
	onChange = (value: string) => {
		
	}

	onBlur = (value: string) => {
		
	}
	onSearch = (val: string) => {
		
	}

	onPChange = (value: string) => {
		
	}

	handleChange = (info: any) => {
		if (info.file.status === 'uploading') {
			this.setState({ loading: true });
			return;
		}
		if (info.file.status === 'done') {

			this.setState({
				headImage: info.file.response.original.sha1,
				loading: false,
			});
		}
	};
	render() {
		return (
			<div>
				<Row>
					<Col span={3}>
					</Col>
					<Col span={12} offset={1}>
						<Form onSubmit={this.handleSubmit} labelCol={{ span: 6 }} wrapperCol={{ span: 15 }}>
							<Form.Item label='登录名'>
								{this.itemUserName()}
							</Form.Item>
							<Form.Item label='昵称'>
								{this.itemNickName()}
							</Form.Item>
							<Form.Item label='手机号码'>
								{this.itemMobile()}
							</Form.Item>
							<Form.Item label='真实姓名'>
								{this.itemTrueName()}
							</Form.Item>
							<Form.Item style={{ paddingLeft: 200 }}>
								<Button type='primary' size='large' htmlType='submit'  >保存</Button>
							</Form.Item>
						</Form>
					</Col>
					<Col span={7} offset={1}>
					</Col>
				</Row >
			</div>
		)
	}
}

export default Form.create<Props & FormComponentProps>()(R);