import React from 'react'
import { Layout, Menu, Icon } from 'antd';
import { history } from '..';
import { SelectParam } from 'antd/lib/menu';
import { RouteComponentProps } from 'react-router';

import store, { userInfo } from '../store';
import logo from '../assets/logo.png';
import logo2 from '../assets/logo2.png';
import { AdminRoute } from '../routes';
const { Content, Sider } = Layout;
const { SubMenu } = Menu;

interface MItem {
	key: string
	title: React.ReactNode
	sub: MItem[]
}

export default class SiderDemo extends React.Component<RouteComponentProps> {
	state = {
		collapsed: false,
	};

	/**
	 * 展开/收起左侧菜单
	 */
	onCollapse = (collapsed: boolean) => {
		this.setState({ collapsed });
	};

	/**
	 * 用户未登录，跳转登录页面
	 */
	componentDidMount() {


		if (!store.getState().user.accessToken) {

			history.replace('/admin/login?old=' +
				encodeURI(history.location.pathname + history.location.search))
		} else {
			if (this.props.location.pathname === '/admin/' ||
				this.props.location.pathname === '/admin') {
				history.replace('/admin/home')
			}
		}
	}

	/**
	 * 菜单点击选中，切换路由
	 */
	selectItem = (item: SelectParam) => {

		if (item.key === 'logout') {
			store.dispatch({ type: 'user-remove' })
			setTimeout(() => {
				history.replace('/admin/login?old=' +
					encodeURI(history.location.pathname + history.location.search))
			}, 300);

		} else {
			if (item.key === 'user') {
				history.replace('/user')
			} else {
				history.push(item.key)
			}
		}
	}

	menuItem = (items: MItem[]) => {

		const rts = (userInfo.adminRoute || new Array<{ path: string }>()).map(v => v.path)
		let ary: React.ReactNode[] = []

		items.forEach(item => {
			if (item.sub.length === 0) {
				if (rts.includes(item.key)) {
					ary.push(<Menu.Item key={item.key}>{item.title}</Menu.Item>)
				}
			} else {
				if (rts.includes(item.key)) {
					ary.push(<SubMenu key={item.key} title={item.title}>
						{
							item.sub.filter(v => rts.includes(v.key))
								.map(v => <Menu.Item key={v.key}>{v.title}</Menu.Item>)
						}
					</SubMenu>)
				}
			}
		})

		return ary
	}

	render() {

		if (!store.getState().user.accessToken) {
			return <div>未登录</div>
		}

		const keys = [this.props.location.pathname]
		const oKeys = this.props.location.pathname.split('/').map((v, i, a) => a.slice(0, i + 1).join('/')).filter(Boolean)

		return (
			<Layout style={styles.root}>
				<Sider theme='light'

					style={styles.left} collapsible
					collapsed={this.state.collapsed}
					onCollapse={this.onCollapse}>
					<div style={{ borderBottom: '#eee 1px solid' }}>
						<img alt='logo' src={this.state.collapsed ? logo2 : logo} style={styles.logo}></img>
					</div>

					<Menu
						style={{ borderRight: '0', paddingBottom: 48 }}
						selectedKeys={keys}
						defaultSelectedKeys={keys}
						defaultOpenKeys={oKeys}
						mode='inline' onSelect={this.selectItem}>

						<Menu.Item key='/admin/home'><Icon type='home' /><span>概览</span></Menu.Item>
						{
							this.menuItem([{
								key: '/admin/picture',
								title: <span><Icon type='picture' /><span>图片管理</span></span>,
								sub: [{
									key: '/admin/picture/addpicture',
									title: '添加图片',
									sub: []
								}, {
									key: '/admin/picture/manager',
									title: '图片上/下架',
									sub: []
								}]
							}, {
								key: '/admin/order',
								title: <span><Icon type='appstore' /><span>订单管理</span></span>,
								sub: [{
									key: '/admin/order/list',
									title: '订单查询',
									sub: []
								}]
							}, {
								key: '/admin/system',
								title: <span><Icon type='setting' /><span>系统管理</span></span>,
								sub: [{
									key: '/admin/system/cate',
									title: '图片分类',
									sub: []
								}, {
									key: '/admin/system/user',
									title: '用户管理',
									sub: []
								}, {
									key: '/admin/system/role',
									title: '角色权限',
									sub: []
								}]
							}])
						}

						<Menu.Item key='/user'><Icon type='user' /><span>个人中心</span></Menu.Item>
						<Menu.Item key='logout' style={styles.logoOut}><Icon type='export' /><span>退出登录</span></Menu.Item>
					</Menu>

				</Sider>
				<Layout >
					<Content style={{ borderLeft: '#eee solid 1px', position: 'relative' }}>
						<AdminRoute></AdminRoute>
					</Content>
				</Layout>
			</Layout >
		);
	}
}

const styles: { [key: string]: React.CSSProperties } = {
	root: { height: '100vh' },
	left: { overflowY: 'scroll', zIndex: 100 },
	right: { margin: '0 16px', overflow: 'scroll' },
	logo: { width: '100%', padding: 16, cursor: 'pointer' },
	logoOut: { color: '#eee', fontWeight: 'bold', backgroundColor: '#1890ff' }
}