import React, { useEffect } from 'react';
import { history } from '..';
import { Result } from 'antd';

const A: React.FC = () => {

	useEffect(() => {
		setTimeout(() => {
			history.replace('/admin/home')
		}, 2000);
	})
	return (
		<Result
			status="403"
			title="即将返回"
			subTitle="对不起，你没有权限访问此页面。"
		/>
	)
}

export default A;