import React from 'react';
import { Upload, Button, Card, Icon, Tooltip } from 'antd';
import { baseUrl } from '../../../request';
import { ImageInfoItem } from './infos';

interface Props {
	/**
	 * 下一步
	 */
	nextStep: (files: ImageInfoItem[]) => void
	/**
	 * 图片信息
	 */
	files: ImageInfoItem[]
}

class PicturesWall extends React.Component<Props> {

	state: { fileList: ImageInfoItem[] } = {
		fileList: this.props.files,

	};
	/**
	 * 上传图片的状态
	 */
	uploadState = (loading: boolean, success: boolean, index: number): React.ReactNode => {

		if (loading) {
			return <Icon type='loading' />
		}
		if (success) {
			return <><Tooltip title='上传成功'>
				<Icon type='check' style={{ color: 'green', marginRight: 8 }} />
			</Tooltip><Button type='link' onClick={() => {
				this.setState({ fileList: this.state.fileList.filter((v, i) => i !== index) })
			}}>删除</Button></>
		}

		return <><Tooltip title='上传失败！'><Icon type='close' style={{ color: 'red', marginRight: 8 }} /></Tooltip>
			<Button type='link' onClick={() => {
				this.setState({ fileList: this.state.fileList.filter((v, i) => i !== index) })
			}}>删除</Button></>
	}

	/**
	 * 上传成功返回的图片展示
	 */
	imageInfos = (title: string, span: string, info: any, offset: number = 1) => {

		if (!info) {
			return null
		}

		return (
			<div style={{ flex: `${ info.width } ${ info.width }px`, maxWidth: span, margin: 6 }}>
				<Card hoverable title={title} cover={<img alt={info.name} src={info.url} />}>
					<Card.Meta title={`尺寸：${ info.width }x${ info.height }PX`}
						description={`大小：${ info.size / 1000 } k`} />
				</Card>
			</div>)
	}


	render() {

		return (
			<div style={{ padding: 20, paddingBottom: 48 }}>
				<div style={{ color: 'gray', marginBottom: 8 }}>*注意目前只支持 PNG, GIF, JPG, JPEG, BMP 格式图片,单次最多选10张。</div>

				<Upload action={baseUrl + '/admin/upload/photo'} fileList={this.state.fileList}
					multiple accept='.png,.gif,.jpg,.jpeg,.bmp' onChange={(f) => {

						const showCheck = f.fileList.length > 1
						this.setState({
							fileList: f.fileList.filter((v, i) => i < 10).map(v => ({
								title: '',
								price: 1,
								keywords: [],
								cate: [],
								checked: true,
								...v,
								showCheck: showCheck,
							}))
						})
					}}>
					<Button icon='plus' size='large' type='dashed' disabled={this.state.fileList.length >= 10}>点击添加图片</Button>
				</Upload>

				<div style={{ marginTop: 20 }}>
					{
						this.state.fileList.map((v, i) => <Card title={v.name} key={v.uid} style={{ marginTop: 20 }}
							extra={this.uploadState(v.status !== 'done', v.response && v.response.state === 'SUCCESS', i)}>
							<div style={{ display: 'flex' }}>
								{
									v.response && v.response.original && this.imageInfos('原图', '500px', v.response.original, 0)
								}
								{
									v.response && v.response.middle && this.imageInfos('中图', '300px', v.response.middle)
								}
								{
									v.response && v.response.small && this.imageInfos('小图', '160px', v.response.small)
								}
							</div>
						</Card>)
					}
				</div>
				<div style={styles.bottom}>
					{
						this.state.fileList.length > 0 &&
						<>
							<span style={styles.bottomTitle}>已选 <b style={{ fontSize: 'large' }}>{this.state.fileList.length}</b> 张</span>

							<span style={styles.bottomTitle}>成功 <b style={{ fontSize: 'large' }}>{this.state.fileList.filter(v => v.status === 'done' && v.response && v.response.state === 'SUCCESS').length}</b> 张</span>
						</>
					}

					<Button type='primary' size='large' style={{ minWidth: 120 }} disabled={!this.state.fileList.filter(v => v.status === 'done' && v.response && v.response.state === 'SUCCESS').length}
						onClick={() => { this.props.nextStep(this.state.fileList.filter(v => v.status === 'done' && v.response && v.response.state === 'SUCCESS')) }}>下一步</Button>
				</div>
			</div >
		);
	}
}

const styles: { [key: string]: React.CSSProperties } = {
	bottomTitle: { marginRight: '4vw', color: '#6af' },
	bottom: {
		position: 'absolute',
		backgroundColor: '#fff',
		left: 0,
		right: 0,
		bottom: 0,
		textAlign: 'center',
		height: 60,
		lineHeight: '60px',
		borderTop: '#eee solid 1px',
		boxShadow: '#ddd -3px 0px 6px'
	}
}

export default PicturesWall;