import { createStore, combineReducers } from 'redux';
import request from './request';

export interface UserInfo {
	headImage?: string
	id?: string
	loginName?: string
	nickName?: string
	accessToken?: string
	userGroup?: string
	adminRoute?: { path: string }[]
}

export interface ShoppingCart {
	loading: boolean
	count: number
}

export let userInfo: UserInfo = {
	...JSON.parse(localStorage.getItem('userInfo') || '{}'),
	accessToken: localStorage.getItem('accessToken') || ''
};

export let rts = (userInfo.adminRoute || new Array<{ path: string }>()).map(v => v.path)

function user(state = userInfo, action: { type: 'user-update' | 'user-remove', data: any }) {

	switch (action.type) {
	case 'user-update':
		localStorage.setItem('accessToken', action.data.accessToken)
		localStorage.setItem('userInfo', JSON.stringify(action.data.userInfo))

		userInfo = { accessToken: action.data.accessToken, ...action.data.userInfo }
		request.main.ui.cart.getCount()
		rts = (userInfo.adminRoute || new Array<{ path: string }>()).map(v => v.path)

		return userInfo;

	case 'user-remove':
		localStorage.removeItem('accessToken')
		localStorage.removeItem('userInfo')
		userInfo = {}

		return userInfo;
	default: return state;
	}
}

function shoppingCart(state = {
	loading: true,
	count: 0
}, action: { type: 'shoppingCart-login', data: any }) {

	switch (action.type) {
	case 'shoppingCart-login':
		return {
			loading: false,
			count: action.data
		}
	}

	return state
}

function showSearch(state = false, action: { type: 'showSearch-show' | 'showSearch-hide' }) {
	
	switch (action.type) {
	case 'showSearch-show':
		return true;
	case 'showSearch-hide':
		return false;
	default:
		return false;
	}
}

const store = createStore(combineReducers({ user, shoppingCart, showSearch }));

export default store;

