import React, { useState, useEffect } from 'react';
import { Icon, Layout, Avatar, Card, Row, Col, message, Divider } from 'antd';
import Title from 'antd/lib/typography/Title';
import request, { ufileDownUrl } from '../../request';
import { Link } from 'react-router-dom';
import { UserInfo } from '../../store';
import { connect } from 'react-redux';

const { Header, Content } = Layout;
const { Meta } = Card;
interface Props {
	user: UserInfo
}
const A: React.FC<Props> = (props) => {

	const [data, setData] = useState();

	useEffect(() => {

		request.admin.tongji.getList().then((res: any) => {

			if (res.code !== 0) {
				message.error(res.message || '返回数据有误')

			} else {
				setData(res.data);
			}
		});
	}, []);
	/**
	 * 卡片 Item
	 * @param color 颜色，边框+文字
	 * @param icon 图标
	 * @param title 标题
	 * @param content 内容
	 */
	const CardItem = (color: string, icon: string, title: string, content: string) => {
		return (
			<Col sm={24} xs={12} md={12} lg={12} xl={6} xxl={6}>
				<Card hoverable
					style={styles.item}
					bodyStyle={{ border: `${ color } solid 3px`, borderRadius: 6 }}
					bordered={false}>
					<Meta title={
						<Title level={3} style={{ color: color }}>
							<Icon type={icon} /> {content}
						</Title>} description={title} />
				</Card>
			</Col>
		);
	}
	return (
		<div>
			<Layout>
				<Header style={styles.headerRoot} >
					{/* <div style={styles.headerItem}>
						<Icon type='question-circle' style={{ color: 'green' }} theme='filled' /> 帮助中心
					</div>
					<div style={styles.headerItem}>
						<Badge count={10} dot>
							<Icon type='message' theme='filled' style={{ color: 'green' }} />
						</Badge> 消息
					</div> */}
					{/* <Divider type='vertical' /> */}
					<Link to='/user'>
						<Avatar src={ufileDownUrl + props.user.headImage} />
						<Divider type='vertical' />
						{props.user.nickName}
					</Link>
				</Header>
				<Content style={styles.content}>
					<Row style={styles.item} >
						{data && CardItem('#f56c6c', 'cloud-download', '当天销售额', data.dayRevenue)}
						{data && CardItem('#67c23a', 'cloud-upload', '当天图片上架数', data.daySell)}
						{data && CardItem('#409eff', 'user-add', '当天图片下载数', data.dayDownLoad)}
						{data && CardItem('#aa8323', 'dollar', '今天用户注册数', data.dayRegisterUser)}
					</Row>

					<Row style={styles.item}>
						{data && CardItem('#a33', 'cloud-download', '最近一月销售额', data.monthRevenue)}
						{data && CardItem('#3a3', 'cloud-upload', '最近一月图片上架数', data.monthSell)}
						{data && CardItem('#33a', 'user-add', '最近一月图片下载数', data.monthDownLoad)}
						{data && CardItem('#aa3', 'dollar', '最近一月用户注册数', data.monthRegisterUser)}
					</Row>
				</Content>
			</Layout>

		</div>
	)
}

const styles: { [key: string]: React.CSSProperties } = {
	headerRoot: { background: '#fff', display: 'flex', justifyContent: 'flex-end' },
	headerItem: { padding: '0 20px', cursor: 'pointer' },
	headerTime: { lineHeight: '64px', width: 160 },
	content: { margin: '16px', backgroundColor: '#fff', textAlign: 'center' },
	item: { margin: 12 },
}
export default connect((state: any) => ({ user: state.user }))(A);
//export default A;