import React, { useEffect, useState } from 'react';
import { RouteComponentProps } from 'react-router';
import { Row, Col, Card, Descriptions, Button, Skeleton, Result, Tag } from 'antd';
import request, { ufileDownUrl } from '../../../request';
import { ProductDetail } from '../../../request/types';
import { history } from '../../..';


const A: React.FC<RouteComponentProps> = (props) => {

	const [data, setData] = useState<ProductDetail>()

	useEffect(() => {
		const id = new URLSearchParams(props.location.search).get('id') || '0'
		request.admin.product.getById({ id: id }).then(res => setData(res))

	}, [props.location.search])

	/**
	 * 加载中
	 */
	if (!data || data === undefined) {
		return (<div style={{ padding: '20px 10%' }}><Skeleton active /></div>)
	}

	/**
	 * 加载失败
	 */
	if (!data.success) {
		return (
			<Result
				status='404'
				title='图片未找到！'
				subTitle='对不起，您查找的图片未找到。'
				extra={<Button type='primary' onClick={() => { history.goBack() }}>返回</Button>}
			/>
		)
	}
	const getTypeName = (value: any) => {
		switch (value) {
		case 'photo':
			return '摄影';
		case 'media':
			return '媒体';
		case 'design':
			return '设计';
		}
	}
	return (
		<div style={{ backgroundColor: '#FFFFFF' }}>
			<Row>
				<Col md={6} xs={24}>
				</Col>
				<Col md={12} xs={24}>
					<Card style={{ margin: 20 }} hoverable title={'图片名称：' + data.mainTitle} extra={<Button type='link' onClick={() => { history.goBack() }}>返回</Button>}
						cover={<img alt={data.mainTitle} src={ufileDownUrl + data.sha1} />}>

						<Descriptions title={'图片编号：' + data.serialNumber} column={1}>
							<Descriptions.Item label='图片分类' >{getTypeName(data.categoryUsage)}/{data.parentCategoryName}/{data.categoryName}</Descriptions.Item>
							<Descriptions.Item label='格式'>{data.format.replace('.', '').toUpperCase()}</Descriptions.Item>
							<Descriptions.Item label='图片尺寸'>{data.width}x{data.height}PX</Descriptions.Item>
							<Descriptions.Item label='图片大小'>{data.size > 1048576 ? (Math.round(data.size / 1024) / 1024).toFixed(2) + ' M' : Math.round(data.size / 1024) + ' K'}</Descriptions.Item>
							<Descriptions.Item label='图片价格' ><Tag color='#e66' className='money_rmb'>{data.price}</Tag></Descriptions.Item>

							<Descriptions.Item label='添加时间'>{data.createTime}</Descriptions.Item>
						</Descriptions>
					</Card>
				</Col>
				<Col md={6} xs={24}>
				</Col>
			</Row>
		</div>
	)
}

export default A;