import axios, { AxiosRequestConfig, AxiosResponse } from 'axios';
import { message } from 'antd';
import store, { userInfo } from '../store';
import { history } from '../index';
import { main } from './main';
import { admin } from './admin';

declare let g: {
	baseUrl: string,
	ufileDownUrl: string,
	footer: string
}

export const footer = g.footer;
export const baseUrl = g.baseUrl;
export const ufileDownUrl = g.ufileDownUrl;
export const request = axios.create({
	baseURL: g.baseUrl,
	timeout: 150000
});
/**
 * 请求统一拦截
 */
request.interceptors.request.use(
	(config: AxiosRequestConfig) => {
		if (userInfo.accessToken) {
			config.headers = {
				Authorization: userInfo.accessToken
			}
		}

		return config;
	},
	(error: any) => {
		message.error(error);
		Promise.reject(error);
	}
);
/**
 * 返回值统一拦截处理
 */
request.interceptors.response.use(
	(response: AxiosResponse) => {

		if (response.config.headers.Authorization && userInfo.accessToken !== response.config.headers.Authorization) {
			localStorage.setItem('accessToken', response.config.headers.Authorization)
			userInfo.accessToken = response.config.headers.Authorization
		}

		if (response.status !== 200) {
			message.error(response.statusText);
		}

		return {
			...response,
			data: response.data || { code: -1 }
		}
	},
	(error: any) => {

		if (error.response && 401 === error.response.status) {
			store.dispatch({ type: 'user-remove' })

			if (history.location.pathname !== '/user/login' &&
				history.location.pathname !== '/admin/login') {
				if (/^\/user|shoppingcart.*/img.test(history.location.pathname)) {
					history.push('/user/login?old=' + encodeURI(history.location.pathname + history.location.search))
					setTimeout(() => {
						message.destroy()
						message.error('登录身份失效，请重新登录！')
					}, 50);

				} else if (/^\/admin.*/i.test(history.location.pathname)) {
					history.push('/admin/login?old=' + encodeURI(history.location.pathname + history.location.search))
					setTimeout(() => {
						message.destroy()
						message.error('登录身份失效，请重新登录！')
					}, 50);
				}
			}
		}

		return { status: (error.response && error.response.status) || 200, data: { code: -1 } }
	}
);


const r = { main, admin }

export default r;

if (store.getState().user.accessToken) {
	r.main.ui.cart.getCount()
}

export const requestCache: { [key: string]: any } = {}
