import React, { useState } from 'react';
import { Button, Divider, Checkbox, message } from 'antd';
import { UploadFile } from 'antd/lib/upload/interface';
import InfoForm from './itemInfo';

export interface ImageInfoItem extends UploadFile {
	price: number
	title: string
	cate: string[]
	keywords: string[]
	checked: boolean
	showCheck: boolean
}

interface Props {
	/**
	 * 上一布
	 */
	preStep: () => void
	/**
	 * 下一步
	 */
	nextStep: () => void
	/**
	 * 更新图片信息
	 */
	onUpdata: (data: ImageInfoItem[]) => void
	/**
	 * 更新分类信息
	 */
	onUpdateCate: (data: any[]) => void
	/**
	 * 更新信息模板
	 */
	onUpdateModel: (data: ImageInfoItem) => void
	/**
	 * 图片信息
	 */
	files: ImageInfoItem[]
	/**
	 * 图片模板数据
	 */
	modelInfo: ImageInfoItem
	/**
	 * 分类信息
	 */
	cates: any[]
}

const A: React.FC<Props> = (props) => {

	const [checkInfo, setCheckInfo] = useState(false)
	const count1 = props.files.filter(v => v.checked).length

	return (
		<div style={{ padding: 20, paddingBottom: 48 }}>

			{
				props.files.length > 1 &&
				<div style={{ padding: '12px 0' }}>
					<Checkbox
						indeterminate={count1 !== 0 && count1 !== props.files.length}
						checked={count1 === props.files.length}
						onChange={v => {
							props.files.forEach(s => {
								s.checked = v.target.checked
							})
							props.onUpdata(props.files)

						}}>全选 (统一编辑)</Checkbox>

					<InfoForm onUpdateCate={props.onUpdateCate} checkInfo={false}
						cates={props.cates} obj={props.modelInfo} onUpdate={v => {
							props.onUpdateModel(v)

						}}></InfoForm>
					<Divider></Divider>
				</div>
			}

			{
				props.files.map((v, i) => <InfoForm onUpdateCate={props.onUpdateCate}
					checkInfo={checkInfo}
					cates={props.cates} key={v.uid} obj={v} onUpdate={(v2, c) => {

						props.files[i] = v2
						props.files[i].checked = c
						props.onUpdata(props.files)
					}}></InfoForm>)
			}

			<Divider></Divider>

			<div style={styles.bottom}>

				<Button size='large' style={{ width: 160 }} onClick={() => {
					props.preStep()
				}}>上一步</Button>

				<Divider type='vertical'></Divider>

				<Button type='primary' size='large' style={{ width: 160 }} onClick={() => {

					setCheckInfo(true)
					if (props.files.some(v => v.cate.length === 0) ||
						props.files.some(v => v.keywords.length === 0)) {
						message.error('提交的信息不完整，请补充完整再提交！')
						return
					}

					props.nextStep()

				}}>提交保存</Button>
			</div>
		</div>
	)
}
const styles: { [key: string]: React.CSSProperties } = {
	bottomTitle: { marginRight: '4vw', color: '#6af' },
	bottom: {
		position: 'absolute',
		backgroundColor: '#fff',
		left: 0,
		right: 0,
		bottom: 0,
		textAlign: 'center',
		height: 60,
		lineHeight: '60px',
		borderTop: '#eee solid 1px',
		boxShadow: '#ddd -3px 0px 6px'
	}
}
export default A;